import { useState } from "react";
import DrivingView from "./DrivingView";
import ReactStreetviewMulti from "./ReactStreetviewMulti";

import { MyTooltip } from "../car_interior/CarInterior";


const CenterScreen = ({
    carPosition,
    setCarPosition,
    heading,
    setHeading,
    speed,
    autopilotOn,
    streetViewService,
    streetViewPanoramaOptions,
    setStreetViewPanoramaOptions,
    MAP_API_KEY,
    touchscreenOpen
}) => {

    const [roomMirrorDown, setRoomMirrorDown] = useState(false);

    return (
        <div style={{
            width: "64%",
            height: "100%",
            backgroundColor: "#f8f8f8"
        }}>
            {carPosition && streetViewService &&
                <DrivingView
                    carPosition={carPosition}
                    setCarPosition={setCarPosition}
                    heading={heading}
                    setHeading={setHeading}
                    speed={speed}
                    autopilotOn={autopilotOn}
                    streetViewService={streetViewService}
                    streetViewPanoramaOptions={streetViewPanoramaOptions}
                    setStreetViewPanoramaOptions={setStreetViewPanoramaOptions}
                    MAP_API_KEY={MAP_API_KEY}
                    touchscreenOpen={touchscreenOpen}
                />
            }

            <div style={{ position: "absolute", zIndex: 1, top: 0, left: "40%", width: "20%", height: roomMirrorDown ? "25%" : 24 }}>
                {streetViewPanoramaOptions &&
                    <ReactStreetviewMulti
                        viewId={"room-mirror"}
                        apiKey={MAP_API_KEY}
                        streetViewPanoramaOptions={
                            {
                                position: streetViewPanoramaOptions.position,
                                pov: { heading: (streetViewPanoramaOptions.pov.heading + 180), pitch: 0 },
                                zoom: 1
                            }
                        }
                    />
                }
            </div>

            <div
                style={{
                    position: "absolute", zIndex: 1, top: (roomMirrorDown ? "25%" : 24), left: "40%",
                    width: 48, height: 24, padding: "0 12px",
                    backgroundColor: "darkgrey", opacity: 0.8, cursor: "pointer"
                }}
                onClick={(e) => {
                    setRoomMirrorDown(!roomMirrorDown);
                }}
                id="room-mirror"
            >
                <img
                    src={roomMirrorDown ? "./interior/up.png" : "./interior/down.png"}
                    width="24" height="24" alt=""
                />
            </div>

            <MyTooltip id="room-mirror" label={roomMirrorDown ? "Room Mirror Up" : "Room Mirror Down"} />
        </div>
    );
}

export default CenterScreen;