import ClusterMain from "./ClusterMain";
import ClusterIndicators from "./ClusterIndicators";
import ExteriorConditions from "../car_exterior/ExteriorConditions";
import Passengers from "../car_exterior/Passengers";
import LockStatus from "../car_exterior/LockStatus";
//import ModalExample from "./ModalExample";

import vssApi from "../vplug/VssAPI.json";

import { MyTooltip } from "./CarInterior";


const Cluster = ({
	gear,
	speed,
	distanceToEmpty,
	hornReq,
	autopilotOn,
	frunkOpen, trunkOpen, chargePortOpen,
	frontWipingMode,
	// Exterior conditions
	airTemperature,
	humidity,
	lightIntensity,
	rainIntensity,
	// Passengers
	row1DriverDoorOpen, row1PassengerDoorOpen, row2DriverDoorOpen, row2PassengerDoorOpen,
	row1DriverSeatOccupied, row1PassengerSeatOccupied, row2DriverSeatOccupied, row2PassengerSeatOccupied,
	row1DriverSeatBelted, row1PassengerSeatBelted, row2DriverSeatBelted, row2PassengerSeatBelted,
	departureReady,
	// Lock
	carLocked,
	// Turn Signal
	hazardReq,
	leftTurnReq, setLeftTurnReq,
	rightTurnReq, setRightTurnReq
}) => {

	const FrontWipingType = vssApi.Vehicle.Body.Windshield.Front.Wiping;

	const clusterStyle = {
		//width: "60%",
		width: "100%",
		height: "100%",
		display: "flex",
		flexDirection: "row",
		justifyContent: "flex-start",
		alignItems: "center",
		gap: 16,
		padding: 16
	};

	const exteriorConditionsStyle = {
		//width: "30%",
		height: "100%",
		display: "flex",
		flexDirection: "column",
		justifyContent: "center",
		alignItems: "flex-start",
		padding: 16
	};

	const passengersStyle = {
		//width: "10%",
		height: "100%",
		display: "flex",
		flexDirection: "row",
		justifyContent: "flex-end",
		alignItems: "center",
		gap: 16,
		padding: 16,
		passengers: {
			width: 80,
			height: 64,
			display: "flex",
	
			borderWidth: 1,
			borderStyle: "solid",
			borderColor: "darkgray",
			borderRadius: 8,
			cursor: "pointer"
		},
		lockStatus: {
			width: 80,
			height: 64,
			display: "flex",
			justifyContent: "center",
			alignItems: "center",
			
			borderWidth: 1,
			borderStyle: "solid",
			borderColor: "darkgray",
			borderRadius: 8
		}
	};

	function isWiperOn() {
		if (frontWipingMode !== FrontWipingType.Mode.allowed.OFF) {
			if (frontWipingMode === FrontWipingType.Mode.allowed.RAIN_SENSOR) {
				if (rainIntensity >= 10) {
					return true;
				}
			}
			else {
				return true;
			}
		}
		return false;
	}

	return (
		<div style={{ width: "100%", height: "100%", display: "flex", flexDirection: "row", gap: 16 }}>
			<div style={exteriorConditionsStyle}>
				<ExteriorConditions
					airTemperature={airTemperature} humidity={humidity} lightIntensity={lightIntensity} rainIntensity={rainIntensity}
				/>
			</div>
			
			<div style={clusterStyle}>
				<ClusterMain
					speed={speed}
					distanceToEmpty={distanceToEmpty}
					hornReq={hornReq}
					hazardReq={hazardReq}
					leftTurnReq={leftTurnReq}
					setLeftTurnReq={setLeftTurnReq}
					rightTurnReq={rightTurnReq}
					setRightTurnReq={setRightTurnReq}
				/>

				{isWiperOn() &&
					<div
						style={{
							width: 128, height: 64,
							display: "flex", alignItems: "center", justifyContent: "center",
						}}
					>
						<img src={"./interior/wiper-washer-5021_256.gif"} alt="" />
					</div>
				}

				<ClusterIndicators
					gear={gear}
					speed={speed}
					autopilotOn={autopilotOn}
					frunkOpen={frunkOpen}
					trunkOpen={trunkOpen}
					chargePortOpen={chargePortOpen}
					row1DriverDoorOpen={row1DriverDoorOpen} row1PassengerDoorOpen={row1PassengerDoorOpen}
					row2DriverDoorOpen={row2DriverDoorOpen} row2PassengerDoorOpen={row2PassengerDoorOpen}
					row1DriverSeatOccupied={row1DriverSeatOccupied} row1PassengerSeatOccupied={row1PassengerSeatOccupied}
					row2DriverSeatOccupied={row2DriverSeatOccupied} row2PassengerSeatOccupied={row2PassengerSeatOccupied}
					row1DriverSeatBelted={row1DriverSeatBelted} row1PassengerSeatBelted={row1PassengerSeatBelted}
					row2DriverSeatBelted={row2DriverSeatBelted} row2PassengerSeatBelted={row2PassengerSeatBelted}
				/>
			</div>

			<div style={passengersStyle}>
				<div style={passengersStyle.passengers} id="belted-passengers" >
					<Passengers
						row1DriverDoorOpen={row1DriverDoorOpen} row1PassengerDoorOpen={row1PassengerDoorOpen}
						row2DriverDoorOpen={row2DriverDoorOpen} row2PassengerDoorOpen={row2PassengerDoorOpen}
						row1DriverSeatOccupied={row1DriverSeatOccupied} row1PassengerSeatOccupied={row1PassengerSeatOccupied}
						row2DriverSeatOccupied={row2DriverSeatOccupied} row2PassengerSeatOccupied={row2PassengerSeatOccupied}
						row1DriverSeatBelted={row1DriverSeatBelted} row1PassengerSeatBelted={row1PassengerSeatBelted}
						row2DriverSeatBelted={row2DriverSeatBelted} row2PassengerSeatBelted={row2PassengerSeatBelted}
						departureReady={departureReady}
					/>
				</div>
				<div style={passengersStyle.lockStatus}>
					<LockStatus carLocked={carLocked} departureReady={departureReady} />
				</div>
			</div>

			<MyTooltip id="belted-passengers" label="Fasten/Release Seatbelt" />

			{
				//<ModalExample hazardReq={hazardReq}/>
			}
		</div>
	);
}

export default Cluster;