import "./bootstrap.min.css";
import { useState, useEffect } from "react";
import { Autocomplete } from '@react-google-maps/api';
import { getDistance, getHeading, getNewLocation } from './GeometryUtil';
import { SIGNAL_TYPES, setSignals, setSignal } from "../vplug/WsReq";
import vssApi from "../vplug/VssAPI.json";
import { MyTooltip } from "../car_interior/CarInterior";

import { mainInterval } from "../App";

const directionsMemory = {
	available: false,
	directionsResponse: null,
	directions: [],
	totalDirections: [],
	activeDirection: 0,
	destination: null,
	routes: [],
	step: 0,
	deltaDistance: 0
};


const DirectionsBox = ({
	destination,
	setDestination,
	directionsResponse,
	setDirectionsResponse,
	mainTicks,
	autopilotOn, setAutopilotOn,
	speed, setSpeed,
	carPosition, setCarPosition,
	heading, setHeading,
	// new features
	SCREEN_WIDTH,
	SCREEN_HEIGHT,
	traveledDistanceSinceStart,
	setTraveledDistanceSinceStart,
	distanceToEmpty,
	setDistanceToEmpty,
	originRef,
	relocate
}) => {

	const SelectedGearType = vssApi.Vehicle.Powertrain.Transmission.SelectedGear;

	const MIN_DELTA_DISTANCE = 20 * mainInterval.period / 3600;

	const [directionsTicks, setDirectionsTicks] = useState(0);
	const [directions, setDirections] = useState([]);
	const [activeDirection, setActiveDirection] = useState(0);

	const [totalDirections, setTotalDirections] = useState([]);
	const [remainingDirections, setRemainingDirections] = useState([]);
	const [remainingDistance, setRemainingDistance] = useState(0);
	const [remainingTime, setRemainingTime] = useState(0);

	const [routes, setRoutes] = useState([]);
	const [step, setStep] = useState(0);
	const [initComplete, setInitComplete] = useState(false);

	useEffect(() => {
		if (!initComplete) {
			restoreDirections();
			setInitComplete(true);
		}
	}, 
	// eslint-disable-next-line
	[]);

	useEffect(() => {
		if (initComplete) {
			if (directions.length > 0) {
				storeDirections();
			}
			else {
				clearDirections();
			}
		}
	}, 
	// eslint-disable-next-line
	[directions]);

	function restoreDirections() {
		if (directionsMemory.available) {
			setDirectionsResponse(directionsMemory.directionsResponse);
			setDirections(directionsMemory.directions);
			setTotalDirections(directionsMemory.totalDirections);
			setActiveDirection(directionsMemory.activeDirection);
			setDestination(directionsMemory.destination);
			setRoutes(directionsMemory.routes);
			setStep(directionsMemory.step);
		}
	}
	
	function storeDirections() {
		directionsMemory.available = true;
		directionsMemory.directionsResponse = directionsResponse;
		directionsMemory.directions = directions;
		directionsMemory.totalDirections = totalDirections;
		directionsMemory.activeDirection = activeDirection;
		directionsMemory.destination = destination;
		directionsMemory.routes = routes;
		directionsMemory.step = step;
	}

	function clearDirections() {
		directionsMemory.available = false;
		directionsMemory.directionsResponse = null;
		directionsMemory.directions = [];
		directionsMemory.totalDirections = [];
		directionsMemory.activeDirection = 0;
		directionsMemory.destination = null;
		directionsMemory.routes = [];
		directionsMemory.step = 0;
	}

	useEffect(() => {
		if (initComplete) {
			if (directions.length === 0) {
				const results = directionsResponse;
				if (results) {
					const routes = results.routes;
					if (routes && routes.length > 0) {
						//console.log(results);
						const newRoutes = [];
						const legs = routes[0].legs;
						for (let i = 0; i < legs.length; i++) {
							const steps = legs[i].steps;
							for (let j = 0; j < steps.length; j++) {
								const nextSegment = steps[j].path;
								for (let k = 0; k < nextSegment.length; k++) {
									const coord = nextSegment[k];
									newRoutes.push(coord);
								}
							}
						}
						setRoutes(newRoutes);
						makeDirections(results);
					}
				}
			}
		}
	}, 
	// eslint-disable-next-line
	[directionsResponse]);

	useEffect(() => {
		if (initComplete) {
			if (destination) {
				if (directions.length === 0) {
					calculateRoute();
				}
			}
			else {
				clearRoute();
				setAutopilotOn(false);
			}
		}
	}, 
	// eslint-disable-next-line
	[destination]);

	async function calculateRoute() {
		const directionsService = new window.google.maps.DirectionsService();
		
		try {
			const results = await directionsService.route({
				origin: carPosition,
				destination: destination,
				travelMode: window.google.maps.TravelMode.DRIVING,  
			});
			setDirectionsResponse(results);
		}
		catch (e) {
			console.error(e);
		}
	}

	function clearRoute() {
		setDestination(null);
		setSignal("destination", null, SIGNAL_TYPES.EXT);
		setDirectionsResponse(null);
		setDirections([]);
		setTotalDirections([]);
		setRemainingDirections([]);

		setRoutes([]);
		setStep(0);

		if (originRef.current) {
			originRef.current.value = "";
		}
	}

	function makeDirections(results) {
		if (results) {
			const routes = results.routes;
			if (routes && routes.length > 0) {
				const legs = routes[0].legs;
				if (legs && legs.length > 0) {
					const steps = legs[0].steps;
					if (steps && steps.length > 0) {
						setDirections(steps);
						setActiveDirection(0);
						setStep(0);

						const newDirections = [];
						steps.forEach((direction, idx) => {
							newDirections.push({
								isActiveDirection: false,
								remainingStepDistance: getRemainingDistanceTxt(Number(direction.distance.value)),
								imageURL: getManeuverImageURL(direction.maneuver),
								maneuver: direction.maneuver,
								instructions: direction.instructions
							});
						});
						setTotalDirections(newDirections);

						const startPoint = steps[0].start_location;
						const nextStepPoint = steps[0].end_location;
						const hdng = getHeading(startPoint, nextStepPoint);
						setCarPosition(startPoint);
						setHeading(hdng);
					}
				}
			}
		}
	}

	useEffect(() => {
		if (autopilotOn) {
			if (speed > 0 && routes.length > 0 && distanceToEmpty > 0) {
				const delta_d = speed * mainInterval.period / 3600; // delta distance
				let deltaDistance = directionsMemory.deltaDistance;
				deltaDistance += delta_d;
				if (deltaDistance > MIN_DELTA_DISTANCE) {
					forward(deltaDistance);
					deltaDistance = 0;
				}
				directionsMemory.deltaDistance = deltaDistance;
			}

			if ((mainTicks % mainInterval.directionsRefreshCount) === 0) {
				setDirectionsTicks(directionsTicks + 1);
			}
		}
	}, 
	// eslint-disable-next-line
	[mainTicks]);

	useEffect(() => {
		if (directions.length > 0) {
			let newDirections = [];
			for (let idx = activeDirection; idx < (activeDirection + 2); idx++) {
				if (getProperDirection(idx)) {
					const isActive = isActiveDirection(idx);
					newDirections.push({
						isActiveDirection: isActive,
						remainingStepDistance: getRemainingStepDistance(idx, isActive),
						imageURL: getDirectionImageURL(idx),
						maneuver: getDirectionManeuver(idx),
						instructions: directions[idx].instructions
					});
				}
			}
			if ((activeDirection + 2) < totalDirections.length) {
				newDirections = newDirections.concat(totalDirections.slice(activeDirection + 2));
			}
			setRemainingDirections(newDirections);

			setRemainingDistance(getRemainingDistance());
			setRemainingTime(getRemainingTime());
		}
	}, 
	// eslint-disable-next-line
	[directionsTicks]);

	useEffect(() => {
		if (autopilotOn) {
			if (routes.length > 0) {
				setSpeed(50);
				setSignals({
					signals: [
						{ name : vssApi.Vehicle.Speed.name, value: 50 },
						{ name : SelectedGearType.name, value: SelectedGearType.allowed.Drive }
					],
					type: SIGNAL_TYPES.VSS
				});
			}
		}
		else {
			if (speed > 0) {
				setSpeed(0);
				setSignals({
					signals: [
						{ name : vssApi.Vehicle.Speed.name, value: 0 },
						{ name : SelectedGearType.name, value: SelectedGearType.allowed.Park }
					],
					type: SIGNAL_TYPES.VSS
				});
			}
		}
	},
	// eslint-disable-next-line
	[autopilotOn]);

	function forward(delta_d) {
        let curStep = step;
        let oldStep = curStep;
        let newStep = curStep + 1;
		let curDirection = activeDirection;

        if (newStep < routes.length) {
			let remaining = delta_d;
			let actionCenter = carPosition;

			let newCenter = carPosition;
			let hdng = heading;

			setTraveledDistanceSinceStart(traveledDistanceSinceStart + delta_d/1000);
			if (distanceToEmpty > delta_d) {
				setDistanceToEmpty(distanceToEmpty - delta_d);
			}
			else {
				setDistanceToEmpty(0);
				setSpeed(0);
				setAutopilotOn(false);
			}
			
			while (remaining > 0) {
				let distanceToNextStep = getDistance(actionCenter, routes[newStep]);
				if (remaining < distanceToNextStep) {
					hdng = getHeading(actionCenter, routes[newStep]);
					newCenter = getNewLocation(actionCenter, remaining, hdng);
					remaining = 0;
				}
				else {
					remaining -= distanceToNextStep;
					actionCenter = routes[newStep];
					newCenter = actionCenter;
					
					const newNewStep = newStep + 1;
					if (newNewStep < routes.length) {
						hdng = getHeading(routes[newStep], routes[newNewStep]);
					}
					else {
						hdng = getHeading(routes[oldStep], routes[newStep]);
					}
					
					curStep = newStep;
					oldStep++;
					newStep++;
					if (newStep === routes.length) {
						remaining = 0;
					}

					if (curDirection < (directions.length - 1)) {
						const distanceToMilestone = getDistance(newCenter, directions[curDirection].end_location);
						if (distanceToMilestone < delta_d) {
							curDirection++;
							setActiveDirection(curDirection);
							directionsMemory.activeDirection = curDirection;
						}
					}
				}
			}

			setCarPosition(newCenter);
			setHeading(hdng);
			setStep(curStep);
			directionsMemory.step = step;
        }
        else {
            clearRoute();
			setAutopilotOn(false);
        }
	}

	function getDirectionNow() {
		if (remainingDirections.length > 0) {
			const direction = remainingDirections[0];
			return (
				<div
					style={{
						display: "flex", flexDirection: "row", width: "100%",
						backgroundColor: "lightyellow", borderRadius: 8
					}}
				>
					<div style={{ width: "25%" }}>
						<div style={{
							width: "100%", height: 32, display: "flex", justifyContent: "center", alignItems: "center", fontSize: 14
						}}>
							<div>{direction.remainingStepDistance}</div>
						</div>
						{direction.imageURL?
						<div style={{ width: "100%", height: 40, display: "flex", justifyContent: "center", alignItems: "center" }}>
							<img src={direction.imageURL} style={{ width: 24, height: 24 }} alt="" />
						</div>
						:
						<div style={{
							fontSize: 11,
							width: "100%", height: 40, display: "flex", justifyContent: "center", alignItems: "center"
						}}>
							<div>{direction.maneuver}</div>
						</div>
						}
					</div>
	
					<div style={{ width: "75%" }}>
						<div
							style={{
								width: "100%", height: "100%", padding: 4, fontSize: 14
							}}
							dangerouslySetInnerHTML={{ __html: direction.instructions }}
						/>
					</div>
				</div>
			);
		}
		else {
			return null;
		}
	}

	function getProperDirection(idx) {
		if (idx < directions.length) {
			const direction = directions[idx];
			if (idx === activeDirection && idx < (directions.length - 1)) {
				const maneuver = directions[idx + 1].maneuver;
				if (maneuver) {
					if (maneuver === "turn-left" || maneuver === "turn-slight-left" || maneuver === "turn-sharp-left"
						|| maneuver === "turn-right" || maneuver === "turn-slight-right" || maneuver === "turn-sharp-right"
						|| maneuver === "u-turn-left" || maneuver === "u-turn-right"
						|| maneuver === "ramp-left" || maneuver === "ramp-right"
						|| maneuver === "roundabout-left" || maneuver === "roundabout-right"
						|| maneuver === "fork-left" || maneuver === "fork-right"
						|| maneuver === "merge"
					) {
						const d = getDistance(carPosition, direction.end_location);
						if (d < 100) {
							return null;
						}
						else {
							const total_d = Number(direction.distance.value);
							const ratio = d / total_d;
							if (ratio < 0.25) {
								return null;
							}
						}
					}
				}
			}
			return direction;
		}
		return null;
	}

	function isActiveDirection(idx) {
		if (idx === activeDirection) {
			return true;
		}
		else if (idx === (activeDirection + 1)) {
			if (!getProperDirection(activeDirection)) {
				return true;
			}
		}
		return false;
	}

	function getDirectionImageURL(idx) {
		const maneuver = directions[idx].maneuver;
		if (idx !== activeDirection) {
			return getManeuverImageURL(maneuver);
		}
		else {
			if (maneuver === "straight"
				|| maneuver === "ramp-left" || maneuver === "ramp-right"
				|| maneuver === "roundabout-left" || maneuver === "roundabout-right") 
			{
				return getManeuverImageURL(maneuver);
			}
		}
		return null;
	}

	function getManeuverImageURL(maneuver) {
		switch(maneuver) {
			case "fork-left":
				return "./map/fork_left_FILL0_wght400_GRAD0_opsz24.png";
			case "fork-right":
				return "./map/fork_right_FILL0_wght400_GRAD0_opsz24.png";
			case "merge":
				return "./map/merge_FILL0_wght400_GRAD0_opsz24.png";
			case "ramp-left":
				return "./map/ramp_left_FILL0_wght400_GRAD0_opsz24.png";
			case "ramp-right":
				return "./map/ramp_right_FILL0_wght400_GRAD0_opsz24.png";
			case "roundabout-left":
				return "./map/roundabout_left_FILL0_wght400_GRAD0_opsz24.png";
			case "roundabout-right":
				return "./map/roundabout_right_FILL0_wght400_GRAD0_opsz24.png";
			case "straight":
				return "./map/straight_FILL0_wght400_GRAD0_opsz24.png";
			case "turn-left":
				return "./map/turn_left_FILL0_wght400_GRAD0_opsz24.png";
			case "turn-right":
				return "./map/turn_right_FILL0_wght400_GRAD0_opsz24.png";
			case "turn-sharp-left":
				return "./map/turn_sharp_left_FILL0_wght400_GRAD0_opsz24.png";
			case "turn-sharp-right":
				return "./map/turn_sharp_right_FILL0_wght400_GRAD0_opsz24.png";
			case "turn-slight-left":
				return "./map/turn_slight_left_FILL0_wght400_GRAD0_opsz24.png";
			case "turn-slight-right":
				return "./map/turn_slight_right_FILL0_wght400_GRAD0_opsz24.png";
			case "u-turn-left":
				return "./map/u_turn_left_FILL0_wght400_GRAD0_opsz24.png";
			case "u-turn-right":
				return "./map/u_turn_right_FILL0_wght400_GRAD0_opsz24.png";
			default:
				return null;
		}
	}

	function getDirectionManeuver(idx) {
		const direction = directions[idx];
		if (idx === activeDirection) {
			const maneuver = direction.maneuver;
			if (maneuver) {
				if (maneuver === "turn-left" || maneuver === "turn-slight-left" || maneuver === "turn-sharp-left"
					|| maneuver === "turn-right" || maneuver === "turn-slight-right" || maneuver === "turn-sharp-right"
					|| maneuver === "u-turn-left" || maneuver === "u-turn-right"
					|| maneuver === "fork-left" || maneuver === "fork-right" || maneuver === "merge") 
				{
					return null;
				}
			}
		}
		return direction.maneuver;
	}

	function getRemainingDistance() {
		let d = 0;
		if (directions.length > 0) {
			for (let i = activeDirection + 1; i < directions.length; i++) {
				d += directions[i].distance.value;
			}
			
			if (step !== 0) {
				// FIXME : Not the straight line at all
				d += getDistance(carPosition, directions[activeDirection].end_location);
			}
			else {
				d += directions[0].distance.value;
			}
		}

		return d;
	}

	function getRemainingDistanceTxt(d = 0) {
		if (d === 0) {
			d = getRemainingDistance();
		}
		if (d >= 1000) {
			return Number(d/1000).toFixed(1) + " km";
		}
		else {
			return Number(d).toFixed(0)  + " m";
		}
	}

	function getRemainingTime() {
		let t = 0;
		if (directions.length > 0) {
			for (let i = activeDirection + 1; i < directions.length; i++) {
				t += directions[i].duration.value;
			}

			if (step !== 0) {
				if (directions[activeDirection].distance != null && directions[activeDirection].distance.value !== 0) {
					// FIXME : Not the straight line at all
					let d = getDistance(carPosition, directions[activeDirection].end_location);
					t += (d * directions[activeDirection].duration.value) / directions[activeDirection].distance.value;
				}
			}
			else {
				t += directions[0].duration.value;
			}
		}
		
		return t;
	}

	function getRemainingTimeTxt(t = 0) {
		if (t === 0) {
			t = getRemainingTime();
		}
		if (t >= 60) {
			return Number(t/60).toFixed(0) + " min";
		}
		else {
			return Number(t).toFixed(0) + " sec";
		}
	}

	function getRemainingStepDistance(idx, isActiveDirection) {
		let d_meter;
		if (isActiveDirection) {
			// FIXME : Not the straight line at all
			d_meter = getDistance(carPosition, directions[activeDirection].end_location);
		}
		else {
			d_meter = Number(directions[idx].distance.value);
		}
		return getRemainingDistanceTxt(d_meter);
	}

	// eslint-disable-next-line
	function getRemainingStepTime(idx, isActiveDirection) {
		let time_sec;
		if (isActiveDirection) {
			// FIXME : Not the straight line at all
			const direction = directions[activeDirection];
			const d = getDistance(carPosition, direction.end_location);
			time_sec = d * direction.duration.value / direction.distance.value;
		}
		else {
			time_sec = Number(directions[idx].duration.value);
		}
		
		return getRemainingTimeTxt(time_sec);
	}

	return (
		<>
			{directions.length === 0 &&
				<div
					style={{
						position: "absolute", zIndex: 1, left: (SCREEN_WIDTH * 0.18 + 16), top: 16,
						display: "flex", flexDirection: "column", justifyContent: "space-between", alignItems: "flex-start",
						backgroundColor: "transparent", gap: 8
					}}
				>
					<Autocomplete>
						<input
							type="text"
							name="Origin"
							className="form-control"
							placeholder="Current Location"
							ref={originRef}
							style={{
								width: (SCREEN_WIDTH * 0.22 - 32), height: 30, borderRadius: 20, fontSize: 16
							}}
						/>
					</Autocomplete>
					<div id="teleport">
						<button
							type="submit" name="submit" className="btn btn-primary"
							onClick={relocate}
							style={{
								width: 100, height: 28, padding: 0, borderRadius: 8, fontSize: 16
							}}
						>
							Teleport
						</button>
					</div>
				</div>
			}

			{directions.length > 0 && autopilotOn &&
				<div
					style={{
						position: "absolute", zIndex: 1, left: 0, top: 0,
						width: "18%", height: (SCREEN_HEIGHT / 2),
						backgroundColor: "white",
						display: "flex", flexDirection: "column", justifyContent: "flex-start", alignItems: "center",
						padding: 8, gap: 8
					}}
				>
					<div style={{ width: "100%", height: 72, overflowY: "scroll" }}>
						{
							//showDirections()
							getDirectionNow()
						}
					</div>
					<div
						style={{
							width: "100%", height: 64, padding: 4,
							display: "flex", justifyContent: "center", alignItems: "center",
							backgroundColor: "lightblue", borderRadius: 8,
							fontSize: 16
						}}
					>
						{destination}
					</div>
					<div
						style={{
							width: "100%", height: 32,
							display: "flex", flexDirection: "row", justifyContent: "center", alignItems: "center",
							gap: 8
						}}
					>
						<div
							style={{
								width: "50%", height: "100%",
								display: "flex", justifyContent: "center", alignItems: "center",
								backgroundColor: "lightblue", borderRadius: 8, fontSize: 16
							}}
						>
							{getRemainingDistanceTxt(remainingDistance)}
						</div>
						<div
							style={{
								width: "50%", height: "100%",
								display: "flex", justifyContent: "center", alignItems: "center",
								backgroundColor: "lightblue", borderRadius: 8, fontSize: 16
							}}
						>
							{getRemainingTimeTxt(remainingTime)}
						</div>
					</div>
					<div
						style={{
							width: "100%", height: 32,
							display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "flex-end",
						}}
					>
						<div
							onClick={() => {
								clearRoute();
								setAutopilotOn(false);
							}}
							style={{
								width: 96, height: "100%", borderRadius: 8,
								display: "flex", justifyContent: "center", alignItems: "center",
								backgroundColor: "darkgray", color: "white", fontSize: 16
							}}
							id="cancel-journey"
						>
							Cancel
						</div>
					</div>
				</div>
			}

			<MyTooltip id="teleport" label="Set Your Location" />
			<MyTooltip id="cancel-journey" label="Stop Navigation" />
		</>
	);
}

export default DirectionsBox;