import { useEffect, useState } from "react";
//import BrightnessSlider from "./components/BrightnessSlider";
//import styled from "styled-components";
//import TabsSelector from "./components/TabsSelector";
//import Modal from "./components/Modal";
//import Switch from "./components/Switch";
//import HeadingDescription from "./components/HeadingDescription";

import { SIGNAL_TYPES, subscribeSignals, unsubscribeSignals, setSignal, setSignals } from "../../vplug/WsReq";
import vssApi from "../../vplug/VssAPI.json";

const MaterialColors = {
	Primary: "#53b5fe",
	FontGray: "#6F6F6F",
};


const QuickControl = () => {

	const IsHighBeamSwitchOnType = vssApi.Vehicle.Body.Lights.IsHighBeamSwitchOn;
	const LightSwitchType = vssApi.Vehicle.Body.Lights.LightSwitch;

	const DriverMirrorType = vssApi.Vehicle.Body.Mirrors.DriverSide;
	const PassengerMirrorType = vssApi.Vehicle.Body.Mirrors.PassengerSide;

	const LeftChildLockType = vssApi.Vehicle.Cabin.Door.Row2.DriverSide.IsChildLockActive;
	const RightChildLockType = vssApi.Vehicle.Cabin.Door.Row2.PassengerSide.IsChildLockActive;

	const IsWindowChildLockEngagedType = vssApi.Vehicle.Cabin.IsWindowChildLockEngaged;
	
	const GloveBoxOpenType = vssApi.Vehicle.Cabin.Light.IsGloveBoxOn;

	const FrontWipingType = vssApi.Vehicle.Body.Windshield.Front.Wiping;
	//const RearWipingType = vssApi.Vehicle.Body.Windshield.Rear.Wiping;
	
	//const SteeringWheelType = vssApi.Vehicle.Chassis.SteeringWheel;

	const [lightSwitch, setLightSwitch] = useState(LightSwitchType.allowed.OFF);
	const [highBeamSwitch, setHighBeamSwitch] = useState(false);

	const [foldMirrors, setFoldMirrors] = useState(false);
	const [driverMirrorFolded, setDriverMirrorFolded] = useState(false);
	const [passengerMirrorFolded, setPassengerMirrorFolded] = useState(false);

	const [leftChildLock, setLeftChildLock] = useState(false);
	const [rightChildLock, setRightChildLock] = useState(false);

	const [gloveboxOpen, setGloveboxOpen] = useState(false);

	const [isWindowChildLockEngaged, setIsWindowChildLockEngaged] = useState(false);

	const [frontWipingMode, setFrontWipingMode] = useState(FrontWipingType.Mode.allowed.OFF);
	// const [rearWipingMode, setRearWipingMode] = useState(RearWipingType.Mode.allowed.OFF);
	// const [frontWipingIntensity, setFrontWipingIntensity] = useState(0);
	// const [rearWipingIntensity, setRearWipingIntensity] = useState(0);

	// const [driverMirrorPan, setDriverMirrorPan] = useState(0);
	// const [driverMirrorTilt, setDriverMirrorTilt] = useState(0);
	// const [passengerMirrorPan, setPassengerMirrorPan] = useState(0);
	// const [passengerMirrorTilt, setPassengerMirrorTilt] = useState(0);

	// const [steeringWheelExtension, setSteeringWheelExtension] = useState(0);
	// const [steeringWheelTilt, setSteeringWheelTilt] = useState(0);

	const signalList = new Map();
	const subscriptionData = {
		channel: "touchscreen/controls",
		type: SIGNAL_TYPES.VSS,
		signals: []
	};

	useEffect(() => {
		initSignalList();
		subscribeSignals(subscriptionData, subscriptionCallback);

		return () => {
			unsubscribeSignals({ channel: subscriptionData.channel });
		};
	}, 
	// eslint-disable-next-line
	[]);

	const subscriptionCallback = (msg) => {
		for (const signal of msg.signals) {
			if (signal.value != null && signal.value !== "undefined") {
				const callback = signalList.get(signal.name);
				if (callback) {
					callback(signal.value);
				}
			}
		}
	}

	function initSignalList() {
		signalList.set(LightSwitchType.name, (value) => {
			setLightSwitch(value);
		});
		signalList.set(IsHighBeamSwitchOnType.name, (value) => {
			setHighBeamSwitch(value === "True");
		});
		signalList.set(DriverMirrorType.IsFolded.name, (value) => {
			setDriverMirrorFolded(value === "True");
		});
		signalList.set(PassengerMirrorType.IsFolded.name, (value) => {
			setPassengerMirrorFolded(value === "True");
		});
		signalList.set(LeftChildLockType.name, (value) => {
			setLeftChildLock(value === "True");
		});
		signalList.set(RightChildLockType.name, (value) => {
			setRightChildLock(value === "True");
		});
		signalList.set(IsWindowChildLockEngagedType.name, (value) => {
			setIsWindowChildLockEngaged(value === "True");
		});
		signalList.set(GloveBoxOpenType.name, (value) => {
			setGloveboxOpen(value === "True");
		});
		signalList.set(FrontWipingType.Mode.name, (value) => {
			setFrontWipingMode(value);
		});
		// signalList.set(FrontWipingType.Intensity.name, (value) => {
		// 	setFrontWipingIntensity(value);
		// });
		// signalList.set(RearWipingType.Mode.name, (value) => {
		// 	setRearWipingMode(value);
		// });
		// signalList.set(RearWipingType.Intensity.name, (value) => {
		// 	setRearWipingIntensity(value);
		// });
		// signalList.set(DriverMirrorType.Pan.name, (value) => {
		// 	setDriverMirrorPan(value);
		// });
		// signalList.set(DriverMirrorType.Tilt.name, (value) => {
		// 	setDriverMirrorTilt(value);
		// });
		// signalList.set(PassengerMirrorType.Pan.name, (value) => {
		// 	setPassengerMirrorPan(value);
		// });
		// signalList.set(PassengerMirrorType.Tilt.name, (value) => {
		// 	setPassengerMirrorTilt(value);
		// });
		// signalList.set(SteeringWheelType.Extension.name, (value) => {
		// 	setSteeringWheelExtension(value);
		// });
		// signalList.set(SteeringWheelType.Tilt.name, (value) => {
		// 	setSteeringWheelTilt(value);
		// });

		for (const signal of signalList.keys()) {
			subscriptionData.signals.push({ name: signal });
		}
	}

	useEffect(() => {
		if (driverMirrorFolded || passengerMirrorFolded) {
			setFoldMirrors(true);
		}
		else {
			setFoldMirrors(false);
		}
	}, [driverMirrorFolded, passengerMirrorFolded]);

	function updateChildLockStatus() {
		if (leftChildLock) {
			if (rightChildLock) {
				setSignal(LeftChildLockType.name, "False");
				setSignal(RightChildLockType.name, "False");
			}
			else {
				setSignal(LeftChildLockType.name, "False");
				setSignal(RightChildLockType.name, "True");
			}
		}
		else {
			setSignal(LeftChildLockType.name, "True");
		}
	}

	function getChildLockStatus() {
		if (leftChildLock) {
			if (rightChildLock) {
				return "Both";
			}
			else {
				return "Left";
			}
		}
		else if (rightChildLock) {
			return "Right";
		}
		else {
			return "Off";
		}
	}

	return (
		<div style={styles.container}>
			<div style={styles.firstRow}>
				<div  
					onClick={() => {
						setSignal(LightSwitchType.name, LightSwitchType.allowed.OFF);
					}}
					style={{
						width: "25%", height: 75, display: "flex", flexDirection: "row", justifyContent: "center", alignItems: "center",
						borderWidth: 2, borderTopLeftRadius: 8, borderBottomLeftRadius: 8, borderColor: "lightgray", borderStyle: "solid",
						backgroundColor: (lightSwitch === LightSwitchType.allowed.OFF? "lightgray" : "#f0f0f0")
					}}
				>
					<div style={{ width: "50%", height: "100%", display: "flex", justifyContent: "flex-end", alignItems: "center" }}>
						<img
							src={"./touchscreen/car_control/bulb.png"}
							style={{ width: 36, height: 36, marginRight: 4 }} alt=""
						/>
					</div>
					<div style={{ width: "50%", height: "100%", display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "flex-start" }}>
						<div style={{ fontSize: 18, fontWeight: 700, color: MaterialColors.FontGray, marginLeft: 4 }}>
							Off
						</div>
					</div>
				</div>
				<div  
					onClick={() => {
						setSignal(LightSwitchType.name, LightSwitchType.allowed.POSITION);
					}}
					style={{
						width: "17%", height: 75, display: "flex", justifyContent: "center", alignItems: "center",
						borderWidth: 2, borderLeftWidth: 0, borderColor: "lightgray", borderStyle: "solid",
						backgroundColor: (lightSwitch === LightSwitchType.allowed.POSITION? "lightgray" : "#f0f0f0")
					}}
				>
					<div style={{ fontSize: 18, fontWeight: 700, color: MaterialColors.FontGray }}>
						Parking
					</div>
				</div>
				<div  
					onClick={() => {
						setSignal(LightSwitchType.name, LightSwitchType.allowed.BEAM);
					}}
					style={{
						width: "17%", height: 75, display: "flex", justifyContent: "center", alignItems: "center",
						borderWidth: 2, borderLeftWidth: 0, borderColor: "lightgray", borderStyle: "solid",
						backgroundColor: (lightSwitch === LightSwitchType.allowed.BEAM? "lightgray" : "#f0f0f0")
					}}
				>
					<div style={{ fontSize: 18, fontWeight: 700, color: MaterialColors.FontGray }}>
						On
					</div>
				</div>
				<div  
					onClick={() => {
						setSignal(LightSwitchType.name, LightSwitchType.allowed.AUTO);
					}}
					style={{
						width: "17%", height: 75, display: "flex", justifyContent: "center", alignItems: "center",
						borderWidth: 2, borderLeftWidth: 0, borderTopRightRadius: 8, borderBottomRightRadius: 8, borderColor: "lightgray", borderStyle: "solid",
						backgroundColor: (lightSwitch === LightSwitchType.allowed.AUTO? "lightgray" : "#f0f0f0")
					}}
				>
					<div style={{ fontSize: 18, fontWeight: 700, color: MaterialColors.FontGray }}>
						Auto
					</div>
				</div>

				<div  
					onClick={() => {
						if (highBeamSwitch) {
							setSignal(IsHighBeamSwitchOnType.name, "False");
						}
						else {
							setSignal(IsHighBeamSwitchOnType.name, "True");
						}
					}}
					style={{
						width: "22%", height: 75, display: "flex", flexDirection: "row", justifyContent: "center", alignItems: "center",
						borderWidth: 2, borderRadius: 8, borderColor: "lightgray", borderStyle: "solid",
						backgroundColor: (highBeamSwitch? MaterialColors.Primary : "#f0f0f0"),
						marginLeft: "2%"
					}}
				>
					<img
						src={highBeamSwitch? "./touchscreen/car_control/auto-high-beam-enabled-on.png" : "./touchscreen/car_control/auto-high-beam-enabled-off.png"}
						style={{ width: 48, height: 48 }} alt=""
					/>
				</div>
			</div>
			<div style={styles.firstRow}>
				<div  
					onClick={() => {
						if (foldMirrors) {
							setSignals({
								signals: [
									{ name: DriverMirrorType.IsFolded.name, value: "False" },
									{ name: PassengerMirrorType.IsFolded.name, value: "False" }
								],
								type: SIGNAL_TYPES.VSS
							});
						}
						else {
							setSignals({
								signals: [
									{ name: DriverMirrorType.IsFolded.name, value: "True" },
									{ name: PassengerMirrorType.IsFolded.name, value: "True" }
								],
								type: SIGNAL_TYPES.VSS
							});
						}
					}}
					style={{
						width: "25%", height: 140, display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center",
						borderWidth: 2, borderTopLeftRadius: 8, borderBottomLeftRadius: 8, borderColor: "lightgray", borderStyle: "solid",
						backgroundColor: (foldMirrors? MaterialColors.Primary : "#f0f0f0")
					}}
				>
					<div style={{ width: "100%", height: "50%", display: "flex", justifyContent: "center", alignItems: "center" }}>
						<img
							src={foldMirrors? "./touchscreen/car_control/side-mirror-left-active.png" : "./touchscreen/car_control/side-mirror-left.png"}
							style={{ width: 32, height: 32, marginTop: 16 }} alt=""
						/>
					</div>
					<div style={{ width: "100%", height: "50%", display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center" }}>
						<div style={{ fontSize: 16, fontWeight: 700, color: (foldMirrors? "white" : MaterialColors.FontGray) }}>
							{foldMirrors? "Unfold" : "Fold"}
						</div>
						<div style={{ fontSize: 16, fontWeight: 700, color: (foldMirrors? "white" : MaterialColors.FontGray) }}>
							Mirrors
						</div>
					</div>
				</div>
				<div  
					onClick={() => {
						updateChildLockStatus();
					}}
					style={{
						width: "25%", height: 140, justifyContent: "center", alignItems: "center",
						borderWidth: 2, borderLeftWidth: 0, borderColor: "lightgray", borderStyle: "solid",
						backgroundColor: ((leftChildLock || rightChildLock)? MaterialColors.Primary : "#f0f0f0")
					}}
				>
					<div style={{ width: "100%", height: "50%", display: "flex", justifyContent: "center", alignItems: "center" }}>
						<img
							src={(leftChildLock || rightChildLock)? "./touchscreen/car_control/child-lock-active.png" : "./touchscreen/car_control/child-lock.png"}
							style={{ width: 32, height: 32, marginTop: 16 }} alt=""
						/>
					</div>
					<div style={{ width: "100%", height: "50%", display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center" }}>
						<div style={{ fontSize: 16, fontWeight: 700, color: ((leftChildLock || rightChildLock)? "white" : MaterialColors.FontGray) }}>
							Child Lock
						</div>
						<div style={{ fontSize: 14, fontWeight: 700, color: ((leftChildLock || rightChildLock)? "white" : MaterialColors.FontGray) }}>
							{getChildLockStatus()}
						</div>
					</div>
				</div>
				<div  
					onClick={() => {
						if (isWindowChildLockEngaged) {
							setSignal(IsWindowChildLockEngagedType.name, "False");
						}
						else {
							setSignal(IsWindowChildLockEngagedType.name, "True");
						}
					}}
					style={{
						width: "25%", height: 140, justifyContent: "center", alignItems: "center",
						borderWidth: 2, borderLeftWidth: 0, borderColor: "lightgray", borderStyle: "solid",
						backgroundColor: (isWindowChildLockEngaged? MaterialColors.Primary : "#f0f0f0")
					}}
				>
					<div style={{ width: "100%", height: "50%", display: "flex", justifyContent: "center", alignItems: "center" }}>
						<img
							src={isWindowChildLockEngaged? "./touchscreen/car_control/window-lock-active.png" : "./touchscreen/car_control/window-lock.png"}
							style={{ width: 32, height: 32, marginTop: 16 }} alt=""
						/>
					</div>
					<div style={{ width: "100%", height: "50%", display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center" }}>
						<div style={{ fontSize: 16, fontWeight: 700, color: (isWindowChildLockEngaged? "white" : MaterialColors.FontGray) }}>
							Window
						</div>
						<div style={{ fontSize: 16, fontWeight: 700, color: (isWindowChildLockEngaged? "white" : MaterialColors.FontGray) }}>
							Lock
						</div>
					</div>
				</div>
				<div  
					onClick={() => {
						if (!gloveboxOpen) {
							setSignal(GloveBoxOpenType.name, "True");
						}
					}}
					style={{
						width: "25%", height: 140, justifyContent: "center", alignItems: "center",
						borderWidth: 2, borderLeftWidth: 0, borderTopRightRadius: 8, borderBottomRightRadius: 8, borderColor: "lightgray", borderStyle: "solid"
					}}
				>
					<div style={{ width: "100%", height: "50%", display: "flex", justifyContent: "center", alignItems: "center" }}>
						<img
							src={"./touchscreen/car_control/wallet.png"}
							style={{ width: 32, height: 32, marginTop: 16 }} alt=""
						/>
					</div>
					<div style={{ width: "100%", height: "50%", display: "flex", justifyContent: "center", alignItems: "center" }}>
						<div style={{ fontSize: 16, fontWeight: 700, color: MaterialColors.FontGray }}>
							Glovebox
						</div>
					</div>
				</div>
			</div>

			<div style={styles.firstRow}>
				<div  
					onClick={() => {
						setSignal(FrontWipingType.Mode.name, FrontWipingType.Mode.allowed.OFF);
					}}
					style={{
						width: "25%", height: 75, display: "flex", flexDirection: "row", justifyContent: "center", alignItems: "center",
						borderWidth: 2, borderTopLeftRadius: 8, borderBottomLeftRadius: 8, borderColor: "lightgray", borderStyle: "solid",
						backgroundColor: (frontWipingMode === FrontWipingType.Mode.allowed.OFF? "lightgray" : "#f0f0f0")
					}}
				>
					<div style={{ width: "50%", height: "100%", display: "flex", justifyContent: "flex-end", alignItems: "center" }}>
						<img
							src={"./touchscreen/car_control/windshield.png"}
							style={{ width: 32, height: 32, marginRight: 8 }} alt=""
						/>
					</div>
					<div style={{ width: "50%", height: "100%", display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "flex-start" }}>
						<div style={{ fontSize: 18, fontWeight: 700, color: MaterialColors.FontGray, marginLeft: 8 }}>
							Off
						</div>
					</div>
				</div>
				<div  
					onClick={() => {
						setSignal(FrontWipingType.Mode.name, FrontWipingType.Mode.allowed.INTERVAL);
					}}
					style={{
						width: "12.5%", height: 75, display: "flex", justifyContent: "center", alignItems: "center",
						borderWidth: 2, borderLeftWidth: 0, borderColor: "lightgray", borderStyle: "solid",
						backgroundColor: (frontWipingMode === FrontWipingType.Mode.allowed.INTERVAL? "lightgray" : "#f0f0f0")
					}}
				>
					<div style={{ fontSize: 18, fontWeight: 700, color: MaterialColors.FontGray }}>
						I
					</div>
				</div>
				<div  
					onClick={() => {
						setSignal(FrontWipingType.Mode.name, FrontWipingType.Mode.allowed.SLOW);
					}}
					style={{
						width: "12.5%", height: 75, display: "flex", justifyContent: "center", alignItems: "center",
						borderWidth: 2, borderLeftWidth: 0, borderColor: "lightgray", borderStyle: "solid",
						backgroundColor: (frontWipingMode === FrontWipingType.Mode.allowed.SLOW? "lightgray" : "#f0f0f0")
					}}
				>
					<div style={{ fontSize: 18, fontWeight: 700, color: MaterialColors.FontGray }}>
						II
					</div>
				</div>
				<div  
					onClick={() => {
						setSignal(FrontWipingType.Mode.name, FrontWipingType.Mode.allowed.MEDIUM);
					}}
					style={{
						width: "12.5%", height: 75, display: "flex", justifyContent: "center", alignItems: "center",
						borderWidth: 2, borderLeftWidth: 0, borderColor: "lightgray", borderStyle: "solid",
						backgroundColor: (frontWipingMode === FrontWipingType.Mode.allowed.MEDIUM? "lightgray" : "#f0f0f0")
					}}
				>
					<div style={{ fontSize: 18, fontWeight: 700, color: MaterialColors.FontGray }}>
						III
					</div>
				</div>
				<div  
					onClick={() => {
						setSignal(FrontWipingType.Mode.name, FrontWipingType.Mode.allowed.FAST);
					}}
					style={{
						width: "12.5%", height: 75, display: "flex", justifyContent: "center", alignItems: "center",
						borderWidth: 2, borderLeftWidth: 0, borderColor: "lightgray", borderStyle: "solid",
						backgroundColor: (frontWipingMode === FrontWipingType.Mode.allowed.FAST? "lightgray" : "#f0f0f0")
					}}
				>
					<div style={{ fontSize: 18, fontWeight: 700, color: MaterialColors.FontGray }}>
						IIII
					</div>
				</div>
				<div  
					onClick={() => {
						setSignal(FrontWipingType.Mode.name, FrontWipingType.Mode.allowed.RAIN_SENSOR);
					}}
					style={{
						width: "25%", height: 75, display: "flex", justifyContent: "center", alignItems: "center",
						borderWidth: 2, borderLeftWidth: 0, borderTopRightRadius: 8, borderBottomRightRadius: 8, borderColor: "lightgray", borderStyle: "solid",
						backgroundColor: (frontWipingMode === FrontWipingType.Mode.allowed.RAIN_SENSOR? "lightgray" : "#f0f0f0")
					}}
				>
					<div style={{ fontSize: 18, fontWeight: 700, color: MaterialColors.FontGray }}>
						Auto
					</div>
				</div>
			</div>

			<div style={styles.firstRow}>
				<div style={{ width: "50%", height: 240, paddingRight: 12 }}>
					<div>
						<div  
							onClick={() => {
							}}
							style={{
								width: "100%", height: 120, display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center",
								borderWidth: 2, borderTopLeftRadius: 8, borderTopRightRadius: 8, borderColor: "lightgray", borderStyle: "solid"
							}}
						>
							<div style={{ width: "100%", height: "50%", display: "flex", justifyContent: "center", alignItems: "center" }}>
								<img
									src={"./touchscreen/car_control/side-mirror-left.png"}
									style={{ width: 32, height: 32, marginTop: 16 }} alt=""
								/>
							</div>
							<div style={{ width: "100%", height: "50%", display: "flex", justifyContent: "center", alignItems: "center" }}>
								<div style={{ fontSize: 18, fontWeight: 700, color: MaterialColors.FontGray }}>Mirrors</div>
							</div>
						</div>
						<div  
							onClick={() => {
							}}
							style={{
								width: "100%", height: 120, display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center",
								borderWidth: 2, borderTopWidth: 0, borderBottomLeftRadius: 8, borderBottomRightRadius: 8, borderColor: "lightgray",
								borderStyle: "solid"
							}}
						>
							<div style={{ width: "100%", height: "50%", display: "flex", justifyContent: "center", alignItems: "center" }}>
								<img
									src={"./touchscreen/car_control/steering-wheel.png"}
									style={{ width: 32, height: 32, marginTop: 16 }} alt=""
								/>
							</div>
							<div style={{ width: "100%", height: "50%", display: "flex", justifyContent: "center", alignItems: "center" }}>
								<div style={{ fontSize: 18, fontWeight: 700, color: MaterialColors.FontGray }}>Steering</div>
							</div>
						</div>
					</div>
				</div>
				<div style={{ width: "50%", height: 240, paddingLeft: 12 }}>
					<div>
						<div  
							onClick={() => {
							}}
							style={{
								width: "100%", height: 120, display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center",
								borderWidth: 2, borderTopLeftRadius: 8, borderTopRightRadius: 8, borderColor: "lightgray",
								borderStyle: "solid"
							}}
						>
							<div style={{ width: "100%", height: "50%", display: "flex", justifyContent: "center", alignItems: "center" }}>
								<img
									src={"./touchscreen/car_control/dashcam-recording.png"}
									style={{ width: 32, height: 32, marginTop: 16 }} alt=""
								/>
							</div>
							<div style={{ width: "100%", height: "50%", display: "flex", justifyContent: "center", alignItems: "center" }}>
								<div style={{ fontSize: 18, fontWeight: 700, color: MaterialColors.FontGray }}>Recording</div>
							</div>
						</div>
						<div  
							onClick={() => {
							}}
							style={{
								width: "100%", height: 120, display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center",
								borderWidth: 2, borderTopWidth: 0, borderBottomLeftRadius: 8, borderBottomRightRadius: 8, borderColor: "lightgray",
								borderStyle: "solid"
							}}
						>
							<div style={{ width: "100%", height: "50%", display: "flex", justifyContent: "center", alignItems: "center" }}>
								<img
									src={"./touchscreen/car_control/SentryMode.png"}
									style={{ width: 32, height: 32, marginTop: 16 }} alt=""
								/>
							</div>
							<div style={{ width: "100%", height: "50%", display: "flex", justifyContent: "center", alignItems: "center" }}>
								<div style={{ fontSize: 18, fontWeight: 700, color: MaterialColors.FontGray }}>Sentry</div>
							</div>
						</div>
					</div>
				</div>
			</div>

			<div style={styles.firstRow}>
				<div  
					onClick={() => {
						//
					}}
					style={{
						width: "75%", height: 75, display: "flex", flexDirection: "row", justifyContent: "center", alignItems: "center",
						borderWidth: 2, borderRadius: 8, borderColor: "lightgray", borderStyle: "solid",
						//backgroundColor: (foldMirrors ? MaterialColors.Primary : "white")
					}}
				>
					<div style={{ width: "85%", height: 4, backgroundColor: "#6f6f6f", marginLeft: 16 }}>
					</div>
					<div style={{ width: "15%", height: "100%", display: "flex", justifyContent: "center", alignItems: "center" }}>
						<img
							src={"./touchscreen/car_control/brightness.png"}
							style={{ width: 32, height: 32 }} alt=""
						/>
					</div>
				</div>

				<div  
					onClick={() => {
						//
					}}
					style={{
						width: "22%", height: 75, display: "flex", flexDirection: "row", justifyContent: "center", alignItems: "center",
						borderWidth: 2, borderRadius: 8, borderColor: "lightgray", borderStyle: "solid",
						//backgroundColor: (foldMirrors ? MaterialColors.Primary : "white")
						marginLeft: "3%"
					}}
				>
					<div style={{ fontSize: 18, fontWeight: 700, color: MaterialColors.FontGray }}>
						Auto
					</div>
				</div>
			</div>
		</div>
	);
}

