import { useState } from "react";
import styled from "styled-components";
import SelectionControl from "./components/SelectControl";
import SwitchControl from "./components/SwitchControl";


const SafetyControls = () => {

	const [sentryMode, setSentryMode] = useState("On");
	const [cameraBasedDetection, setCameraBasedDetection] = useState(true);
	const [disableSentryModeSounds, setDisableSentryModeSounds] = useState(false);
	const [viewLiveCameraViaMobileApp, setViewLiveCameraViaMobileApp] = useState(true);

	const [allowMobileAccess, setAllowMobileAccess] = useState(true);
	const [PINToDrive, setPINToDrive] = useState(false);
	const [gloveboxPIN, setGloveboxPIN] = useState(false);

	const [dashcam, setDashcam] = useState("Auto");

	return (
		<StyledDiv>
			<SelectionControl
				name="Sentry Mode"
				valueList={["Off", "On"]}
				value={sentryMode}
				setValue={setSentryMode}
				desc={["Sentry Mode will be enabled when you leave the car"]}
			/>
			<SwitchControl
				name="Camera-Based Detection"
				value={cameraBasedDetection}
				setValue={setCameraBasedDetection}
			/>
			<SwitchControl
				name="Disable Sentry Mode Sounds"
				value={disableSentryModeSounds}
				setValue={setDisableSentryModeSounds}
			/>
			<SwitchControl
				name="View Live Camera via Mobile App"
				value={viewLiveCameraViaMobileApp}
				setValue={setViewLiveCameraViaMobileApp}
			/>

			<SelectionControl
				name="Dashcam"
				valueList={["Off", "Manual", "Auto"]}
				value={dashcam}
				setValue={setDashcam}
			/>
			<div style={{ display: "flex", flexDirection: "row", gap: 16 }}>
				<div 
					onClick={() => {
					}}
					style={{
						width: 225, height: 64, display: "flex", justifyContent: "center", alignItems: "center",
						//borderWidth: 2, borderRadius: 8, borderColor: "darkgray", borderStyle: "solid",
						backgroundColor: "lightgray", borderRadius: 8,
						marginTop: 16
					}}
				>
					<div style={{ fontSize: 18, fontWeight: 700 }}>Disable Dashcam Clips</div>
				</div>
				<div 
					onClick={() => {
					}}
					style={{
						width: 225, height: 64, display: "flex", justifyContent: "center", alignItems: "center",
						//borderWidth: 2, borderRadius: 8, borderColor: "darkgray", borderStyle: "solid",
						backgroundColor: "lightgray", borderRadius: 8,
						marginTop: 16
					}}
				>
					<div style={{ fontSize: 18, fontWeight: 700 }}>Format USB Drive</div>
				</div>
			</div>

			<SwitchControl
				name="Allow Mobile Access"
				value={allowMobileAccess}
				setValue={setAllowMobileAccess}
			/>
			<SwitchControl
				name="PIN to Drive"
				value={PINToDrive}
				setValue={setPINToDrive}
			/>
			<SwitchControl
				name="Glovebox PIN"
				value={gloveboxPIN}
				setValue={setGloveboxPIN}
			/>
		</StyledDiv>
	);
}

export default SafetyControls;

const StyledDiv = styled.div`
	width: 100%;
	height: 100%;
	background-color: #f0f0f0;
	padding: 32px;
	position: relative;
	color: #6F6F6F;
`;
