import { useState, useEffect } from "react";

import { SIGNAL_TYPES, setSignal, subscribeSignals, unsubscribeSignals } from "../../vplug/WsReq";
import vssApi from "../../vplug/VssAPI.json";


const MaterialColors = {
	Primary: "#2979FF",
	FontGray: "#6F6F6F",
};


const ClimateControl = ({
	APP_HEIGHT
}) => {

	const HVAC_Type = vssApi.Vehicle.Cabin.HVAC;
	const DriverSeatSwitchType = vssApi.Vehicle.Cabin.Seat.Row1.DriverSide.Switch;
	const PassengerSeatSwitchType = vssApi.Vehicle.Cabin.Seat.Row1.PassengerSide.Switch;

	const signalList = new Map();
	const subscriptionData = {
		channel: "touchscreen/climate",
		type: SIGNAL_TYPES.VSS,
		signals: []
	};

	useEffect(() => {
		initSignalList();
		subscribeSignals(subscriptionData, subscriptionCallback);

		return () => {
			unsubscribeSignals({ channel: subscriptionData.channel });
		};
	}, 
	// eslint-disable-next-line
	[]);

	const subscriptionCallback = (msg) => {
		for (const signal of msg.signals) {
			if (signal.value != null && signal.value !== "undefined") {
				const callback = signalList.get(signal.name);
				if (callback) {
					callback(signal.value);
				}
			}
		}
	}

	function initSignalList() {
		signalList.set(HVAC_Type.IsAirConditioningActive.name, (value) => {
			setAirConOn(value === "True");
		});
		signalList.set(HVAC_Type.Station.Row1.Driver.AirDistribution.name, (value) => {
			if (value === HVAC_Type.Station.Row1.Driver.AirDistribution.allowed.UP) {
				setVentUp(true);
				setVentMiddle(false);
				setVentDown(false);
			}
			else if (value === HVAC_Type.Station.Row1.Driver.AirDistribution.allowed.MIDDLE) {
				setVentUp(false);
				setVentMiddle(true);
				setVentDown(false);
			}
			else if (value === HVAC_Type.Station.Row1.Driver.AirDistribution.allowed.DOWN) {
				setVentUp(false);
				setVentMiddle(false);
				setVentDown(true);
			}
		});
		signalList.set(HVAC_Type.IsFrontDefrosterActive.name, (value) => {
			setDefogDefrosterOn(value === "True");
		});
		signalList.set(HVAC_Type.IsRearDefrosterActive.name, (value) => {
			setWarmRearWindshieldOn(value === "True");
		});
		signalList.set(DriverSeatSwitchType.IsWarmerEngaged.name, (value) => {
			setLeftSeatHeaterOn(value === "True");
		});
		signalList.set(PassengerSeatSwitchType.IsWarmerEngaged.name, (value) => {
			setRightSeatHeaterOn(value === "True");
		});
		signalList.set(HVAC_Type.IsRecirculationActive.name, (value) => {
			setRecirculation(value === "True");
		});

		for (const signal of signalList.keys()) {
			subscriptionData.signals.push({ name: signal });
		}
	}

	const [frontClimate, setFrontClimate] = useState(true);
	const [rearClimate, setRearClimate] = useState(false);

	const [climatePowerOn, setClimatePowerOn] = useState(false);
	const [climateAutoOn, setClimateAutoOn] = useState(false);
	const [defogDefrosterOn, setDefogDefrosterOn] = useState(false);
	const [warmRearWindshieldOn, setWarmRearWindshieldOn] = useState(false);
	const [airConOn, setAirConOn] = useState(false);
	const [recirculation, setRecirculation] = useState(false);

	const [ventUp, setVentUp] = useState(false);
	const [ventMiddle, setVentMiddle] = useState(true);
	const [ventDown, setVentDown] = useState(false);

	const [leftSeatHeaterOn, setLeftSeatHeaterOn] = useState(false);
	const [leftSeatHeaterAutoOn, setLeftSeatHeaterAutoOn] = useState(false);
	const [rightSeatHeaterOn, setRightSeatHeaterOn] = useState(false);
	const [rightSeatHeaterAutoOn, setRightSeatHeaterAutoOn] = useState(false);

	const [steeringWheelHeaterOn, setSteeringWheelHeaterOn] = useState(false);
	const [windshieldWiperHeaterOn, setWindshieldWiperHeaterOn] = useState(false);

	const styles = {
		container: {
			position: "relative",
			zIndex: 2, 
			width: "100%", 
			height: APP_HEIGHT,
			display: "flex",
			flexDirection: "row",
			backgroundColor:"#eeeeee",
			color: MaterialColors.FontGray,
		},
		leftPane: {
			width: "10%",
			//height: "100%",
			//backgroundColor: "lightyellow",
		},
		rightPane: {
			width: "90%",
			//height: "100%",
			display: "flex",
			flexDirection: "column",
		},
		firstRow: {
			height: "15%",
			//backgroundColor: "lightblue",
			display: "flex",
			flexDirection: "row",
		},
		secondRow: {
			height: "70%",
			//backgroundColor: "lightgreen",
			display: "flex",
			flexDirection: "row",
		},
		thirdRow: {
			height: "15%",
			//backgroundColor: "lightblue",
			display: "flex",
			flexDirection: "row",
		},
	};

	return (
		<div style={styles.container}>
			<div style={styles.leftPane}>
				<div 
					onClick={() => { 
						if (!frontClimate) { 
							setFrontClimate(!frontClimate); setRearClimate(frontClimate); 
						} 
					}}
					style={{
						display: "flex", 
						height: "50%", justifyContent: "center", alignItems: "center", borderRadius: 8,
						backgroundColor: (frontClimate ? "#bbbbbb" : "#eeeeee")
					}}
				>
					<span style={{ fontSize: 20, fontWeight: 900 }}>Front</span>
				</div>
				<div 
					onClick={() => { 
						if (!rearClimate) { 
							setRearClimate(!rearClimate); setFrontClimate(rearClimate); 
						} 
					}}
					style={{
						display: "flex", 
						height: "50%", justifyContent: "center", alignItems: "center", borderRadius: 8, 
						backgroundColor: (rearClimate ? "#bbbbbb" : "#eeeeee")
					}}
				>
					<span style={{ fontSize: 20, fontWeight: 900 }}>Rear</span>
				</div>
			</div>
			<div style={styles.rightPane}>	
				<div style={styles.firstRow}>
					<div style={{ width: "20%", display: "flex", flexDirection: "row", padding: 16, justifyContent: "flex-start" }}>
						<div 
							onClick={() => {
								setClimatePowerOn(!climatePowerOn);
							}}
							style={{ 
								display: "flex",
								width: 80, height: 80, justifyContent: "center", alignItems: "center",
								borderWidth: 2, borderStyle: "solid", borderColor: "darkgray",
								borderTopLeftRadius: 8, borderBottomLeftRadius: 8, 
								backgroundColor: (climatePowerOn ? MaterialColors.Primary : "#eeeeee") 
							}}
						>
							<img 
								src={climatePowerOn ? "./touchscreen/climate/power-active.png" : "./touchscreen/climate/power.png"} 
								alt=""
								style={{width: 40, height: 40}}
							/>
						</div>
						<div 
							onClick={() => {
								setClimateAutoOn(!climateAutoOn);
							}}
							style={{ 
								display: "flex",
								width: 100, height: 80, justifyContent: "center", alignItems: "center",
								borderWidth: 2, borderLeftWidth: 0, borderStyle: "solid", borderColor: "darkgray",
								borderTopRightRadius: 8, borderBottomRightRadius: 8, 
								backgroundColor: (climateAutoOn ? MaterialColors.Primary : "#eeeeee") 
							}}
						>
							<span style={{ color: (climateAutoOn ? "white" : MaterialColors.FontGray), fontSize: 20, fontWeight: 900 }}>
								Auto
							</span>
						</div>
					</div>
					<div style={{ width: "60%", display: "flex", flexDirection: "row", padding: 16, justifyContent: "center" }}>
						<div 
							onClick={() => { 
								if (defogDefrosterOn) {
									setSignal(HVAC_Type.IsFrontDefrosterActive.name, "False");
								}
								else {
									setSignal(HVAC_Type.IsFrontDefrosterActive.name, "True");
								}
							}}
							style={{ 
								display: "flex",
								width: 80, height: 80, justifyContent: "center", alignItems: "center",
								borderWidth: 2, borderStyle: "solid", borderColor: "darkgray",
								borderTopLeftRadius: 8, borderBottomLeftRadius: 8
							}}
						>
							<img 
								src={defogDefrosterOn ? "./touchscreen/climate/car-aircon-icon-active.png" : "./touchscreen/climate/car-aircon-icon.png"} 
								alt=""
								style={{width: 40, height: 40}}
							/>
						</div>
						<div 
							onClick={() => { 
								if (warmRearWindshieldOn) {
									setSignal(HVAC_Type.IsRearDefrosterActive.name, "False");
								}
								else {
									setSignal(HVAC_Type.IsRearDefrosterActive.name, "True");
								}
							}}
							style={{ 
								display: "flex",
								width: 80, height: 80, justifyContent: "center", alignItems: "center",
								borderWidth: 2, borderLeftWidth: 0, borderStyle: "solid", borderColor: "darkgray",
								borderTopRightRadius: 8, borderBottomRightRadius: 8
							}}
						>
							<img 
								src={warmRearWindshieldOn ? "./touchscreen/climate/car-heater-icon-active.png" : "./touchscreen/climate/car-heater-icon.png"} 
								alt=""
								style={{width: 48, height: 48}}
							/>
						</div>
						
						<div style={{ width: 16 }} />
						
						<div 
							onClick={() => {
								if (airConOn) {
									setSignal(HVAC_Type.IsAirConditioningActive.name, "False");
								}
								else {
									setSignal(HVAC_Type.IsAirConditioningActive.name, "True");
								}
							}}
							style={{ 
								display: "flex",
								width: 80, height: 80, justifyContent: "center", alignItems: "center",
								borderWidth: 2, borderRadius: 8, borderStyle: "solid", borderColor: "darkgray",
								backgroundColor: (airConOn ? MaterialColors.Primary : "#eeeeee") 
							}}
						>
							<span style={{ color: (airConOn ? "white" : MaterialColors.FontGray), fontSize: 20, fontWeight: 900 }}>
								A/C
							</span>
						</div>
						
						<div style={{ width: 16 }} />
						
						<div 
							style={{ 
								width: 256, height: 80, borderWidth: 2, borderStyle: "solid", borderColor: "darkgray", borderRadius: 8,
								display: "flex", flexDirection: "row", gap: 16, justifyContent: "center", alignItems: "center" 
							}}
						>
							<img 
								src={"./touchscreen/climate/fan.png"} 
								alt=""
								style={{width: 36, height: 36}}
							/>
							<span style={{ fontSize: 32, fontWeight: 900 }}>5</span>
							<span style={{ fontSize: 20, fontWeight: 900 }}>----O--------</span>
						</div>
						
						<div style={{ width: 16 }} />
						<div 
							onClick={() => {
								if (recirculation) {
									setSignal(HVAC_Type.IsRecirculationActive.name, "False");
								}
								else {
									setSignal(HVAC_Type.IsRecirculationActive.name, "True");
								}
							}}
							style={{ 
								display: "flex",
								width: 80, height: 80, justifyContent: "center", alignItems: "center", 
								borderWidth: 2, borderRadius: 8, borderStyle: "solid", borderColor: "darkgray",
								backgroundColor: (recirculation ? MaterialColors.Primary : "#eeeeee") 
							}
						}>
							<img 
								src={recirculation? "./touchscreen/climate/arrow-loop-active.png" : "./touchscreen/climate/arrow-loop.png"} 
								alt=""
								style={{width: 40, height: 40}}
							/>
						</div>

						<div style={{ width: 16 }} />
						<div 
							style={{ 
								display: "flex",
								width: 80, height: 80, justifyContent: "center", alignItems: "center",
								borderWidth: 2, borderRadius: 8, borderStyle: "solid", borderColor: "darkgray" 
							}
						}>
							<span style={{ fontSize: 20, fontWeight: 900 }}>HEPA</span>
						</div>
					</div>
					<div style={{ width: "20%", display: "flex", flexDirection: "row", padding: 16, justifyContent: "flex-end" }}>
						<div
							style={{ 
								backgroundColor: '#eeeeee',
								display: "flex", width: 200, height: 80,  
								justifyContent: "center", alignItems: "center", 
								borderWidth: 2, borderRadius: 8, borderStyle: "solid", borderColor: "darkgray"
							}}
						 	onClick={() => {  } }
						>
							<span style={{ fontSize: 20, fontWeight: 900 }}>Schedule</span>
						</div>
					</div>
				</div>
				
				<div style={styles.secondRow}>
					<div style={{ width: "10%", display: "flex", flexDirection: "column", padding: 16, justifyContent: "center", alignItems: "flex-start" }}>
						<div 
							onClick={() => { 
								if (!ventUp) {
									setSignal(HVAC_Type.Station.Row1.Driver.AirDistribution.name, HVAC_Type.Station.Row1.Driver.AirDistribution.allowed.UP);
								}
							}}
							style={{ 
								display: "flex",
								width: 80, height: 80, justifyContent: "center", alignItems: "center",
								borderWidth: 2, borderStyle: "solid", borderColor: "darkgray",
								borderTopLeftRadius: 8, borderTopRightRadius: 8,
								backgroundColor: (ventUp ? MaterialColors.Primary : "#eeeeee") 
							}}
						>
							<img 
								src={ventUp ? "./touchscreen/climate/ventilation-face-active.png" : "./touchscreen/climate/ventilation-face.png"} 
								alt=""
								style={{width: 40, height: 40}}
							/>
						</div>
						<div 
							onClick={() => { 
								if (!ventMiddle) {
									setSignal(HVAC_Type.Station.Row1.Driver.AirDistribution.name, HVAC_Type.Station.Row1.Driver.AirDistribution.allowed.MIDDLE);
								}
							}}
							style={{ 
								display: "flex",
								width: 80, height: 80, justifyContent: "center", alignItems: "center",
								borderWidth: 2, borderTopWidth: 0, borderBottomWidth: 0, borderStyle: "solid", borderColor: "darkgray",
								backgroundColor: (ventMiddle ? MaterialColors.Primary : "#eeeeee") 
							}}
						>
							<img 
								src={ventMiddle ? "./touchscreen/climate/ventilation-middle-active.png" : "./touchscreen/climate/ventilation-middle.png"} 
								alt=""
								style={{width: 40, height: 40}}
							/>
						</div>
						<div 
							onClick={() => { 
								if (!ventDown) {
									setSignal(HVAC_Type.Station.Row1.Driver.AirDistribution.name, HVAC_Type.Station.Row1.Driver.AirDistribution.allowed.DOWN);
								}
							}}
							style={{ 
								display: "flex",
								width: 80, height: 80, justifyContent: "center", alignItems: "center",
								borderWidth: 2, borderStyle: "solid", borderColor: "darkgray",
								borderBottomLeftRadius: 8, borderBottomRightRadius: 8,
								backgroundColor: (ventDown ? MaterialColors.Primary : "#eeeeee") 
							}}
						>
							<img 
								src={ventDown ? "./touchscreen/climate/ventilation-leg-active.png" : "./touchscreen/climate/ventilation-leg.png"} 
								alt=""
								style={{width: 40, height: 40}}
							/>
						</div>
					</div>
					<div style={{ display: "flex", width: "80%", justifyContent: "center", alignItems: "center" }}>
						<img 
							src={"./touchscreen/climate/ClimateControl.png"} 
							alt=""
							style={{ width: 760, height: 337 }} 
						/>
					</div>
					<div style={{ width: "10%", display: "flex", flexDirection: "column", padding: 16, justifyContent: "center", alignItems: "flex-end" }}>
						<div 
							style={{ 
								display: "flex",
								width: 80, height: 80, justifyContent: "center", alignItems: "center",
								borderWidth: 2, borderStyle: "solid", borderColor: "darkgray",
								borderTopLeftRadius: 8, borderTopRightRadius: 8
							}}
						>
							<span style={{ fontSize: 16, fontWeight: 700 }}>Keep</span>
						</div>
						<div 
							style={{ 
								display: "flex",
								width: 80, height: 80, justifyContent: "center", alignItems: "center",
								borderWidth: 2, borderTopWidth: 0, borderBottomWidth: 0, borderStyle: "solid", borderColor: "darkgray" 
							}}
						>
							<span style={{ fontSize: 16, fontWeight: 700 }}>Dog</span>
						</div>
						<div 
							style={{ 
								display: "flex",
								width: 80, height: 80, justifyContent: "center", alignItems: "center", 
								borderWidth: 2, borderStyle: "solid", borderColor: "darkgray",
								borderBottomLeftRadius: 8, borderBottomRightRadius: 8
							}}
						>
							<span style={{ fontSize: 16, fontWeight: 700 }}>Camp</span>
						</div>
					</div>
				</div>
				
				<div style={styles.thirdRow}>
					<div style={{ width: "33%", display: "flex", flexDirection: "row", padding: 16, justifyContent: "flex-start", alignItems: "center" }}>
						<div 
							onClick={() => { 
								if (leftSeatHeaterOn) {
									setSignal(DriverSeatSwitchType.IsWarmerEngaged.name, "False");
								}
								else {
									setSignal(DriverSeatSwitchType.IsWarmerEngaged.name, "True");
								} 
							}}
							style={{ 
								display: "flex",
								width: 80, height: 80, justifyContent: "center", alignItems: "center",
								borderWidth: 2, borderTopLeftRadius: 8, borderBottomLeftRadius: 8, borderStyle: "solid", borderColor: "darkgray" 
							}}
						>
							<img 
								src={leftSeatHeaterOn ? "./touchscreen/climate/car-seat-left-active.png" : "./touchscreen/climate/car-seat-left.png"} 
								alt=""
								style={{width: 40, height: 40}}
							/>
						</div>
						<div 
							onClick={() => { 
								setLeftSeatHeaterAutoOn(!leftSeatHeaterAutoOn); 
							}}
							style={{ 
								display: "flex",
								width: 100, height: 80, justifyContent: "center", alignItems: "center",
								borderWidth: 2, borderLeftWidth: 0, borderTopRightRadius: 8, borderBottomRightRadius: 8, borderStyle: "solid", borderColor: "darkgray",
								backgroundColor: (leftSeatHeaterAutoOn ? MaterialColors.Primary : "#eeeeee") 
							}}
						>
							<span style={{ color: (leftSeatHeaterAutoOn ? "white" : MaterialColors.FontGray), fontSize: 20, fontWeight: 900 }}>
								Auto
							</span>
						</div>
					</div>
					<div style={{ width: "34%", display: "flex", flexDirection: "row", padding: 16, justifyContent: "center", alignItems: "center" }}>
						<div 
							onClick={() => { 
								setSteeringWheelHeaterOn(!steeringWheelHeaterOn); 
							}}
							style={{ 
								display: "flex",
								width: 80, height: 80, justifyContent: "center", alignItems: "center", 
								borderWidth: 2, borderTopLeftRadius: 8, borderBottomLeftRadius: 8, borderStyle: "solid", borderColor: "darkgray" 
							}}
						>
							<img 
								src={steeringWheelHeaterOn ? "./touchscreen/climate/steering_wheel_heated_active.png" : "./touchscreen/climate/steering_wheel_heated.png"} 
								alt=""
								style={{width: 40, height: 40}}
							/>
						</div>
						<div 
							onClick={() => { 
								setWindshieldWiperHeaterOn(!windshieldWiperHeaterOn); 
							}}
							style={{ 
								display: "flex",
								width: 80, height: 80, justifyContent: "center", alignItems: "center",
								borderWidth: 2, borderLeftWidth: 0, borderTopRightRadius: 8, borderBottomRightRadius: 8, borderStyle: "solid", borderColor: "darkgray" 
							}}
						>
							<img 
								src={windshieldWiperHeaterOn? "./touchscreen/climate/windshield_heated_active.png" : "./touchscreen/climate/windshield_heated.png"} 
								alt=""
								style={{width: 40, height: 40}}
							/>
						</div>
					</div>
					<div style={{ width: "33%", display: "flex", flexDirection: "row", padding: 16, justifyContent: "flex-end", alignItems: "center" }}>
						<div 
							onClick={() => {
								setRightSeatHeaterAutoOn(!rightSeatHeaterAutoOn); 
							}}
							style={{ 
								display: "flex",
								width: 100, height: 80, justifyContent: "center", alignItems: "center",
								borderWidth: 2, borderRightWidth: 0, borderTopLeftRadius: 8, borderBottomLeftRadius: 8, borderStyle: "solid", borderColor: "darkgray",
								backgroundColor: (rightSeatHeaterAutoOn ? MaterialColors.Primary : "#eeeeee") 
							}}
						>
							<span style={{ color: (rightSeatHeaterAutoOn ? "white" : MaterialColors.FontGray), fontSize: 20, fontWeight: 900 }}>
									Auto
							</span>
						</div>
						<div 
							onClick={() => { 
								setRightSeatHeaterOn(!rightSeatHeaterOn);
								if (rightSeatHeaterOn) {
									setSignal(PassengerSeatSwitchType.IsWarmerEngaged.name, "False");
								}
								else {
									setSignal(PassengerSeatSwitchType.IsWarmerEngaged.name, "True");
								} 
							}}
							style={{ 
								display: "flex",
								width: 80, height: 80, justifyContent: "center", alignItems: "center",
								borderWidth: 2, borderTopRightRadius: 8, borderBottomRightRadius: 8, borderStyle: "solid", borderColor: "darkgray" 
							}}
						>
							<img 
								src={rightSeatHeaterOn ? "./touchscreen/climate/car-seat-right-active.png" : "./touchscreen/climate/car-seat-right.png"} 
								alt=""
								style={{width: 40, height: 40}}
							/>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}

export default ClimateControl;