import { useEffect, useRef, useState } from "react";
import SocketIOClient from "socket.io-client";

import { getServerIP } from "../../vplug/WsReq";

var webrtc_port = getServerIP() + ":" + process.env.REACT_APP_WEBRTC_PORT;
const callerId = "27099634"; //Math.floor(100000 + Math.random() * 900000).toString();
var socket;
var peerConnection;

export default function WebRTC({ localStream, setLocalStream, remoteStream, setRemoteStream }) {
  //const [localStream, setLocalStream] = useState(null);

  //const [remoteStream, setRemoteStream] = useState(null);

  const [type, setType] = useState('WEBRTC_ROOM');

  const otherUserId = useRef(null);

  const [localMicOn, setlocalMicOn] = useState(true);

  const [localWebcamOn, setlocalWebcamOn] = useState(true);

  // eslint-disable-next-line
  const [callConnected, setCallConnected] = useState(false);

  let remoteRTCMessage = useRef(null);

  useEffect(() => {
    createConnection();

    return () => {
      destroyConnection();
    };
  }, 
  // eslint-disable-next-line
  []);

  function createConnection() {
    //otherUserId.current = "27099634";

    peerConnection = new RTCPeerConnection({
      iceServers: [
        {
          urls: 'stun:stun.l.google.com:19302',
        },
        {
          urls: 'stun:stun1.l.google.com:19302',
        },
        {
          urls: 'stun:stun2.l.google.com:19302',
        },
      ],
    });

    webrtc_port = getServerIP() + ":" + process.env.REACT_APP_WEBRTC_PORT;
    socket = SocketIOClient(webrtc_port, {
      transports: ['websocket'],
      query: {
        callerId,
      },
    });

    socket.on('newCall', data => {
      remoteRTCMessage.current = data.rtcMessage;
      otherUserId.current = data.callerId;
      setType('INCOMING_CALL');
    });

    socket.on('callAnswered', data => {
      remoteRTCMessage.current = data.rtcMessage;
      peerConnection.setRemoteDescription(
        new RTCSessionDescription(remoteRTCMessage.current),
      );
      setType('WEBRTC_ROOM');
    });

    socket.on('ICEcandidate', data => {
      let message = data.rtcMessage;

      if (peerConnection) {
        peerConnection.addIceCandidate(
            new RTCIceCandidate({
              candidate: message.candidate,
              sdpMid: message.id,
              sdpMLineIndex: message.label,
            }),
          )
          .then(data => {
            console.log('SUCCESS');
          })
          .catch(err => {
            console.log('Error', err);
          });
      }
    });

    // Got stream!
    peerConnection.addEventListener('track', gotRemoteStream);
    localStream.getTracks().forEach(track => {
      console.log("local track: ");
      console.log(track);
      peerConnection.addTrack(track, localStream);
    });
    
    peerConnection.onaddstream = event => {
      console.log("onaddstream: ");
      console.log(event);
      setRemoteStream(event.stream);
    };

    // Setup ice handling
    peerConnection.onicecandidate = event => {
      if (event.candidate) {
        sendICEcandidate({
          calleeId: otherUserId.current,
          rtcMessage: {
            label: event.candidate.sdpMLineIndex,
            id: event.candidate.sdpMid,
            candidate: event.candidate.candidate,
          },
        });
      } else {
        console.log('End of candidates.');
      }
    };
  }

  function destroyConnection() {
    if (socket) {
      socket.off('newCall');
      socket.off('callAnswered');
      socket.off('ICEcandidate');
      socket = null;
    }
    peerConnection = null;
  }

  function gotRemoteStream(e) {
    console.log("gotRemoteStream: ");
    console.log(e);
    if (localStream !== e.streams[0]) {
      setRemoteStream(e.streams[0]);
      console.log('pc2 received remote stream');
    }
  }

//   useEffect(() => {
//     InCallManager.start();
//     InCallManager.setKeepScreenOn(true);
//     InCallManager.setForceSpeakerphoneOn(true);

//     return () => {
//       InCallManager.stop();
//     };
//   }, []);

  function sendICEcandidate(data) {
    socket.emit('ICEcandidate', data);
  }

  // eslint-disable-next-line
  async function processCall() {
    const sessionDescription = await peerConnection.createOffer();
    await peerConnection.setLocalDescription(sessionDescription);
    sendCall({
      calleeId: otherUserId.current,
      rtcMessage: sessionDescription,
    });
    setCallConnected(true);
  }

  async function processAccept() {
    peerConnection.setRemoteDescription(
      new RTCSessionDescription(remoteRTCMessage.current),
    );
    const sessionDescription = await peerConnection.createAnswer();
    await peerConnection.setLocalDescription(sessionDescription);
    answerCall({
      callerId: otherUserId.current,
      rtcMessage: sessionDescription,
    });
    setCallConnected(true);
  }

  function answerCall(data) {
    socket.emit('answerCall', data);
  }

  function sendCall(data) {
    socket.emit('call', data);
  }

  useEffect(() => {
    if (type === "INCOMING_CALL") {
      processAccept();
      setType('WEBRTC_ROOM');
    }
    // if (type === "JOIN") {
    //   //otherUserId.current = "27099634";
    //   //console.log('TEST', otherUserId.current);
    //   //setType('OUTGOING_CALL');
    //   //processCall();
    // }
    // else if (type === "WEBRTC_ROOM") {
    //   //
    // }
    // else if (type === "OUTGOING_CALL") {
    //   //setTimeout(() => setType("WEBRTC_ROOM"), 10000);
    // }
  }, 
  // eslint-disable-next-line
  [type]);

  // eslint-disable-next-line
  function switchCamera() {
    localStream.getVideoTracks().forEach(track => {
      track._switchCamera();
    });
  }

  // eslint-disable-next-line
  function toggleCamera() {
    localWebcamOn ? setlocalWebcamOn(false) : setlocalWebcamOn(true);
    localStream.getVideoTracks().forEach(track => {
      localWebcamOn ? (track.enabled = false) : (track.enabled = true);
    });
  }

  function toggleMic() {
    localMicOn ? setlocalMicOn(false) : setlocalMicOn(true);
    localStream.getAudioTracks().forEach(track => {
      localMicOn ? (track.enabled = false) : (track.enabled = true);
    });
  }

  // eslint-disable-next-line
  function leaveCall() {
    peerConnection.close();
    //setLocalStream(null);
    //setType('JOIN');
    setCallConnected(false);
  }

  const WebrtcRoomScreen = () => {
    return (
      <>
        <div
          style={{
            position: "absolute",
            height: 160,
            top: 16,
            right: 16,
            //marginVertical: 12,
            display: "flex",
            flexDirection: 'column',
            justifyContent: 'space-evenly',
          }}>
          {/*callConnected?
            <IconContainer
              backgroundColor={'red'}
              onPress={() => {
                leaveCall();
                destroyConnection();
              }}
              Icon={() => {
                return (
                  <div style={{ color: "white"}} >
                    END
                  </div>
                );
              }}
            />
          :
            <IconContainer
              backgroundColor={'green'}
              onPress={() => {
                createConnection();
                processCall();
              }}
              Icon={() => {
                return (
                  <div style={{ color: "white"}} >
                    CALL
                  </div>
                );
              }}
            />
          */}
          <IconContainer
            backgroundColor={"gray"}
            onPress={() => {
              toggleMic();
            }}
            Icon={() => {
              return (
                <div style={{ color: "white"}} >
                  {localMicOn? "MIC On" : "MIC Off"}
                </div>
              );
            }}
          />
        </div>
      </>
    );
  };

  return WebrtcRoomScreen();
}

const IconContainer = ({backgroundColor, onPress, Icon, style}) => {
  return (
    <div
      onClick={onPress}
      style={{
        ...style,
        backgroundColor: backgroundColor ? backgroundColor : 'transparent',
        borderRadius: 16,
        height: 60,
        width: 120,
        aspectRatio: 1,
        display: "flex",
        justifyContent: 'center',
        alignItems: 'center',
        opacity: 0.5,
      }}>
      <Icon />
    </div>
  );
};
