const PressButton = ({
    isActive,
    onClick=null,
    onMouseDown=null,
    onMouseUp=null,
	activeImage=null,
	inactiveImage,
	IMAGE_WIDTH,
    IMAGE_HEIGHT,
    label=null,
    BORDER_WIDTH=2,
    animated=false,
    backgroundColor="transparent"
}) => {

    const animatedImageStyle = {
        width: IMAGE_WIDTH,
        height: IMAGE_HEIGHT,
        animation: "blink 0.5s",
        animationIterationCount: "infinite",
    };

    const normalImageStyle = {
        width: IMAGE_WIDTH,
        height: IMAGE_HEIGHT
    };

    return (
		<div 
			style={{ 
				width: 48, height: 48, cursor: "pointer", 
				display: "flex", justifyContent: "center", alignItems: "center",
				borderWidth: BORDER_WIDTH, 
				borderStyle: "solid", 
				borderColor: isActive() ? "#fbb03b" : "white",
                backgroundColor: backgroundColor
			}}
			onClick={() => {
				if (onClick) {
					onClick();
				}
			}}
			onMouseDown={() => {
				if (onMouseDown) {
					onMouseDown();
				}
			}}
			onMouseUp={() => {
                if (onMouseUp) {
					onMouseUp();
				}
            }}
		>
            {activeImage &&
                <img
                    src={isActive() ? activeImage : inactiveImage}
                    style={(animated && isActive()) ? animatedImageStyle : normalImageStyle}
                    alt={label}
                />
            }
            {label &&
                <div style={{ fontSize: 36, fontWeight: 700, color: isActive() ? "#fbb03b" : "white" }}>
                    {label}
                </div>
            }
		</div>
	);
}

export default PressButton;