import CarStatus from "./car_status/CarStatus";
import MapSection from "./map/Map";
import NavigationBar from "./nav/NavigationBar";
import { MyTooltip } from "../car_interior/CarInterior";


const Touchscreen = ({
	touchscreenOpen,
	setTouchscreenOpen,
	mainTicks,
	isLargeWindow,
	SCREEN_WIDTH_MIN, SCREEN_HEIGHT_MIN
}) => {

	const BROWSER_WIDTH = window.innerWidth;
	const BROWSER_HEIGHT = window.innerHeight;
	const isNormalSize = (touchscreenOpen || isLargeWindow);

	// 1480 * 924 ?
	const SCREEN_WIDTH = isNormalSize ? 1480 : (BROWSER_WIDTH * 0.8);
	const SCREEN_HEIGHT = isNormalSize ? 924 : (BROWSER_HEIGHT * 0.8);
	const STATUS_BAR_MARGIN = 32;
	
	const LEFT_WIDTH = SCREEN_WIDTH * 0.4;
	//const RIGHT_WIDTH = SCREEN_WIDTH * 0.6;
	const BORDER_WIDTH = isNormalSize ? 24 : 1;

	const TOUCHSCREEN_WIDTH = SCREEN_WIDTH + 2 * BORDER_WIDTH;
	const TOUCHSCREEN_HEIGHT = SCREEN_HEIGHT + 2 * BORDER_WIDTH;
	
	const BOTTOM_TASKBAR_HEIGHT = (SCREEN_HEIGHT - STATUS_BAR_MARGIN) * 0.1; // 10%
	const UPPER_CONTENT_HEIGHT = SCREEN_HEIGHT - BOTTOM_TASKBAR_HEIGHT; // 90%
	const APP_HEIGHT = UPPER_CONTENT_HEIGHT - STATUS_BAR_MARGIN;

	const TOP_OFFSET = isLargeWindow ? (BROWSER_HEIGHT - TOUCHSCREEN_HEIGHT) / 2 : (SCREEN_HEIGHT_MIN - TOUCHSCREEN_HEIGHT) / 2;
	const LEFT_OFFSET = isLargeWindow ? (BROWSER_WIDTH - TOUCHSCREEN_WIDTH) / 2 : (SCREEN_WIDTH_MIN - TOUCHSCREEN_WIDTH) / 2;

	document.addEventListener("contextmenu", function(e) {
		e.preventDefault();
	}, false);

	return (
		<div
			style={{
				position: "absolute",
				zIndex: touchscreenOpen ? 100 : -1,
				top: TOP_OFFSET,
				left: LEFT_OFFSET,
				width: TOUCHSCREEN_WIDTH,
				height: TOUCHSCREEN_HEIGHT,
				border: BORDER_WIDTH + "px solid #0e0e0e",
				borderRadius: 16,
				backgroundColor: "#0e0e0e",

				display: "flex",
				flexDirection: "column",
				justifyContent: "center",
				alignItems: "center"
			}}
		>
			<div style={{ width: "100%", height: UPPER_CONTENT_HEIGHT, display: "flex", flexDirection: "row" }}>
				<div style={{ width: "40%", height: "100%" }}>
					<CarStatus/>
				</div>
				<div style={{ width: "60%", height: "100%" }}>
					<MapSection mainTicks={mainTicks} />
				</div>
			</div>
			<div style={{ width: "100%", height: BOTTOM_TASKBAR_HEIGHT }}>
				<NavigationBar
					APP_LEFT={LEFT_WIDTH} APP_HEIGHT={APP_HEIGHT}
				/>
			</div>

			<div
				onClick={() => setTouchscreenOpen(false)}
				style={{
					position: "absolute", left: 0, bottom: 0, width: 48, height: 48,
					display: "flex", justifyContent: "center", alignItems: "center",
					cursor: "pointer"
				}}
				id="enlarged-touchscreen"
			>
				<img
                    src={"./touchscreen/minimize.png"}
                    style={{ width: 40, height: 40 }}
                    alt="minimize"
                />
			</div>

			<MyTooltip id="enlarged-touchscreen" label="Close Touchscreen" />
		</div>
	);
}

export default Touchscreen;