import { useEffect, useState } from "react";
import styled from "styled-components";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";


import CarControlTabs from "../car_control/CarControlTabs";
import NavigationBarItem from "./NavigationBarItem";
import MediaMain from "../media/MediaMain";
import Dashcam from "../camera/Dashcam2";

import TemperatureControl from "../climate/TemperatureControl";
import ClimateControl from "../climate/ClimateControl";
import VolumeControl from "../media/VolumeControl";
import { MyTooltip } from "../../car_interior/CarInterior";

import { SIGNAL_TYPES, subscribeSignals, unsubscribeSignals } from "../../vplug/WsReq";
import vssApi from "../../vplug/VssAPI.json";
import { signalMap } from "../../vplug/Signals";


const NavigationBar = ({
	APP_LEFT, APP_HEIGHT
}) => {
	
	const [tabIndex, setTabIndex] = useState(-1);
	const [appStatus, setAppStatus] = useState([false, false, false, false]);
	const [volumeRefresh, setVolumeRefresh] = useState(false);

	const [driverTemperature, setDriverTemperature] = useState(25);
	const [passengerTemperature, setPassengerTemperature] = useState(25);

	useEffect(() => {
		subscribeSignals(subscriptionData, subscriptionCallback);

		return () => {
			unsubscribeSignals({ channel: subscriptionData.channel });
		};
	}, 
	// eslint-disable-next-line
	[]);

	const subscriptionData = {
		channel: "touchscreen/navigation-bar",
		type: SIGNAL_TYPES.VSS,
		signals: [
			{ name: vssApi.Vehicle.Cabin.HVAC.Station.Row1.Driver.Temperature.name },
			{ name: vssApi.Vehicle.Cabin.HVAC.Station.Row1.Passenger.Temperature.name }
		]
	};

	const subscriptionCallback = (msg) => {
		for (const signal of msg.signals) {
			if (signal.value != null && signal.value !== "undefined") {
				if (signal.name === vssApi.Vehicle.Cabin.HVAC.Station.Row1.Driver.Temperature.name) {
					setDriverTemperature(signal.value);
				}
				else if (signal.name === vssApi.Vehicle.Cabin.HVAC.Station.Row1.Passenger.Temperature.name) {
					setPassengerTemperature(signal.value);
				}

				signalMap.set(signal.name, signal.value);
			}
		}
	}

	function updateAppStatus(app) {
		const status = [];
		let anyAppActive = false;
		for (let i = 0; i < appStatus.length; i++) {
			status[i] = false;
			if (appStatus[i]) {
				anyAppActive = true;
			}
		}
		if (!appStatus[app]) {
			status[app] = true;
			if (anyAppActive) {
				setTimeout(() => {
					setAppStatus(status);
				}, 600);
				return;
			}
		}
		setAppStatus(status);
	}

	return (
		<StyledDiv className="navigation-bar" APP_LEFT={APP_LEFT} APP_HEIGHT={APP_HEIGHT} appStatus={appStatus} >
			<Tabs
				selectedIndex={tabIndex}
				onSelect={(index) => setTabIndex(index !== tabIndex ? index : -1)}
				forceRenderTabPanel={true}
				// forceRender={true}
			>
				<TabList className="nav-bar-tab-list">
					<Tab>
						<div
							onClick={() => updateAppStatus(APP_TYPES.CAR_CONTROL)}
							id="car-control"
						>
							<NavigationBarItem
								background={
									tabIndex === APP_TYPES.CAR_CONTROL ? 
									"./touchscreen/nav/car-control-active.png" : "./touchscreen/nav/car-control.png"
								}
								activeBackground={null}
								activeBackgroundBlur={null}
								size="48px"
							/>
						</div>
					</Tab>
					<Tab>
						<div
							onClick={() => updateAppStatus(APP_TYPES.MEDIA)}
							id="media"
						>
							<NavigationBarItem
								background={
									tabIndex === APP_TYPES.MEDIA ? 
									"./touchscreen/nav/media-active.png" : "./touchscreen/nav/media.png"
								}
								activeBackground={null}
								activeBackgroundBlur={null}
								size="36px"
							/>
						</div>
					</Tab>
					<Tab>
						<div
							onClick={() => updateAppStatus(APP_TYPES.DASHCAM)}
							id="camera"
						>
							<NavigationBarItem
								background={
									tabIndex === APP_TYPES.DASHCAM ? 
									"./touchscreen/nav/dashcam-active.png" : "./touchscreen/nav/dashcam.png"
								}
								activeBackground={null}
								activeBackgroundBlur={null}
								size="48px"
							/>
						</div>
					</Tab>

					<div id="left-temp">
						<TemperatureControl signalName={vssApi.Vehicle.Cabin.HVAC.Station.Row1.Driver.Temperature.name} temperature={driverTemperature} />
					</div>
					<Tab>
						<div
							onClick={() => updateAppStatus(APP_TYPES.CLIMATE)}
							id="climate"
						>
							<NavigationBarItem
								background={
									tabIndex === APP_TYPES.CLIMATE ? 
									"./touchscreen/nav/climate-active.png" : "./touchscreen/nav/climate.png"
								}
								activeBackground={null}
								activeBackgroundBlur={null}
								size="36px"
							/>
						</div>
					</Tab>
					<div id="right-temp">
						<TemperatureControl signalName={vssApi.Vehicle.Cabin.HVAC.Station.Row1.Passenger.Temperature.name} temperature={passengerTemperature} />
					</div>

					<div id="volume-control">
						<VolumeControl
							refresh={volumeRefresh} setRefresh={setVolumeRefresh}
							id="volume-control" 
						/>
					</div>

					<MyTooltip id="car-control" label="Car Controls" />
					<MyTooltip id="media" label="Media Apps" />
					<MyTooltip id="camera" label="Camera" />
					<MyTooltip id="left-temp" label="Driver-side Temperature" />
					<MyTooltip id="climate" label="Climate" />
					<MyTooltip id="right-temp" label="Passenger-side Temperature" />
					<MyTooltip id="volume-control" label="Volume Control" />
				</TabList>

				<div className="nav-bar-tab-panels">
					<TabPanel>
						<div
							style={{
								maxHeight: appStatus[APP_TYPES.CAR_CONTROL] ? APP_HEIGHT + "px" : "0vh",
								transition: "all 0.6s ease",
							}}
						>
							<CarControlTabs APP_HEIGHT={APP_HEIGHT}/>
						</div>
					</TabPanel>
					<TabPanel>
						<div
							style={{
								maxHeight: appStatus[APP_TYPES.MEDIA] ? APP_HEIGHT + "px" : "0vh",
								transition: "all 0.6s ease",
							}}
						>
							<MediaMain 
								APP_HEIGHT={APP_HEIGHT}
								setVolumeRefresh={setVolumeRefresh}
							/>
						</div>
					</TabPanel>
					<TabPanel>
						<div
							style={{
								maxHeight: appStatus[APP_TYPES.DASHCAM] ? APP_HEIGHT + "px" : "0vh",
								transition: "all 0.6s ease",
							}}
						>
							<Dashcam active={appStatus[APP_TYPES.DASHCAM]} APP_HEIGHT={APP_HEIGHT}/>
						</div>
					</TabPanel>
					<TabPanel>
						<div
							style={{
								maxHeight: appStatus[APP_TYPES.CLIMATE] ? APP_HEIGHT + "px" : "0vh",
								transition: "all 0.6s ease",
							}}
						>
							<ClimateControl APP_HEIGHT={APP_HEIGHT}/>
						</div>
					</TabPanel>
				</div>
			</Tabs>
		</StyledDiv>
	);
}

export default NavigationBar;

const APP_TYPES = {
	CAR_CONTROL : 0,
	MEDIA      : 1,
	DASHCAM    : 2,
	CLIMATE    : 3
};

const StyledDiv = styled.div`
	background-color: #000000;
	border-bottom-left-radius: 8px;
	border-bottom-right-radius: 8px;
	padding-top: 16px;
	padding-bottom: 16px;
	z-index: 10;
	height: 92px;

	.navigation {
		display: flex;
	}
	.react-tabs {
		position: relative;
	}

	.nav-bar-tab-list {
		margin: 0;
		list-style-type: none;
		padding: 0;
		padding-left: 96px;
		padding-right: 96px;
		display: flex;
		justify-content: space-between;
		//gap: 48px;
		align-items: center;
	}
	.nav-bar-tab-panels {
		position: absolute;
		bottom: 76px;
		right: 0px;
		left: ${(props) => ((props.appStatus[APP_TYPES.CAR_CONTROL] || props.appStatus[APP_TYPES.MEDIA]) ? props.APP_LEFT : 0)}px;
		overflow: hidden;
		//transition: all 0.6s ease;
	}

	.react-tabs__tab-panel {
		max-height: 0;
		overflow: hidden;
		//transition: all 0.6s ease;
	}
	.react-tabs__tab-panel.react-tabs__tab-panel--selected {
		max-height: ${(props) => (props.APP_HEIGHT)}px;
		transition: all 0.6s ease;
	}
`;
