const LockStatus = ({
	carLocked, departureReady, handleCarLock = null
}) => {
	return (
		<div 
			style={{
				width: "100%",
				height: "100%",
				display: "flex",
				flexDirection: "row",
				justifyContent: "center",
				alignItems: "center",
				cursor: departureReady? "default" : "pointer"
			}}
			onClick={() => {
				if (!departureReady && handleCarLock) {
					handleCarLock();
				}
			}}
		>
			<img
				style={{ width: 48, height: 48 }}
				src={carLocked ? "./exterior/padlock-locked.png" : "./exterior/padlock-unlocked.png"}
				alt=""
			/>
		</div>
	);
}

export default LockStatus;