import MyGoogleMap from "./MyGoogleMap";
import ReactStreetviewMulti from "./ReactStreetviewMulti";
import { MyTooltip } from "../car_interior/CarInterior";


const RightSideBar = ({
    isLoaded,
    map,
    setMap,
    carPosition,
    setCarPosition,
    mapType,
    heading,
    directionsResponse,
    speed,
    autopilotOn,
    changeMapType,
    MAP_API_KEY,
    streetViewPanoramaOptions
}) => {

    return (
        <div style={{
            width: "18%",
            height: "100%",
            backgroundColor: '#f8f8f8'
        }}>
            <div style={{ position: "relative", height: "50%", width: "100%" }}>
                {carPosition && isLoaded &&
                    <MyGoogleMap
                        map={map}
                        setMap={setMap}
                        carPosition={carPosition}
                        setCarPosition={setCarPosition}
                        mapType={mapType}
                        heading={heading}
                        directionsResponse={directionsResponse}
                        speed={speed}
                        autopilotOn={autopilotOn}
                    />
                }
                <div
                    style={{
                        position: "absolute", zIndex: 1,
                        left: 8, bottom: 8,
                        display: "flex", flexDirection: "column", gap: 8,
                        backgroundColor: "transparent", cursor: "pointer"
                    }}
                    id="map-type"
                >
                    <div
                        onClick={changeMapType}
                        style={{
                            width: 48, height: 36, borderRadius: 8,
                            display: "flex", justifyContent: "center", alignItems: "center",
                            backgroundColor: "darkgray", opacity: 0.8, color: "white", fontSize: 16
                        }}
                    >
                        &gt;&gt;
                    </div>
                </div>
            </div>
            <div style={{ height: "50%", width: "100%" }}>
                {streetViewPanoramaOptions &&
                    <ReactStreetviewMulti
                        viewId={"right-side-mirror"}
                        apiKey={MAP_API_KEY}
                        streetViewPanoramaOptions={
                            {
                                position: streetViewPanoramaOptions.position,
                                pov: { heading: (streetViewPanoramaOptions.pov.heading + 135), pitch: 0 },
                                zoom: 1
                            }
                        }
                    />
                }
            </div>

            <MyTooltip id="map-type" label={"Change Map Type"} />
        </div>
    );
}

export default RightSideBar;