import { useState } from "react";
import styled from "styled-components";
import SelectionControl from "./components/SelectControl";
import SwitchControl from "./components/SwitchControl";


const LocksControl = () => {

	const [walkwayDoorLock, setWalkwayDoorLock] = useState(true);
	const [driverDoorUnlockMode, setDriverDoorUnlockMode] = useState(false);
	const [unlockOnPark, setUnlockOnPark] = useState(true);

	const [carLeftOpenNotifications, setCarLeftOpenNotifications] = useState("Doors & Windows");

	const [lockConfirmationSound, setLockConfirmationSound] = useState(true);
	const [closeWindowsOnLock, setCloseWindowsOnLock] = useState(true);

	return (
		<StyledDiv>
			<div style={{ display: "flex", flexDirection: "row", gap: 16 }}>
				<div 
					onClick={() => {
					}}
					style={{
						width: 225, height: 64, display: "flex", justifyContent: "center", alignItems: "center",
						//borderWidth: 2, borderRadius: 8, borderColor: "darkgray", borderStyle: "solid",
						backgroundColor: "lightgray", borderRadius: 8,
						marginTop: 16
					}}
				>
					<div style={{ fontSize: 18, fontWeight: 700 }}>Window Lock</div>
				</div>
				<div 
					onClick={() => {
					}}
					style={{
						width: 225, height: 64, display: "flex", justifyContent: "center", alignItems: "center",
						//borderWidth: 2, borderRadius: 8, borderColor: "darkgray", borderStyle: "solid",
						backgroundColor: "lightgray", borderRadius: 8,
						marginTop: 16
					}}
				>
					<div style={{ fontSize: 18, fontWeight: 700 }}>Child Lock</div>
				</div>
			</div>

			<SwitchControl
				name="Walk-Away Door Lock"
				value={walkwayDoorLock}
				setValue={setWalkwayDoorLock}
				additionalCheck={"Exclude Home"}
				desc={[
					"Automatically lock doors and trunks when leaving with a phone or key fob",
					"Walk-Away Door Lock is disabled at this location"
				]}
			/>
			<SwitchControl
				name="Driver Door Unlock Mode"
				value={driverDoorUnlockMode}
				setValue={setDriverDoorUnlockMode}
				desc={["Only unlock the driver door when first unlocking the vehicle"]}
			/>
			<SwitchControl
				name="Unlock on Park"
				value={unlockOnPark}
				setValue={setUnlockOnPark}
			/>

			<SelectionControl
				name="Car Left Open Notifications"
				valueList={["Off", "Doors", "Doors & Windows"]}
				value={carLeftOpenNotifications}
				setValue={setCarLeftOpenNotifications}
				additionalCheck={"Exclude Home"}
				desc={[
					"",
					"You will be notified if any trunk or door is left open",
					"You will be notified if any trunk, door or window is left open"
				]}
			/>

			<SwitchControl
				name="Lock Confirmation Sound"
				value={lockConfirmationSound}
				setValue={setLockConfirmationSound}
			/>
			<SwitchControl
				name="Close Windows On Lock"
				value={closeWindowsOnLock}
				setValue={setCloseWindowsOnLock}
			/>
		</StyledDiv>
	);
}

export default LocksControl;

const StyledDiv = styled.div`
	width: 100%;
	height: 100%;
	background-color: #f0f0f0;
	padding: 32px;
	position: relative;
	color: #6F6F6F;
`;
