import { useEffect, useState } from "react";
import styled from "styled-components";
import ReactPlayer from 'react-player/lazy';

import MediaApps from "./MediaApps";
import MediaPlayer from "./MediaPlayer";
import { RadioStreamingList, getInternetStations, internetRadioUrls } from "./RadioStreamingList";


const MediaMain = ({ 
	APP_HEIGHT,
	setVolumeRefresh
}) => {

	const [allAppsView, setAllAppsView] = useState(true);
	const [currentApp, setCurrentApp] = useState(null);
	const [stations, setStations] = useState([]);
	const [currentStation, setCurrentStation] = useState(0);
	const [stationUrl, setStationUrl] = useState("");
	const [initComplete, setInitComplete] = useState(false);

	const [currentPlaying, setCurrentPlaying] = useState(null);
	const [isPlaying, setIsPlaying] = useState(false);
	const [expand, setExpand] = useState(true);

	useEffect(() => {
		if (!initComplete) {
			if (playerMemory.available) {
				setAllAppsView(!playerMemory.available);
				setCurrentApp(playerMemory.currentApp);
				setStations(playerMemory.stations);
				setCurrentStation(playerMemory.currentStation);
				setStationUrl(playerMemory.stationUrl);
				setIsPlaying(playerMemory.isPlaying);
				if (playerMemory.isPlaying) {
					if (playerMemory.currentApp.name === "Radio" || playerMemory.currentApp.name === "BBC Radio") {
						setCurrentPlaying(playerMemory.stations[playerMemory.currentStation]);
					}
				}
			}
			else {
				setStations(internetRadioUrls.list.filter(isKoreanStation));
			}

			setInitComplete(true);
		}
	}, 
	// eslint-disable-next-line
	[]);

	const nextSong = () => {
		let newIndex;
		if (currentApp) {
			if (currentApp.name === "Radio" || currentApp.name === "BBC Radio") {
				if (stations.length > 0) {
					if (currentStation + 1 < stations.length) {
						newIndex = currentStation + 1;
					} else {
						newIndex = 0;
					}
					setCurrentStation(newIndex);
					setStationUrl(stations[newIndex].url);
				}
			}
		}
	};

	const prevSong = () => {
		let newIndex;
		if (currentApp) {
			if (currentApp.name === "Radio" || currentApp.name === "BBC Radio") {
				if (stations.length > 0) {
					if (currentStation > 0) {
						newIndex = currentStation - 1;
					} else {
						newIndex = stations.length - 1;
					}
					setCurrentStation(newIndex);
					setStationUrl(stations[newIndex].url);
				}
			}
		}
	};

	useEffect(() => {
		if (initComplete) {
			if (currentApp) {
				if (currentApp.name === "Radio" || currentApp.name === "BBC Radio") {
					if (currentStation < stations.length && stationUrl != null && stationUrl !== "") {
						setCurrentPlaying(stations[currentStation]);
						try {
							playerMemory.currentApp = currentApp;
							playerMemory.currentStation = currentStation;
							playerMemory.stationUrl = stationUrl;
							playerMemory.stations = stations;
							playerMemory.isPlaying = true;
							playerMemory.available = true;
						} catch (e) {
							playerMemory.isPlaying = false;
							console.log(`cannot play the sound file`, e);
						}
					}
	
					setAllAppsView(false);
				}
				else if (currentApp.url.length === 0) {
					playerMemory.currentApp = currentApp;
					playerMemory.isPlaying = true;
					playerMemory.available = true;
	
					setCurrentPlaying({
						name: currentApp.name,
						desc: currentApp.desc,
						image: currentApp.image
					});
	
					setAllAppsView(false);
				}
				else {
					playerMemory.available = false;
				}
			}
			else {
				setCurrentPlaying({name:"", image:"", desc: ""});
				playerMemory.available = false;
			}
		}
	}, 
	// eslint-disable-next-line
	[currentApp, currentStation, stationUrl]);

	async function toggleExpand() {
		setExpand(!expand);
	}

	function listRadioStations() {
		if (!internetRadioUrls.allOK) {
			getInternetStations(setRegionalStations, setStationUrl);
		}
		return (
			<>
				{stations.map((station, idx) =>
					<RadioStreamingList
						channel={station}
						currentIndex={idx}
						setCurrentStation={setCurrentStation}
						setStationUrl={setStationUrl}
						key={station.name}
					/>
				)}
			</>
		);
	}

	function isBBCStation(station) {
		return (station.name.indexOf("BBC") > -1);
	}

	function isKoreanStation(station) {
		return (station.name.indexOf("BBC") === -1);
	}

	function setRegionalStations(app = currentApp) {
		let stations;
		if (!app || app.name === "Radio") {
			stations = internetRadioUrls.list.filter(isKoreanStation);
		}
		else {
			stations = internetRadioUrls.list.filter(isBBCStation);
		}
		setStations(stations);
		return stations;
	}

	function _onReady(event) {
		// access to player in all event handlers via event.target
		//if (currentApp.name === "Radio" || currentApp.name === "BBC Radio") {
		//	setCurrentPlaying(stations[currentStation]);
		//}
	}

	return (
		<div
			style={{
				backgroundColor: "#ffffff",
				height: (expand? APP_HEIGHT : 80),
				position: "relative",
				zIndex: 2,
				width: "100%",
				color: "#707070"
			}}
		>
			<MediaPlayer
				isPlaying={isPlaying}
				currentPlaying={currentPlaying}
				setIsPlaying={setIsPlaying}
				nextSong={nextSong}
				prevSong={prevSong}
				setExpand={setExpand}
				toggleExpand={toggleExpand}
				expand={expand}
				currentApp={currentApp}
				setVolumeRefresh={setVolumeRefresh}
			/>

			{allAppsView ?
				<div style={mediaMainStyle.mediaApps}>
					<MediaApps
						currentApp={currentApp}
						setCurrentApp={setCurrentApp}
						setRegionalStations={setRegionalStations}
						setCurrentStation={setCurrentStation}
						setStationUrl={setStationUrl}
						setCurrentPlaying={setCurrentPlaying}
						allAppsView={allAppsView}
						setAllAppsView={setAllAppsView}
					>
					</MediaApps>
				</div>
				:
				<>
					{currentApp && (currentApp.name === "Radio" || currentApp.name === "BBC Radio") &&
						<>
							<StyledRadioList>
								{listRadioStations()}
							</StyledRadioList>
							<ReactPlayer
								id="myRadio"
								controls={false}
								playing={true}
								muted={false}
								width="0px"
								height="0px"
								url={stationUrl}
								onReady={_onReady}
							/>
						</>
					}
				</>
			}
			{expand && !allAppsView &&
				<div 
					onClick={() => { 
						setAllAppsView(true);
						setCurrentApp(null);
					}}
					style={{
						position: "absolute", right: 10, bottom: 10, width: 96, height: 48, 
						display: "flex", justifyContent: "center", alignItems: "center", 
						backgroundColor: "gray", opacity: 0.5, borderRadius: 40,
						fontSize: 24, color: "white", cursor: "pointer"
					}}
				>
					<div>Exit</div>
				</div>
			}
		</div>
	);
}

export default MediaMain;

const playerMemory = {
	available: false,
	currentApp: null,
	currentStation: 0,
	stationUrl: "",
	isPlaying: false,
	stations: []
};

const mediaMainStyle = {
	position: "absolute", 
    left: 0, 
    top: 0,  
    width: "100%", 
    height: "100%", 
    backgroundColor:"white",

	mediaApps: {
		padding: 8,
		display: "flex",
		flexDirection: "row",
		flexWrap: "wrap",
		alignItems: "center", 
	},

	radioPlaylist: {
		padding: 8,
		display: "flex",
		flexDirection: "row",
		flexWrap: "wrap",
		alignItems: "center",
		width: "100%",
		maxHeight: "calc(100% - 84px)",
		overflow: "scroll"
	},
};

const StyledRadioList = styled.div`
	padding: 8px;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: center;
	width: 100%;
	max-height: calc(100% - 84px);
	overflow: scroll;

	/* width */
	::-webkit-scrollbar {
  		width: 10px;
	}

	/* Track */
	::-webkit-scrollbar-track {
  		background: #f1f1f1;
	}

	/* Handle */
	::-webkit-scrollbar-thumb {
  		background: #888;
	}

	/* Handle on hover */
	::-webkit-scrollbar-thumb:hover {
  		background: #555;
	}
`;
