import { useState } from "react";
import styled from "styled-components";
import SelectionControl from "./components/SelectControl";
import SwitchControl from "./components/SwitchControl";


const LightsControl = () => {

	const [headlights, setHeadlights] = useState("Auto");
	const [domeLights, setDomeLights] = useState("Auto");
	const [ambientIndicatorMode, setAmbientIndicatorMode] = useState("Auto Cancel");

	const [autoHighBeam, setAutoHighBeam] = useState(true);
	const [headlightsAfterExit, setHeadlightsAfterExit] = useState(true);
	const [steeringWheelLights, setSteeringWheelLights] = useState(true);

	return (
		<StyledDiv>
			<SelectionControl
				icon={"./touchscreen/car_control/beam.png"}
				name="Headlights"
				valueList={["Off", "Parking", "On", "Auto"]}
				value={headlights}
				setValue={setHeadlights}
			/>
			<div style={{ display: "flex", flexDirection: "row", gap: 16 }}>
				<div 
					onClick={() => {
					}}
					style={{
						width: 225, height: 64, display: "flex", justifyContent: "center", alignItems: "center",
						//borderWidth: 2, borderRadius: 8, borderColor: "darkgray", borderStyle: "solid",
						backgroundColor: "lightgray", borderRadius: 8,
						marginTop: 16
					}}
				>
					<div style={{ fontSize: 18, fontWeight: 700 }}>Front Fog</div>
				</div>
				<div 
					onClick={() => {
					}}
					style={{
						width: 225, height: 64, display: "flex", justifyContent: "center", alignItems: "center",
						//borderWidth: 2, borderRadius: 8, borderColor: "darkgray", borderStyle: "solid",
						backgroundColor: "lightgray", borderRadius: 8,
						marginTop: 16
					}}
				>
					<div style={{ fontSize: 18, fontWeight: 700 }}>Rear Fog</div>
				</div>
			</div>

			<SelectionControl
				name="Dome Lights"
				valueList={["Off", "On", "Auto"]}
				value={domeLights}
				setValue={setDomeLights}
			/>

			<div 
				onClick={() => {
				}}
				style={{
					width: 300, height: 64, display: "flex", justifyContent: "center", alignItems: "center",
					//borderWidth: 2, borderRadius: 8, borderColor: "darkgray", borderStyle: "solid",
					backgroundColor: "lightgray", borderRadius: 8,
					marginTop: 16
				}}
			>
				<div style={{ fontSize: 18, fontWeight: 700 }}>Ambient Lights</div>
			</div>
			<SelectionControl
				name="Ambient Indicator Mode"
				valueList={["Off", "Auto Cancel"]}
				value={ambientIndicatorMode}
				setValue={setAmbientIndicatorMode}
			/>
			<SwitchControl
				name="Auto High Beam"
				value={autoHighBeam}
				setValue={setAutoHighBeam}
			/>
			<SwitchControl
				name="Headlights After Exit"
				value={headlightsAfterExit}
				setValue={setHeadlightsAfterExit}
			/>
			<SwitchControl
				name="Steering Wheel Lights"
				value={steeringWheelLights}
				setValue={setSteeringWheelLights}
			/>
		</StyledDiv>
	);
}

export default LightsControl;

const StyledDiv = styled.div`
	width: 100%;
	height: 100%;
	background-color: #f0f0f0;
	padding: 32px;
	position: relative;
	color: #6F6F6F;
`;
