import { useEffect, useState } from "react";

import { SIGNAL_TYPES, subscribeSignals, unsubscribeSignals } from "../../vplug/WsReq";
import vssApi from "../../vplug/VssAPI.json";

const IndicatorOverlay = ({
	gear,
	frunkOpen, trunkOpen, chargePortOpen,
	autopilotOn
}) => {

	const Row1DriverSideDoorType = vssApi.Vehicle.Cabin.Door.Row1.DriverSide;
	const Row1DriverSideSeatType = vssApi.Vehicle.Cabin.Seat.Row1.DriverSide;
	const Row1PassengerSideDoorType = vssApi.Vehicle.Cabin.Door.Row1.PassengerSide;
	const Row1PassengerSideSeatType = vssApi.Vehicle.Cabin.Seat.Row1.PassengerSide;
	const Row2DriverSideDoorType = vssApi.Vehicle.Cabin.Door.Row2.DriverSide;
	const Row2DriverSideSeatType = vssApi.Vehicle.Cabin.Seat.Row2.DriverSide;
	const Row2PassengerSideDoorType = vssApi.Vehicle.Cabin.Door.Row2.PassengerSide;
	const Row2PassengerSideSeatType = vssApi.Vehicle.Cabin.Seat.Row2.PassengerSide;

	const [parkingBrakeEngaged, setParkingBrakeEngaged] = useState(false);

	const [lowBeamOn, setLowBeamOn] = useState(false);
	const [highBeamOn, setHighBeamOn] = useState(false);
	const [frontFogLightOn, setFrontFogLightOn] = useState(false);
	const [rearFogLightOn, setRearFogLightOn] = useState(false);
	const [parkingLightOn, setParkingLightOn] = useState(false);
	
	const [row1LeftTirePressureLow, setRow1LeftTirePressureLow] = useState(false);
	const [row1RightTirePressureLow, setRow1RightTirePressureLow] = useState(false);
	const [row2LeftTirePressureLow, setRow2LeftTirePressureLow] = useState(false);
	const [row2RightTirePressureLow, setRow2RightTirePressureLow] = useState(false);

	const [row1DriverSeatOccupied, setRow1DriverSeatOccupied] = useState(false);
	const [row1PassengerSeatOccupied, setRow1PassengerSeatOccupied] = useState(false);
	const [row2DriverSeatOccupied, setRow2DriverSeatOccupied] = useState(false);
	const [row2PassengerSeatOccupied, setRow2PassengerSeatOccupied] = useState(false);
	const [row1DriverSeatBelted, setRow1DriverSeatBelted] = useState(false);
	const [row1PassengerSeatBelted, setRow1PassengerSeatBelted] = useState(false);
	const [row2DriverSeatBelted, setRow2DriverSeatBelted] = useState(false);
	const [row2PassengerSeatBelted, setRow2PassengerSeatBelted] = useState(false);

	const [frontDefrosterActive, setFrontDefrosterActive] = useState(false);
	const [RearDefrosterActive, setRearDefrosterActive] = useState(false);
	
	const [row1DriverDoorOpen, setRow1DriverDoorOpen] = useState(false);
	const [row1PassengerDoorOpen, setRow1PassengerDoorOpen] = useState(false);
	const [row2DriverDoorOpen, setRow2DriverDoorOpen] = useState(false);
	const [row2PassengerDoorOpen, setRow2PassengerDoorOpen] = useState(false);

	const [trailerMode, setTrailerMode] = useState(false);

	const [ABSError, setABSError] = useState(false);
	const [stabilityControlOn, setStabilityControlOn] = useState(false);

	const signalList = new Map();
	const subscriptionData = {
		channel: "touchscreen/cluster-indicators",
		type: SIGNAL_TYPES.VSS,
		signals: []
	};

	useEffect(() => {
		initSignalList();
		subscribeSignals(subscriptionData, subscriptionCallback);

		return () => {
			unsubscribeSignals({ channel: subscriptionData.channel });
		};
	}, 
	// eslint-disable-next-line
	[]);

	const subscriptionCallback = (msg) => {
		for (const signal of msg.signals) {
			if (signal.value != null && signal.value !== "undefined") {
				const callback = signalList.get(signal.name);
				if (callback) {
					callback(signal.value);
				}
			}
		}
	}

	function initSignalList() {
		// Lights
		signalList.set(vssApi.Vehicle.Chassis.ParkingBrake.IsEngaged.name, (value) => {
			setParkingBrakeEngaged(value === "True");
		});
		signalList.set(vssApi.Vehicle.Body.Lights.Beam.Low.IsOn.name, (value) => {
			setLowBeamOn(value === "True");
		});
		signalList.set(vssApi.Vehicle.Body.Lights.Beam.High.IsOn.name, (value) => {
			setHighBeamOn(value === "True");
		});
		signalList.set(vssApi.Vehicle.Body.Lights.Fog.Front.IsOn.name, (value) => {
			setFrontFogLightOn(value === "True");
		});
		signalList.set(vssApi.Vehicle.Body.Lights.Fog.Rear.IsOn.name, (value) => {
			setRearFogLightOn(value === "True");
		});
		signalList.set(vssApi.Vehicle.Body.Lights.Parking.IsOn.name, (value) => {
			setParkingLightOn(value === "True");
		});
		// Tire Pressure
		signalList.set(vssApi.Vehicle.Chassis.Axle.Row1.Wheel.Left.Tire.IsPressureLow.name, (value) => {
			setRow1LeftTirePressureLow(value === "True");
		});
		signalList.set(vssApi.Vehicle.Chassis.Axle.Row1.Wheel.Right.Tire.IsPressureLow.name, (value) => {
			setRow1RightTirePressureLow(value === "True");
		});
		signalList.set(vssApi.Vehicle.Chassis.Axle.Row2.Wheel.Left.Tire.IsPressureLow.name, (value) => {
			setRow2LeftTirePressureLow(value === "True");
		});
		signalList.set(vssApi.Vehicle.Chassis.Axle.Row2.Wheel.Right.Tire.IsPressureLow.name, (value) => {
			setRow2RightTirePressureLow(value === "True");
		});
		// Seat Belt
		signalList.set(Row1DriverSideSeatType.IsOccupied.name, (value) => {
			setRow1DriverSeatOccupied(value === "True");
		});
		signalList.set(Row1PassengerSideSeatType.IsOccupied.name, (value) => {
			setRow1PassengerSeatOccupied(value === "True");
		});
		signalList.set(Row2DriverSideSeatType.IsOccupied.name, (value) => {
			setRow2DriverSeatOccupied(value === "True");
		});
		signalList.set(Row2PassengerSideSeatType.IsOccupied.name, (value) => {
			setRow2PassengerSeatOccupied(value === "True");
		});
		signalList.set(Row1DriverSideSeatType.IsBelted.name, (value) => {
			setRow1DriverSeatBelted(value === "True");
		});
		signalList.set(Row1PassengerSideSeatType.IsBelted.name, (value) => {
			setRow1PassengerSeatBelted(value === "True");
		});
		signalList.set(Row2DriverSideSeatType.IsBelted.name, (value) => {
			setRow2DriverSeatBelted(value === "True");
		});
		signalList.set(Row2PassengerSideSeatType.IsBelted.name, (value) => {
			setRow2PassengerSeatBelted(value === "True");
		});
		// Defroster
		signalList.set(vssApi.Vehicle.Cabin.HVAC.IsFrontDefrosterActive.name, (value) => {
			setFrontDefrosterActive(value === "True");
		});
		signalList.set(vssApi.Vehicle.Cabin.HVAC.IsRearDefrosterActive.name, (value) => {
			setRearDefrosterActive(value === "True");
		});
		// Doors
		signalList.set(Row1DriverSideDoorType.IsOpen.name, (value) => {
			setRow1DriverDoorOpen(value === "True");
		});
		signalList.set(Row1PassengerSideDoorType.IsOpen.name, (value) => {
			setRow1PassengerDoorOpen(value === "True");
		});
		signalList.set(Row2DriverSideDoorType.IsOpen.name, (value) => {
			setRow2DriverDoorOpen(value === "True");
		});
		signalList.set(Row2PassengerSideDoorType.IsOpen.name, (value) => {
			setRow2PassengerDoorOpen(value === "True");
		});
		// Trailer
		signalList.set(vssApi.Vehicle.Trailer.IsConnected.name, (value) => {
			setTrailerMode(value === "True");
		});
		// Braking
		signalList.set(vssApi.Vehicle.ADAS.ABS.IsError.name, (value) => {
			setABSError(value === "True");
		});
		signalList.set(vssApi.Vehicle.ADAS.ESC.IsEnabled.nam, (value) => {
			setStabilityControlOn(value === "True");
		});

		for (const signal of signalList.keys()) {
			subscriptionData.signals.push({ name: signal });
		}
	}

	function isTirePressureLow() {
		return (row1LeftTirePressureLow || row1RightTirePressureLow || row2LeftTirePressureLow || row2RightTirePressureLow);
	}

	function isFogLightOn() {
		return (frontFogLightOn || rearFogLightOn);
	}

	function isAnySeatBeltWarning() {
		if (row1DriverSeatOccupied && !row1DriverSeatBelted) {
			return true;
		}
		else if (row1PassengerSeatOccupied && !row1PassengerSeatBelted) {
			return true;
		}
		else if (row2DriverSeatOccupied && !row2DriverSeatBelted) {
			return true;
		}
		else if (row2PassengerSeatOccupied && !row2PassengerSeatBelted) {
			return true;
		}
		return false;
	}

	function isDefrosterOn() {
		return (frontDefrosterActive || RearDefrosterActive);
	}

	function isAnyDoorOpen() {
		return (row1DriverDoorOpen || row1PassengerDoorOpen || row2DriverDoorOpen || row2PassengerDoorOpen
			|| frunkOpen || trunkOpen || chargePortOpen);
	}

	function showWarnings() {
		return (
			<>
				{
					// <img src={"./tesla/icons/parking-brake-fault.png"} alt=""
					// 	style={{ width: 32, height: 32, margin: 8 }} />
				}
				{parkingBrakeEngaged &&
					<img src={"./tesla/icons/parking-brake-manual.png"} alt=""
						style={{ width: 36, height: 36, margin: 6 }} />
				}
				{lowBeamOn &&
					<img src={"./tesla/icons/low-beam.png"} alt=""
						style={{ width: 36, height: 36, margin: 6 }} />
				}
				{highBeamOn &&
					<img src={"./tesla/icons/high-beam.png"} alt=""
						style={{ width: 36, height: 36, margin: 6 }} />
				}
				{parkingLightOn &&
					<img src={"./tesla/icons/parking-light.png"} alt=""
						style={{ width: 40, height: 40, margin: 4 }} />
				}
				{isFogLightOn() &&
					<img src={"./tesla/icons/front-fog-light.png"} alt=""
						style={{ width: 40, height: 40, margin: 4 }} />
				}
				{
					// <img src={"./tesla/icons/auto-high-beam-enabled-off.png"} alt=""
					// 	style={{ width: 40, height: 40, margin: 4 }} />
					// <img src={"./tesla/icons/auto-high-beam-enabled-on.png"} alt=""
					// 	style={{ width: 40, height: 40, margin: 4 }} />
				}
				{isTirePressureLow() &&
					<img src={"./tesla/icons/tire-pressure-warning.png"} alt=""
						style={{ width: 48, height: 48, margin: 0 }} />
				}
				{isAnySeatBeltWarning() &&
					<img src={"./tesla/icons/seat-belt-reminder.png"} alt=""
						style={{ width: 48, height: 48, margin: 0 }} />
				}
				{
					// <img src={"./tesla/icons/airbag-safety-warning.png"} alt=""
					// 	style={{ width: 48, height: 48, margin: 0 }} />
				}
				{isDefrosterOn() &&
					<img src={"./tesla/icons/blue-snowflake.png"} alt=""
						style={{ width: 32, height: 32, margin: 8 }} />
				}
				{isAnyDoorOpen() &&
					<img src={"./tesla/icons/door-frunk-trunk-open.png"} alt=""
						style={{ width: 36, height: 36, margin: 6 }} />
				}
				{trailerMode &&
					<img src={"./tesla/icons/trailer-mode.png"} alt=""
						style={{ width: 36, height: 36, margin: 6 }} />
				}
				{autopilotOn ?
					<img src={"./tesla/icons/car-steering-wheel.png"} alt=""
						style={{ width: 24, height: 24, margin: 12 }} />
					:
					<img src={"./tesla/icons/autosteer-inactive.png"} alt=""
						style={{ width: 42, height: 42, margin: 2 }} />
				}
				{
					// <img src={"./tesla/icons/brake-system-fault.png"} alt=""
					// 	style={{ width: 34, height: 34, margin: 7 }} />
				}
				{ABSError &&
					<img src={"./tesla/icons/ABS-fault.png"} alt=""
						style={{ width: 32, height: 32, margin: 8 }} />
				}
				{
					// <img src={"./tesla/icons/brake-booster-fault.png"} alt=""
					// 	style={{ width: 34, height: 34, margin: 7 }} />
				}
				{
					// <img src={"./tesla/icons/electronic-stability-control-off.png"} alt=""
					// 	style={{ width: 42, height: 42, margin: 3 }} />
				}
				{stabilityControlOn &&
					<img src={"./tesla/icons/stability-control.png"} alt=""
						style={{ width: 48, height: 48, margin: 0 }} />
				}
				{
					// <img src={"./tesla/icons/regen-braking-limited.png"} alt=""
					// 	style={{ width: 38, height: 38, margin: 5 }} />
				}
				{
					// <img src={"./tesla/icons/vehicle-hold.png"} alt=""
					// 	style={{ width: 36, height: 36, margin: 6 }} />
				}
				{
					// <img src={"./tesla/icons/vehicle-power-limited.png"} alt=""
					// 	style={{ width: 32, height: 32, margin: 8 }} />
				}
			</>
		);
	}

	const LEFT_TASKBAR_WIDTH = 64;

	const styledContainer = {
		position: "absolute",
		top: gear === "d" ? 150 : 56,
		left: 0,
		width: LEFT_TASKBAR_WIDTH,
		//backgroundColor: gear === "d" ? "#eeeeee" : "#f8f8f8",
		alignItems: "center",
		paddingTop: 8
	};

	return (
		<div style={styledContainer}>
			<div style={{ padding: 8 }}>
				{showWarnings()}
			</div>
		</div>
	);
}

export default IndicatorOverlay;