//import { Text, View } from 'react-native';
import Switch from "./Switch";

const SwitchControl = ({ 
    name, value, setValue, additionalCheck, desc
}) => {

    function getDesc() {
        if (desc.length > 1) {
            if (value) {
                return desc[0];
            }
            else {
                return desc[1];
            }
        }
        else {
            return desc[0];
        }
    }

    return (
        <div style={{ marginTop: 16, display: "flex", flexDirection: "row", justifyContent: "flex-start", alignItems: "center" }}>
            <div style={{ width: 92, display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "flex-start" }}>
                <div style={{ height: 36, display: "flex", justifyContent: "center", alignItems: "center" }}>
                    <div style={{ marginTop: 5, marginLeft: 5 }}>
                        <Switch value={value} setValue={setValue} />
                    </div>
                </div>
                {desc &&
                <div style={{ height: 48 }}/>
                }
                {additionalCheck &&
                <div style={{ height: 36 }}/>
                }
            </div>
            <div style={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "flex-start" }}>
                <div style={{ height: 36, display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "flex-start" }}>
                    <div style={{ fontSize: 18, fontWeight: 700, color: "#6F6F6F" }}>
                        {name}
                    </div>
                </div>
                {desc &&
                <div style={{ height: 48, width: 400, display: "flex", flexDirection: "column", justifyContent: "flex-start", alignItems: "flex-start" }}>
                    <div style={{ fontSize: 18, fontWeight: 700, color: "#ABABAB" }}>
                        {getDesc()}
                    </div>
                </div>
                }
                {additionalCheck &&
                <div style={{ height: 36, display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "flex-start" }}>
                    <div style={{ fontSize: 18, fontWeight: 700, color: "#ABABAB" }}>
                        {"V " + additionalCheck}
                    </div>
                </div>
                }
            </div>
        </div>
    );
};

export default SwitchControl;