import { useState } from "react";
import styled from "styled-components";
import SelectionControl from "./components/SelectControl";
import SwitchControl from "./components/SwitchControl";


const PedalsSteeringControl = () => {

	const [acceleration, setAcceleration] = useState("Standard");
	const [steeringMode, setSteeringMode] = useState("Standard");
	const [stoppingMode, setStoppingMode] = useState("Creep");
	const [regenerativeBraking, setRegenerativeBraking] = useState("Standard");

	const [offRoadAssist, setOffRoadAssist] = useState(false);
	const [slipStart, setSlipStart] = useState(false);

	return (
		<StyledDiv>
			<SelectionControl
				name={"Acceleration"}
				valueList={["Chill", "Standard"]}
				value={acceleration}
				setValue={setAcceleration}
			/>
			<SelectionControl
				name={"Steering Mode"}
				valueList={["Comfort", "Standard", "Sport"]}
				value={steeringMode}
				setValue={setSteeringMode}
			/>
			<SelectionControl
				name={"Stopping Mode"}
				valueList={["Creep", "Roll", "Hold"]}
				value={stoppingMode}
				setValue={setStoppingMode}
				desc={[
					"Slowly move when pedals are released", 
					"Roll when pedals are released", 
					"Maximises range by extending regenerative braking to lower speeds and automatically blends in brakes to hold the vehicle at a stop"
				]}
			/>
			<SelectionControl
				name={"Regenerative Braking"}
				valueList={["Low", "Standard"]}
				value={regenerativeBraking}
				setValue={setRegenerativeBraking}
			/>

			<div style={{ height: 16 }}></div>

			<SwitchControl
				name="Off-Road Assist"
				value={offRoadAssist}
				setValue={setOffRoadAssist}
			/>
			<SwitchControl
				name="Slip Start"
				value={slipStart}
				setValue={setSlipStart}
				desc={["Use to help free vehicle stuck in snow, sand or mud."]}
			/>
		</StyledDiv>
	);
}

export default PedalsSteeringControl;

const StyledDiv = styled.div`
	width: 100%;
	height: 100%;
	background-color: #f0f0f0;
	padding: 32px;
	position: relative;
	color: #6F6F6F;
`;
