import { useState, useEffect } from "react";
import styled from "styled-components";
import { useJsApiLoader } from '@react-google-maps/api';
import Iframe from 'react-iframe';

import MyGoogleMap from "./MyGoogleMap";
import DirectionsBox from "./DirectionsBox";
import { SIGNAL_TYPES, subscribeSignals, unsubscribeSignals } from "../../vplug/WsReq";
import vssApi from "../../vplug/VssAPI.json";
import { getValue } from "../car_control/CarControlSettings";

const MAP_API_KEY = process.env.REACT_APP_GOOGLE_API_KEY;
const libraries = ["geometry", "drawing", "places"];

const mapMemory = {
	carPosition: null
};


const MapSection = ({
	mainTicks
}) => {

	const [autopilotOn, setAutopilotOn] = useState(false);
	const [speed, setSpeed] = useState(0);

	const tilt = 45;

	const [mapType, setMapType] = useState("terrain");
	const [carPosition, setCarPosition] = useState(null);
	const [latitude, setLatitude] = useState(null);
	const [longitude, setLongitude] = useState(null);
	const [zoom, setZoom] = useState(15);
	
	const [destination, setDestination] = useState(null);
	const [directionsResponse, setDirectionsResponse] = useState(null);
	
	const [heading, setHeading] = useState(0);

	const { isLoaded } = useJsApiLoader({
		id: 'google-map-script',
		googleMapsApiKey: MAP_API_KEY,
		libraries,
	});

	useEffect(() => {
		if (mapMemory.carPosition) {
			setCarPosition(mapMemory.carPosition);
			setLatitude(mapMemory.carPosition.latitude);
			setLongitude(mapMemory.carPosition.longitude);
		}
		else {
			navigator.geolocation.getCurrentPosition(async (position) => {
				setTimeout(() => {
					setCarPosition({ lat: position.coords.latitude, lng: position.coords.longitude });
				}, 500);
			});
		}

		subscribeSignals(subscriptionData, subscriptionCallback);
		subscribeSignals(subscriptionDataExt, subscriptionCallbackExt);

		return () => {
			unsubscribeSignals({ channel: subscriptionData.channel });
			unsubscribeSignals({ channel: subscriptionDataExt.channel });
		};
	}, 
	// eslint-disable-next-line
	[]);

	const subscriptionData = {
		channel: "touchscreen/track-my-car",
		type: SIGNAL_TYPES.VSS,
		signals: [
			{ name: vssApi.Vehicle.CurrentLocation.Latitude.name },
			{ name: vssApi.Vehicle.CurrentLocation.Longitude.name },
			{ name: vssApi.Vehicle.CurrentLocation.Heading.name },
			{ name: vssApi.Vehicle.Speed.name }
		]
	};

	const subscriptionCallback = (msg) => {
		for (const signal of msg.signals) {
			if (signal.value != null && signal.value !== "undefined") {
				if (signal.name === vssApi.Vehicle.CurrentLocation.Latitude.name) {
					setLatitude(signal.value);
				}
				else if (signal.name === vssApi.Vehicle.CurrentLocation.Longitude.name) {
					setLongitude(signal.value);
				}
				else if (signal.name === vssApi.Vehicle.CurrentLocation.Heading.name) {
					setHeading(signal.value);
				}
				else if (signal.name === vssApi.Vehicle.Speed.name) {
					setSpeed(signal.value);
				}
			}
		}
	}

	const subscriptionDataExt = {
		channel: "touchscreen/track-my-car-ext",
		type: SIGNAL_TYPES.EXT,
		signals: [
			{ name: "autopilot" },
			{ name: "destination" }
		]
	};

	const subscriptionCallbackExt = (msg) => {
		for (const signal of msg.signals) {
			if (signal.name === "autopilot") {
				setAutopilotOn(signal.value);
			}
			else if (signal.name === "destination") {
				setDestination(signal.value);
			}
		}
	}

	useEffect(() => {
		if (latitude != null && longitude != null) {
			const pos = { lat: latitude, lng: longitude };
			setCarPosition(pos);
			mapMemory.carPosition = pos;
		}
	}, [latitude, longitude]);

	useEffect(() => {
		if (speed === 0) {
			setZoom(15);
		}
		else {
			const zoomIn = Math.min(Math.floor(speed / 30), 8);
			setZoom(19 - zoomIn);
		}
	}, [speed]);

	return (
		<StyledDiv>
			{
				getValue("mapType") && getValue("mapType") !== "Custom" ?
					<Iframe url={"https://map.kakao.com"}
						width="100%"
						height="100%"
						id="my-iframe"
						className=""
						display="block"
						position="relative"
					/>
					:
					<>
						{carPosition && isLoaded &&
							<div style={{
								width: "100%",
								height: "100%"
							}}>
								<MyGoogleMap
									carPosition={carPosition}
									zoom={zoom}
									setZoom={setZoom}
									mapType={mapType}
									heading={heading}
									tilt={tilt}
									directionsResponse={directionsResponse}
									autopilotOn={autopilotOn}
								/>
								<DirectionsBox
									destination={destination}
									setDestination={setDestination}
									directionsResponse={directionsResponse}
									setDirectionsResponse={setDirectionsResponse}
									autopilotOn={autopilotOn}
									speed={speed}
									carPosition={carPosition}
									mapType={mapType}
									setMapType={setMapType}
									zoom={zoom}
									setZoom={setZoom}
									mainTicks={mainTicks}
								/>
							</div>
						}
					</>
			}
		</StyledDiv>
	);
}

export default MapSection;

const StyledDiv = styled.div`
	width: 100%;
	height: 100%;
	position: relative;
	overflow: hidden;
	background: white;
	border-top-right-radius: 8px;
	display: flex;
	flex-direction: row;
`;
