//import SwitchControl from "./components/SwitchControl";


const ServiceControl = () => {
	return (
		<StyledDiv>
			<div style={{ display: "flex", flexDirection: "row", flexWrap: "wrap" }}>
				<div
					style={{
						width: "50%", height: 80, display: "flex", justifyContent: "center", alignItems: "center",
						padding: 8
					}}
				>
					<div
						style={{
							width: "100%", height: "100%", display: "flex", justifyContent: "center", alignItems: "center",
						}}
					>
						<div style={{ fontSize: 18, fontWeight: 700 }}>46 psi</div>
					</div>
				</div>
				<div
					style={{
						width: "50%", height: 80, display: "flex", justifyContent: "center", alignItems: "center",
						padding: 8
					}}
				>
					<div
						style={{
							width: "100%", height: "100%", display: "flex", justifyContent: "center", alignItems: "center",
						}}
					>
						<div style={{ fontSize: 18, fontWeight: 700 }}>47 psi</div>
					</div>
				</div>
				<div
					style={{
						width: "50%", height: 80, display: "flex", justifyContent: "center", alignItems: "center",
						padding: 8
					}}
				>
					<div
						style={{
							width: "100%", height: "100%", display: "flex", justifyContent: "center", alignItems: "center",
						}}
					>
						<div style={{ fontSize: 18, fontWeight: 700 }}>47 psi</div>
					</div>
				</div>
				<div
					style={{
						width: "50%", height: 80, display: "flex", justifyContent: "center", alignItems: "center",
						padding: 8
					}}
				>
					<div
						style={{
							width: "100%", height: "100%", display: "flex", justifyContent: "center", alignItems: "center",
						}}
					>
						<div style={{ fontSize: 18, fontWeight: 700 }}>47 psi</div>
					</div>
				</div>
			</div>

			<div style={{ display: "flex", flexDirection: "row", flexWrap: "wrap", marginTop: 24 }}>
				<HalfButton label={"Owner's Manual"}/>
				<HalfButton label={"Car Wash Mode"}/>

				<HalfButton label={"Adjust Headlights"}/>
				<HalfButton label={"Towing"}/>

				<HalfButton label={"Reset TPMS sensors"}/>
				<HalfButton label={"Wheel & Tire"} label1={"Configuration"}/>

				<HalfButton label={"Notifications"}/>
				<HalfButton label={"Camera Preview"}/>

				<HalfButton label={"Camera Calibration"}/>
				<HalfButton label={"Driver Seat, Steering &"} label1={"Mirrors Calibration"}/>

				<HalfButton label={"Clear Browser Data"}/>
				<HalfButton label={"Factory Reset"}/>

				<HalfButton label={"Wiper Service Mode"}/>
			</div>
		</StyledDiv>
	);
}

const HalfButton = ({label, label1=null}) => {
	return (
		<div
			style={{
				width: "50%", height: 80, display: "flex", justifyContent: "center", alignItems: "center",
				padding: 8
			}}
		>
			<div
				onClick={() => {
				}}
				style={{
					width: "100%", height: "100%", 
					display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center",
					//borderWidth: 2, borderRadius: 8, borderColor: "darkgray", borderStyle: "solid"
					backgroundColor: "lightgray", borderRadius: 8
				}}
			>
				<div style={{ fontSize: 18, fontWeight: 700 }}>
					{label}
				</div>
				{label1 &&
					<div style={{ fontSize: 18, fontWeight: 700 }}>
						{label1}
					</div>
				}
			</div>
		</div>
	);
}

export default ServiceControl;

const StyledDiv = styled.div`
	width: 100%;
	height: 100%;
	background-color: #f0f0f0;
	padding: 32px;
	position: relative;
	color: #6F6F6F;
`;
