import { useState } from "react";
import styled from "styled-components";
import SelectionControl from "./components/SelectControl";
//import SwitchControl from "./components/SwitchControl";
//import SliderControl from "./components/SliderControl";
//import BrightnessSlider from "./components/BrightnessSlider";


const DisplayControl = ({
	APP_HEIGHT
}) => {

	const [displayMode, setDisplayMode] = useState("Auto");
	//const [brightnessValue, setBrightnessValue] = useState(70);

	const [time, setTime] = useState("24 Hour");
	const [energyDisplay, setEnergyDisplay] = useState("Percentage");
	const [distanceUnit, setDistanceUnit] = useState("Miles");
	const [temperatureUnit, setTemperatureUnit] = useState("\u00B0C");
	const [tyrePressure, setTyrePressure] = useState("PSI");

	return (
		<StyledDiv APP_HEIGHT={APP_HEIGHT}>
			<SelectionControl
				name="Appearance"
				valueList={["Light", "Dark", "Auto"]}
				value={displayMode}
				setValue={setDisplayMode}
			/>

			<div style={{ height: 48, display: "flex", flexDirection: "row", justifyContent: "flex-start", alignItems: "center", gap: 16, marginTop: 16 }}>
				<img src={"./touchscreen/car_control/display_brightness.png"} style={{ width: 32, height: 32 }} alt="" />
				<div style={{ fontSize: 18, fontWeight: 700, color: "#6F6F6F" }}>
					Brightness
				</div>
			</div>
			<div style={{ width: "100%", height: 56, display: "flex", flexDirection: "row" }}>
				<div  
					onClick={() => {
						//
					}}
					style={{
						width: "75%", height: 56, display: "flex", flexDirection: "row", justifyContent: "center", alignItems: "center",
						borderWidth: 2, borderRadius: 8, borderColor: "lightgray", borderStyle: "solid",
						//backgroundColor: (foldMirrors ? MaterialColors.Primary : "white")
					}}
				>
					<div style={{ width: "85%", height: 4, backgroundColor: "#6f6f6f", marginLeft: 16 }}>
					</div>
					<div style={{ width: "15%", height: "100%", display: "flex", justifyContent: "center", alignItems: "center" }}>
						<img
							src={"./touchscreen/car_control/brightness.png"}
							style={{ width: 32, height: 32 }} alt=""
						/>
					</div>
				</div>

				<div  
					onClick={() => {
						//
					}}
					style={{
						width: "22%", height: 56, display: "flex", flexDirection: "row", justifyContent: "center", alignItems: "center",
						borderWidth: 2, borderRadius: 8, borderColor: "lightgray", borderStyle: "solid",
						//backgroundColor: (foldMirrors ? MaterialColors.Primary : "white")
						marginLeft: "3%"
					}}
				>
					<div style={{ fontSize: 18, fontWeight: 700, color: "#6f6f6f" }}>
						Auto
					</div>
				</div>
			</div>

			<SelectionControl
				name="Time"
				valueList={["12 Hour", "24 Hour"]}
				value={time}
				setValue={setTime}
			/>
			<SelectionControl
				name="Energy Display"
				valueList={["Percentage", "Distance"]}
				value={energyDisplay}
				setValue={setEnergyDisplay}
			/>
			<SelectionControl
				name="Distance"
				valueList={["Kilometers", "Miles"]}
				value={distanceUnit}
				setValue={setDistanceUnit}
			/>
			<SelectionControl
				name="Temperature"
				valueList={["\u00B0C", "\u00B0F"]}
				value={temperatureUnit}
				setValue={setTemperatureUnit}
			/>
			<SelectionControl
				name="Tire Pressure"
				valueList={["Bar", "PSI"]}
				value={tyrePressure}
				setValue={setTyrePressure}
			/>
		</StyledDiv>
	);
}

export default DisplayControl;

const StyledDiv = styled.div`
	width: 100%;
	height: 100%;
	background-color: #f0f0f0;
	padding: 32px;
	position: relative;
	color: #6F6F6F;
	max-height: ${(props) => props.APP_HEIGHT}px;
	overflow-y: scroll;

	/* width */
	::-webkit-scrollbar {
  		width: 10px;
	}

	/* Track */
	::-webkit-scrollbar-track {
  		background: #f1f1f1;
	}

	/* Handle */
	::-webkit-scrollbar-thumb {
  		background: #888;
	}

	/* Handle on hover */
	::-webkit-scrollbar-thumb:hover {
  		background: #555;
	}
`;
