import { useEffect, useState } from "react";

import { setSignal } from "../vplug/WsReq";
import vssApi from "../vplug/VssAPI.json";

import useSound from 'use-sound';
//import drivingCarSound from './sounds/driving-in-a-car-6227.mp3';
import hazardLightsSound from './sounds/039355_hazard-lights-60570.mp3';
import hornSound from './sounds/car-horn-90973.mp3';

import { MyTooltip } from "./CarInterior";
import Battery from "../car_exterior/Battery";


const ClusterMain = ({ 
	speed, distanceToEmpty,
	hornReq, hazardReq,
	leftTurnReq, setLeftTurnReq,
	rightTurnReq, setRightTurnReq
}) => {

	const [leftTurnLedOn, setLeftTurnLedOn] = useState(false);
	const [rightTurnLedOn, setRightTurnLedOn] = useState(false);

	//const [playDrivingCar, { stop: stopDrivingCar }] = useSound(drivingCarSound, { interrupt: true });
	const [playHazardLights, { stop: stopHazardLights }] = useSound(hazardLightsSound, { interrupt: true });
	const [playHorn, { stop: stopHorn }] = useSound(hornSound, { interrupt: true });

	//const [drivingCarInterval, setDrivingCarInterval] = useState(null);
	const [hazardLightsInterval, setHazardLightsInterval] = useState(null);
	const [hornInterval, setHornInterval] = useState(null);

	useEffect(() => {
		return () => {
			// if (drivingCarInterval) {
			// 	stopDrivingCar();
			// 	clearInterval(drivingCarInterval);
			// }
			if (hazardLightsInterval) {
				stopHazardLights();
				clearInterval(hazardLightsInterval);
			}
			if (hornInterval) {
				stopHorn();
				clearInterval(hornInterval);
			}
		};
	}, 
	// eslint-disable-next-line
	[]);
	
	const styledSpeedMeter = {
		container: {
			width: 232,
			height: 64,
			display: "flex",
			flexDirection: "row",
			alignItems: "center",
			justifyContent: "space-between",
			borderWidth: 1,
			borderColor: "darkgray",
			borderStyle: "solid",
			borderRadius: 8,
		},
		speed: {
			fontSize: 48,
			fontWeight: 700,
			textAlign: "right",
			color: "#000000",
		},
		speedUnit: {
			fontSize: 16,
			fontWeight: 700,
			textAlign: "left",
		},
		image: {
			width: 32,
			height: 32
		}
	}

	const styledTurnSignals = {
		container: {
			height: 64,
			width: 96,
			display: "flex",
			flexDirection: "row",
			alignItems: "center",
			justifyContent: "space-evenly",
			borderWidth: 1,
			borderColor: "darkgray",
			borderStyle: "solid",
			borderRadius: 8,
			cursor: "pointer"
		},
		imageOn: {
			width: 40,
			height: 40,
			animation: "blink 0.5s",
			animationIterationCount: "infinite",
		},
		imageOff: {
			width: 40,
			height: 40
		}
	};

	const styledDistanceToEmpty = {
		container: {
			height: 64,
			width: 120,
			display: "flex",
			flexDirection: "row",
			alignItems: "center",
			justifyContent: "center",
			gap: 8,
			borderWidth: 1,
			borderColor: "darkgray",
			borderStyle: "solid",
			borderRadius: 8
		},
		label: {
			color: "#707070",
			fontSize: 16,
			fontWeight: 700,
		},
		image: {
			width: 32,
			height: 32,
		}
	};

	// useEffect(() => {
	// 	if (speed > 0) {
	// 		if (!drivingCarInterval) {
	// 			playDrivingCar();
	// 			const interval = setInterval(() => {
	// 				playDrivingCar();
	// 			}, 60000);
	// 			setDrivingCarInterval(interval);
	// 		}
	// 	}
	// 	else {
	// 		stopDrivingCar();
	// 		if (drivingCarInterval) {
	// 			clearInterval(drivingCarInterval);
	// 			setDrivingCarInterval(null);
	// 		}
	// 	}
	// }, 
	// // eslint-disable-next-line
	// [speed]);

	useEffect(() => {
		if (hazardReq) {
			if (leftTurnReq) {
				setLeftTurnReq(false);
				setSignal(vssApi.Vehicle.Body.Lights.DirectionIndicator.Left.IsSignaling.name, "False");
			}
			if (rightTurnReq) {
				setRightTurnReq(false);
				setSignal(vssApi.Vehicle.Body.Lights.DirectionIndicator.Right.IsSignaling.name, "False");
			}
			
			setLeftTurnLedOn(false);
			setRightTurnLedOn(false);

			setTimeout(() => {
				setLeftTurnLedOn(true);
				setRightTurnLedOn(true);
			}, 100);
		}
		else if (leftTurnReq) {	
			setLeftTurnLedOn(true);
			setRightTurnLedOn(false);
		}
		else if (rightTurnReq) {
			setLeftTurnLedOn(false);
			setRightTurnLedOn(true);
		}
		else {
			setLeftTurnLedOn(false);
			setRightTurnLedOn(false);
		}
	}, 
	// eslint-disable-next-line
	[hazardReq, leftTurnReq, rightTurnReq]);

	useEffect(() => {
		if (leftTurnLedOn || rightTurnLedOn) {
			if (!hazardLightsInterval) {
				playHazardLights();
				const interval = setInterval(() => {
					playHazardLights();
				}, 23000);
				setHazardLightsInterval(interval);
			}
		}
		else {
			stopHazardLights();
			if (hazardLightsInterval) {
				clearInterval(hazardLightsInterval);
				setHazardLightsInterval(null);
			}
		}
	}, 
	// eslint-disable-next-line
	[leftTurnLedOn, rightTurnLedOn]);

	useEffect(() => {
		if (hornReq) {
			if (!hornInterval) {
				playHorn();
				const interval = setInterval(() => {
					playHorn();
				}, 8000);
				setHornInterval(interval);
			}
		}
		else {
			stopHorn();
			if (hornInterval) {
				clearInterval(hornInterval);
				setHornInterval(null);
			}
		}
	}, 
	// eslint-disable-next-line
	[hornReq]);

	function handleLeftTurnSignal() {
		if (!hazardReq && (!leftTurnReq || !rightTurnReq)) {
			if (!leftTurnReq) {
				setSignal(vssApi.Vehicle.Body.Lights.DirectionIndicator.Left.IsSignaling.name, "True");
				if (rightTurnReq) {
					setSignal(vssApi.Vehicle.Body.Lights.DirectionIndicator.Right.IsSignaling.name, "False");
				}
			}
			else {
				setSignal(vssApi.Vehicle.Body.Lights.DirectionIndicator.Left.IsSignaling.name, "False");
			}
		}
	}

	function handleRightTurnSignal() {
		if (!hazardReq && (!leftTurnReq || !rightTurnReq)) {
			if (!rightTurnReq) {
				setSignal(vssApi.Vehicle.Body.Lights.DirectionIndicator.Right.IsSignaling.name, "True");
				if (leftTurnReq) {
					setSignal(vssApi.Vehicle.Body.Lights.DirectionIndicator.Left.IsSignaling.name, "False");
				}
			}
			else {
				setSignal(vssApi.Vehicle.Body.Lights.DirectionIndicator.Right.IsSignaling.name, "False");
			}
		}	
	}

    return (
        <>
			<div style={styledSpeedMeter.container}>
				<div style={{ width: "75%", height: "100%", display: "flex", flexDirection: "row" }}>
					<div style={{ width: "65%", height: "100%", display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "flex-end" }}>
						<div style={styledSpeedMeter.speed}>{speed}</div>
					</div>
					<div style={{ width: "35%", height: "100%", display: "flex", flexDirection: "column", justifyContent: "flex-end", alignItems: "center", paddingBottom: 16 }}>
						<div style={styledSpeedMeter.speedUnit}>km/h</div>
					</div>
				</div>
				<div style={{
					width: "25%", height: "100%", display: "flex",
					justifyContent: "center", alignItems: "center", //padding: 8
				}}>
					<img src={"./interior/speed-limit.png"} style={styledSpeedMeter.image} alt="" />
				</div>
			</div>
			
			<div style={styledTurnSignals.container}>
				<div
					style={{ width: "50%", height: "100%", display: "flex", justifyContent: "center", alignItems: "center" }}
					onClick={handleLeftTurnSignal}
					id="left-turn"
				>
					<img 
						src={leftTurnLedOn? "./tesla/icons/turn-left-active.png": "./tesla/icons/turn-left.png"}
						style={leftTurnLedOn? styledTurnSignals.imageOn : styledTurnSignals.imageOff} alt=""
					/>
				</div>
				<div 
					style={{ width: "50%", height: "100%", display: "flex", justifyContent: "center", alignItems: "center" }}
					onClick={handleRightTurnSignal}
					id="right-turn"
				>
					<img 
						src={rightTurnLedOn? "./tesla/icons/turn-right-active.png": "./tesla/icons/turn-right.png"}
						style={rightTurnLedOn? styledTurnSignals.imageOn : styledTurnSignals.imageOff} alt=""
					/>
				</div>
			</div>

			<div style={styledDistanceToEmpty.container}>
				<div style={styledDistanceToEmpty.label}>
					{Number(distanceToEmpty/1000).toFixed(0)} km
				</div>
				<Battery distanceToEmpty={distanceToEmpty} size={32} />
			</div>

			<MyTooltip id="left-turn" label="Left Turn Signal" />
			<MyTooltip id="right-turn" label="Right Turn Signal" />
        </>
    );
}

export default ClusterMain;