import { useEffect, useState } from "react";
import { Tooltip } from 'react-tooltip';

import Cluster from "./Cluster";
import Cabin from "./Cabin";
import ReactStreetviewMulti from '../map/ReactStreetviewMulti';

import { SIGNAL_TYPES, subscribeSignals, unsubscribeSignals } from "../vplug/WsReq";
import vssApi from "../vplug/VssAPI.json";
import { signalMap } from "../vplug/Signals";

const MAP_API_KEY = process.env.REACT_APP_GOOGLE_API_KEY;


const CarInterior = ({
	SCREEN_WIDTH, SCREEN_HEIGHT,
	touchscreenOpen, setTouchscreenOpen,
	autopilotOn, setAutopilotOn,
	speed, setSpeed,
	heading, setHeading,
	destination,
	gear, setGear, distanceToEmpty,
	departureReady, setDepartureReady,
	streetViewPanoramaOptions
}) => {

	const SelectedGearType = vssApi.Vehicle.Powertrain.Transmission.SelectedGear;

	const Row1DriverSideDoorType = vssApi.Vehicle.Cabin.Door.Row1.DriverSide;
	const Row1DriverSideSeatType = vssApi.Vehicle.Cabin.Seat.Row1.DriverSide;
	const Row1PassengerSideDoorType = vssApi.Vehicle.Cabin.Door.Row1.PassengerSide;
	const Row1PassengerSideSeatType = vssApi.Vehicle.Cabin.Seat.Row1.PassengerSide;
	const Row2DriverSideDoorType = vssApi.Vehicle.Cabin.Door.Row2.DriverSide;
	const Row2DriverSideSeatType = vssApi.Vehicle.Cabin.Seat.Row2.DriverSide;
	const Row2PassengerSideDoorType = vssApi.Vehicle.Cabin.Door.Row2.PassengerSide;
	const Row2PassengerSideSeatType = vssApi.Vehicle.Cabin.Seat.Row2.PassengerSide;

	const TrunkFrontType = vssApi.Vehicle.Body.Trunk.Front;
	const TrunkRearType = vssApi.Vehicle.Body.Trunk.Rear;
	const ChargePortStatusType = vssApi.Vehicle.Powertrain.TractionBattery.Charging.ChargePortFlap;

	const FrontWipingType = vssApi.Vehicle.Body.Windshield.Front.Wiping;

	const ExteriorType = vssApi.Vehicle.Exterior;

	const [carLocked, setCarLocked] = useState(true);
	const [row1DriverDoorLocked, setRow1DriverDoorLocked] = useState(true);
	const [row1PassengerDoorLocked, setRow1PassengerDoorLocked] = useState(true);
	const [row2DriverDoorLocked, setRow2DriverDoorLocked] = useState(true);
	const [row2PassengerDoorLocked, setRow2PassengerDoorLocked] = useState(true);
	const [frunkLocked, setFrunkLocked] = useState(true);
	const [trunkLocked, setTrunkLocked] = useState(true);

	const [frunkOpen, setFrunkOpen] = useState(false);
	const [trunkOpen, setTrunkOpen] = useState(false);
	const [chargePortOpen, setChargePortOpen] = useState(false);

	const [row1DriverDoorOpen, setRow1DriverDoorOpen] = useState(false);
	const [row1DriverSeatOccupied, setRow1DriverSeatOccupied] = useState(false);
	const [row1DriverSeatBelted, setRow1DriverSeatBelted] = useState(false);
	const [row1DriverWindowPos, setRow1DriverWindowPos] = useState(0);

	const [row1PassengerDoorOpen, setRow1PassengerDoorOpen] = useState(false);
	const [row1PassengerSeatOccupied, setRow1PassengerSeatOccupied] = useState(false);
	const [row1PassengerSeatBelted, setRow1PassengerSeatBelted] = useState(false);
	const [row1PassengerWindowPos, setRow1PassengerWindowPos] = useState(0);

	const [row2DriverDoorOpen, setRow2DriverDoorOpen] = useState(false);
	const [row2DriverSeatOccupied, setRow2DriverSeatOccupied] = useState(false);
	const [row2DriverSeatBelted, setRow2DriverSeatBelted] = useState(false);
	const [row2DriverWindowPos, setRow2DriverWindowPos] = useState(0);

	const [row2PassengerDoorOpen, setRow2PassengerDoorOpen] = useState(false);
	const [row2PassengerSeatOccupied, setRow2PassengerSeatOccupied] = useState(false);
	const [row2PassengerSeatBelted, setRow2PassengerSeatBelted] = useState(false);
	const [row2PassengerWindowPos, setRow2PassengerWindowPos] = useState(0);

	const [leftChildLock, setLeftChildLock] = useState(false);
	const [rightChildLock, setRightChildLock] = useState(false);

	const [windowChildLock, setWindowChildLock] = useState(false);

	const [frontWipingMode, setFrontWipingMode] = useState(FrontWipingType.Mode.allowed.OFF);

	const [hornReq, setHornReq] = useState(false);
	const [hazardReq, setHazardReq] = useState(false);
	const [leftTurnReq, setLeftTurnReq] = useState(false);
	const [rightTurnReq, setRightTurnReq] = useState(false);

	const [airTemperature, setAirTemperature] = useState(25);
	const [humidity, setHumidity] = useState(50);
	const [lightIntensity, setLightIntensity] = useState(100);
	const [rainIntensity, setRainIntensity] = useState(0);

	const signalList = new Map();
	const subscriptionData = {
		channel: "car-interior",
		type: SIGNAL_TYPES.VSS,
		signals: []
	};

	useEffect(() => {
		initSignalList();
		subscribeSignals(subscriptionData, subscriptionCallback);

		return () => {
			unsubscribeSignals({ channel: subscriptionData.channel });
		};
	}, 
	// eslint-disable-next-line
	[]);

	const subscriptionCallback = (msg) => {
		for (const signal of msg.signals) {
			if (signal.value != null && signal.value !== "undefined") {
				const callback = signalList.get(signal.name);
				if (callback) {
					callback(signal.value);
				}
				signalMap.set(signal.name, signal.value);
			}
		}
	}

	function initSignalList() {
		signalList.set(SelectedGearType.name, (value) => {
			if (value === SelectedGearType.allowed.Drive) {
				setGear("d");
			}
			else if (value === SelectedGearType.allowed.Neutral) {
				setGear("n");
			}
			else if (value === SelectedGearType.allowed.Reverse) {
				setGear("r");
			}
			else {
				setGear("p");
			}
		});

		signalList.set(Row1DriverSideDoorType.IsLocked.name, (value) => {
			setRow1DriverDoorLocked(value === "True");
		});
		signalList.set(Row1PassengerSideDoorType.IsLocked.name, (value) => {
			setRow1PassengerDoorLocked(value === "True");
		});
		signalList.set(Row2DriverSideDoorType.IsLocked.name, (value) => {
			setRow2DriverDoorLocked(value === "True");
		});
		signalList.set(Row2PassengerSideDoorType.IsLocked.name, (value) => {
			setRow2PassengerDoorLocked(value === "True");
		});
		signalList.set(TrunkFrontType.IsLocked.name, (value) => {
			setFrunkLocked(value === "True");
		});
		signalList.set(TrunkRearType.IsLocked.name, (value) => {
			setTrunkLocked(value === "True");
		});

		signalList.set(TrunkFrontType.IsOpen.name, (value) => {
			setFrunkOpen(value === "True");
		});
		signalList.set(TrunkRearType.IsOpen.name, (value) => {
			setTrunkOpen(value === "True");
		});
		signalList.set(ChargePortStatusType.name, (value) => {
			setChargePortOpen(value === ChargePortStatusType.allowed.OPEN);
		});
		
		signalList.set(Row1DriverSideDoorType.IsOpen.name, (value) => {
			setRow1DriverDoorOpen(value === "True");
		});
		signalList.set(Row1PassengerSideDoorType.IsOpen.name, (value) => {
			setRow1PassengerDoorOpen(value === "True");
		});
		signalList.set(Row2DriverSideDoorType.IsOpen.name, (value) => {
			setRow2DriverDoorOpen(value === "True");
		});
		signalList.set(Row2PassengerSideDoorType.IsOpen.name, (value) => {
			setRow2PassengerDoorOpen(value === "True");
		});

		signalList.set(Row2DriverSideDoorType.IsChildLockActive.name, (value) => {
			setLeftChildLock(value === "True");
		});
		signalList.set(Row2PassengerSideDoorType.IsChildLockActive.name, (value) => {
			setRightChildLock(value === "True");
		});

		signalList.set(Row1DriverSideSeatType.IsOccupied.name, (value) => {
			setRow1DriverSeatOccupied(value === "True");
		});
		signalList.set(Row1PassengerSideSeatType.IsOccupied.name, (value) => {
			setRow1PassengerSeatOccupied(value === "True");
		});
		signalList.set(Row2DriverSideSeatType.IsOccupied.name, (value) => {
			setRow2DriverSeatOccupied(value === "True");
		});
		signalList.set(Row2PassengerSideSeatType.IsOccupied.name, (value) => {
			setRow2PassengerSeatOccupied(value === "True");
		});

		signalList.set(Row1DriverSideSeatType.IsBelted.name, (value) => {
			setRow1DriverSeatBelted(value === "True");
		});
		signalList.set(Row1PassengerSideSeatType.IsBelted.name, (value) => {
			setRow1PassengerSeatBelted(value === "True");
		});
		signalList.set(Row2DriverSideSeatType.IsBelted.name, (value) => {
			setRow2DriverSeatBelted(value === "True");
		});
		signalList.set(Row2PassengerSideSeatType.IsBelted.name, (value) => {
			setRow2PassengerSeatBelted(value === "True");
		});

		signalList.set(Row1DriverSideDoorType.Window.Position.name, (value) => {
			setRow1DriverWindowPos(value);
		});
		signalList.set(Row1PassengerSideDoorType.Window.Position.name, (value) => {
			setRow1PassengerWindowPos(value);
		});
		signalList.set(Row2DriverSideDoorType.Window.Position.name, (value) => {
			setRow2DriverWindowPos(value);
		});
		signalList.set(Row2PassengerSideDoorType.Window.Position.name, (value) => {
			setRow2PassengerWindowPos(value);
		});

		signalList.set(ExteriorType.AirTemperature.name, (value) => {
			setAirTemperature(value);
		});
		signalList.set(ExteriorType.Humidity.name, (value) => {
			setHumidity(value);
		});
		signalList.set(ExteriorType.LightIntensity.name, (value) => {
			setLightIntensity(value);
		});
		signalList.set(vssApi.Vehicle.Body.Raindetection.Intensity.name, (value) => {
			setRainIntensity(value);
		});

		signalList.set(vssApi.Vehicle.Cabin.IsWindowChildLockEngaged.name, (value) => {
			setWindowChildLock(value === "True");
		});
		signalList.set(FrontWipingType.Mode.name, (value) => {
			setFrontWipingMode(value);
		});

		signalList.set(vssApi.Vehicle.Body.Lights.Hazard.IsSignaling.name, (value) => {
			setHazardReq(value === "True");
		});
		signalList.set(vssApi.Vehicle.Body.Lights.DirectionIndicator.Left.IsSignaling.name, (value) => {
			setLeftTurnReq(value === "True");
		});
		signalList.set(vssApi.Vehicle.Body.Lights.DirectionIndicator.Right.IsSignaling.name, (value) => {
			setRightTurnReq(value === "True");
		});

		for (const signal of signalList.keys()) {
			subscriptionData.signals.push({ name: signal });
		}
	}

	useEffect(() => {
		if (row1DriverDoorLocked && row1PassengerDoorLocked
			&& row2DriverDoorLocked && row2PassengerDoorLocked
			&& frunkLocked && trunkLocked) {
			setCarLocked(true);
		}
		else {
			setCarLocked(false);
		}
	}, [row1DriverDoorLocked, row1PassengerDoorLocked, row2DriverDoorLocked, row2PassengerDoorLocked, frunkLocked, trunkLocked]);


	const IMAGE_HEIGHT = (SCREEN_HEIGHT * 0.8);
	const IMAGE_WIDTH = (IMAGE_HEIGHT * 2.88);

	const WINDOW_HEIGHT = IMAGE_HEIGHT;
	const WINDOW_WIDTH = (SCREEN_WIDTH - IMAGE_WIDTH - 32) / 2;

	return (
		<div
			style={{
				position: "relative",
				width: "100%",
				height: "100%",
				display: "flex",
				flexDirection: "column",
				backgroundColor: "#f8f8f8"
			}}
		>
			<div style={{ width: "100%", height: "20%" }}>
				<Cluster
					gear={gear}
					speed={speed}
					distanceToEmpty={distanceToEmpty}
					hornReq={hornReq}
					autopilotOn={autopilotOn}
					frunkOpen={frunkOpen}
					trunkOpen={trunkOpen}
					chargePortOpen={chargePortOpen}
					frontWipingMode={frontWipingMode}
					airTemperature={airTemperature}
					humidity={humidity}
					lightIntensity={lightIntensity}
					rainIntensity={rainIntensity}

					row1DriverDoorOpen={row1DriverDoorOpen} row1PassengerDoorOpen={row1PassengerDoorOpen}
					row2DriverDoorOpen={row2DriverDoorOpen} row2PassengerDoorOpen={row2PassengerDoorOpen}
					row1DriverSeatOccupied={row1DriverSeatOccupied} row1PassengerSeatOccupied={row1PassengerSeatOccupied}
					row2DriverSeatOccupied={row2DriverSeatOccupied} row2PassengerSeatOccupied={row2PassengerSeatOccupied}
					row1DriverSeatBelted={row1DriverSeatBelted} row1PassengerSeatBelted={row1PassengerSeatBelted}
					row2DriverSeatBelted={row2DriverSeatBelted} row2PassengerSeatBelted={row2PassengerSeatBelted}
					departureReady={departureReady}
					carLocked={carLocked}

					hazardReq={hazardReq}
					leftTurnReq={leftTurnReq} setLeftTurnReq={setLeftTurnReq}
					rightTurnReq={rightTurnReq} setRightTurnReq={setRightTurnReq}
				/>
			</div>
			
			<div style={{ width: "100%", height: "80%", display: "flex", flexDirection: "row", justifyContent: "center", alignItems: "center", gap: 8 }}>
				<div
					style={{
						position: "relative", width: WINDOW_WIDTH, height: WINDOW_HEIGHT,
						borderWidth: 1, borderStyle: "solid", borderColor: "darkgray",
					}}
				>
					{streetViewPanoramaOptions &&
						<ReactStreetviewMulti
							viewId={"left-window"}
							apiKey={MAP_API_KEY}
							streetViewPanoramaOptions={
								{
									position: streetViewPanoramaOptions.position,
									pov: { heading: (streetViewPanoramaOptions.pov.heading - 90), pitch: 0 },
									zoom: 1
								}
							}
						/>
					}
					<div
						style={{
							position: "absolute", right: 0, top: 0, width: "50%", height: "100%",
							zIndex: 1, backgroundColor: "transparent", opacity: 0.5,
							borderWidth: 1, borderStyle: "solid", borderColor: "darkgray",
						}}
					>
						<div style={{ width: "100%", height: row1DriverWindowPos + "%" }} />
						<div style={{ width: "100%", height: (100 - row1DriverWindowPos) + "%", backgroundColor: "lightgray" }} />
						<div
							style={{
								position: "absolute", width: "100%", height: "10%", top: 0,
								display: "flex", justifyContent: "center", alignItems: "center"
							}}
						>
							<div>{row1DriverWindowPos} %</div>
						</div>
					</div>

					<div
						style={{
							position: "absolute", right: "50%", top: 0, width: "50%", height: "100%",
							zIndex: 1, backgroundColor: "transparent", opacity: 0.5,
							borderWidth: 1, borderStyle: "solid", borderColor: "darkgray",
						}}
					>
						<div style={{ width: "100%", height: row2DriverWindowPos + "%" }} />
						<div style={{ width: "100%", height: (100 - row2DriverWindowPos) + "%", backgroundColor: "lightgray" }} />
						<div
							style={{
								position: "absolute", width: "100%", height: "10%", top: 0,
								display: "flex", justifyContent: "center", alignItems: "center"
							}}
						>
							<div>{row2DriverWindowPos} %</div>
						</div>
					</div>
				</div>

				<Cabin
					IMAGE_HEIGHT={IMAGE_HEIGHT} IMAGE_WIDTH={IMAGE_WIDTH}
					touchscreenOpen={touchscreenOpen} setTouchscreenOpen={setTouchscreenOpen}

					autopilotOn={autopilotOn} setAutopilotOn={setAutopilotOn} distanceToEmpty={distanceToEmpty}
					speed={speed} setSpeed={setSpeed}
					heading={heading} setHeading={setHeading}
					destination={destination}
					gear={gear}
					carLocked={carLocked}
					row1DriverDoorOpen={row1DriverDoorOpen} setRow1DriverDoorOpen={setRow1DriverDoorOpen}
					row1PassengerDoorOpen={row1PassengerDoorOpen} row2DriverDoorOpen={row2DriverDoorOpen} row2PassengerDoorOpen={row2PassengerDoorOpen}
					row1DriverSeatOccupied={row1DriverSeatOccupied}
					leftChildLock={leftChildLock} rightChildLock={rightChildLock}
					setDepartureReady={setDepartureReady}
					
					frontLeftWindowPos={row1DriverWindowPos} setFrontLeftWindowPos={setRow1DriverWindowPos}
					frontRightWindowPos={row1PassengerWindowPos} setFrontRightWindowPos={setRow1PassengerWindowPos}
					rearLeftWindowPos={row2DriverWindowPos} setRearLeftWindowPos={setRow2DriverWindowPos}
					rearRightWindowPos={row2PassengerWindowPos} setRearRightWindowPos={setRow2PassengerWindowPos}
					windowChildLock={windowChildLock}
					hornReq={hornReq} setHornReq={setHornReq}
					hazardReq={hazardReq}
				/>

				<div
					style={{
						position: "relative", width: WINDOW_WIDTH, height: WINDOW_HEIGHT,
						borderWidth: 1, borderStyle: "solid", borderColor: "darkgray",
					}}
				>
					{streetViewPanoramaOptions &&
						<ReactStreetviewMulti
							viewId={"right-window"}
							apiKey={MAP_API_KEY}
							streetViewPanoramaOptions={
								{
									position: streetViewPanoramaOptions.position,
									pov: { heading: (streetViewPanoramaOptions.pov.heading + 90), pitch: 0 },
									zoom: 1
								}
							}
						/>
					}
					<div
						style={{
							position: "absolute", left: 0, top: 0, width: "50%", height: "100%",
							zIndex: 1, backgroundColor: "transparent", opacity: 0.5,
							borderWidth: 1, borderStyle: "solid", borderColor: "darkgray",
						}}
					>
						<div style={{ width: "100%", height: row1PassengerWindowPos + "%" }} />
						<div style={{ width: "100%", height: (100 - row1PassengerWindowPos) + "%", backgroundColor: "lightgray" }} />
						<div
							style={{
								position: "absolute", width: "100%", height: "10%", top: 0,
								display: "flex", justifyContent: "center", alignItems: "center"
							}}
						>
							<div>{row1PassengerWindowPos} %</div>
						</div>
					</div>

					<div
						style={{
							position: "absolute", left: "50%", top: 0, width: "50%", height: "100%",
							zIndex: 1, backgroundColor: "transparent", opacity: 0.5,
							borderWidth: 1, borderStyle: "solid", borderColor: "darkgray",
						}}
					>
						<div style={{ width: "100%", height: row2PassengerWindowPos + "%" }} />
						<div style={{ width: "100%", height: (100 - row2PassengerWindowPos) + "%", backgroundColor: "lightgray" }} />
						<div
							style={{
								position: "absolute", width: "100%", height: "10%", top: 0,
								display: "flex", justifyContent: "center", alignItems: "center"
							}}
						>
							<div>{row2PassengerWindowPos} %</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}

export const MyTooltip = ({
	id, label
}) => {
	return (
		<Tooltip 
			anchorSelect={"#"+id} place="top" 
			style={{ zIndex: 10, backgroundColor: "yellow", color: "#222", borderRadius: 8, fontSize: 20 }}
			content={label}
		/>
	);
}

export const MyTooltip2 = ({
	id, label
}) => {
	return (
		<Tooltip 
			anchorSelect={"."+id} place="top" 
			style={{ zIndex: 10, backgroundColor: "yellow", color: "#222", borderRadius: 8, fontSize: 20 }}
			content={label}
		/>
	);
}

export default CarInterior;