import { useState } from "react";
import styled from "styled-components";
//import SelectionControl from "./components/SelectControl";
//import SwitchControl from "./components/SwitchControl";


const ChargingControl = () => {

	const [chargeCurrent, setChargeCurrent] = useState(16);
	const [scheduledChargeStart, setScheduledChargeStart] = useState(5);

	return (
		<StyledDiv>
			<div 
				onClick={() => {
				}}
				style={{
					width: 300, height: 64, display: "flex", justifyContent: "center", alignItems: "center",
					//borderWidth: 2, borderRadius: 8, borderColor: "darkgray", borderStyle: "solid",
					backgroundColor: "lightgray", borderRadius: 8,
					marginTop: 16
				}}
			>
				<div style={{ fontSize: 18, fontWeight: 700 }}>Open Charge Port</div>
			</div>

			<div style={{ height: 48, display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "flex-start" }}>
				<div style={{ fontSize: 18, fontWeight: 700, color: "#6F6F6F" }}>
					{"Charge Current at this location"}
				</div>
			</div>
			<div
				style={{ 
					display: "flex", flexDirection: "row", 
					width: 250, height: 64, justifyContent: "center", alignItems: "center",
					//borderWidth: 2, borderRadius: 8, borderColor: "darkgray", borderStyle: "solid"
					backgroundColor: "lightgray", borderRadius: 8
				}}
			>
				<div 
					onClick={() => {
						if (chargeCurrent > 0) {
							setChargeCurrent(chargeCurrent - 1);
						}
					}}
					style={{
						width: 75, height: 64, display: "flex", justifyContent: "center", alignItems: "center",
					}}
				>
					<div style={{ fontSize: 28, fontWeight: 900 }}>{"\u2212"}</div>
				</div>
				<div
					style={{
						width: 100, height: 64, display: "flex", justifyContent: "center", alignItems: "center"
					}}
				>
					<div style={{ fontSize: 24, fontWeight: 700 }}>{chargeCurrent} A</div>
				</div>
				<div 
					onClick={() => {
						if (chargeCurrent < 30) {
							setChargeCurrent(chargeCurrent + 1);
						}
					}}
					style={{
						width: 75, height: 64, display: "flex", justifyContent: "center", alignItems: "center",
					}}
				>
					<div style={{ fontSize: 28, fontWeight: 900 }}>{"\u002B"}</div>
				</div>
			</div>

			<div style={{ height: 48, display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "flex-start" }}>
				<div style={{ fontSize: 18, fontWeight: 700, color: "#6F6F6F" }}>
					{"Scheduled Charge Start at this location"}
				</div>
			</div>
			<div
				style={{ 
					display: "flex", flexDirection: "row", 
					width: 250, height: 64, justifyContent: "center", alignItems: "center",
					//borderWidth: 2, borderRadius: 8, borderColor: "darkgray", borderStyle: "solid"
					backgroundColor: "lightgray", borderRadius: 8
				}}
			>
				<div 
					onClick={() => {
						if (scheduledChargeStart > 0) {
							setScheduledChargeStart(scheduledChargeStart - 1);
						}
					}}
					style={{
						width: 75, height: 64, display: "flex", justifyContent: "center", alignItems: "center",
					}}
				>
					<div style={{ fontSize: 28, fontWeight: 900 }}>{"\u2212"}</div>
				</div>
				<div
					style={{
						width: 100, height: 64, display: "flex", justifyContent: "center", alignItems: "center"
					}}
				>
					<div style={{ fontSize: 24, fontWeight: 700 }}>{scheduledChargeStart}:00</div>
				</div>
				<div 
					onClick={() => {
						if (scheduledChargeStart < 24) {
							setScheduledChargeStart(scheduledChargeStart + 1);
						}
					}}
					style={{
						width: 75, height: 64, display: "flex", justifyContent: "center", alignItems: "center",
					}}
				>
					<div style={{ fontSize: 28, fontWeight: 900 }}>{"\u002B"}</div>
				</div>
			</div>
		</StyledDiv>
	);
}

export default ChargingControl;

const StyledDiv = styled.div`
	width: 100%;
	height: 100%;
	background-color: #f0f0f0;
	padding: 32px;
	position: relative;
	color: #6F6F6F;
`;
