const Battery = ({
    distanceToEmpty, size=48
}) => {

    const percentage = distanceToEmpty / 10000;

    if (percentage > 95) {
        return <img src={"./exterior/battery100.png"} style={{ width: size, height: size }} alt="" />;
    }
    else if (percentage > 85) {
        return <img src={"./exterior/battery90.png"} style={{ width: size, height: size }} alt="" />;
    }
    else if (percentage > 75) {
        return <img src={"./exterior/battery80.png"} style={{ width: size, height: size }} alt="" />;
    }
    else if (percentage > 65) {
        return <img src={"./exterior/battery70.png"} style={{ width: size, height: size }} alt="" />;
    }
    else if (percentage > 55) {
        return <img src={"./exterior/battery60.png"} style={{ width: size, height: size }} alt="" />;
    }
    else if (percentage > 45) {
        return <img src={"./exterior/battery50.png"} style={{ width: size, height: size }} alt="" />;
    }
    else if (percentage > 35) {
        return <img src={"./exterior/battery40.png"} style={{ width: size, height: size }} alt="" />;
    }
    else if (percentage > 25) {
        return <img src={"./exterior/battery30.png"} style={{ width: size, height: size }} alt="" />;
    }
    else if (percentage > 15) {
        return <img src={"./exterior/battery20.png"} style={{ width: size, height: size }} alt="" />;
    }
    else if (percentage > 5) {
        return <img src={"./exterior/battery10.png"} style={{ width: size, height: size }} alt="" />;
    }
    else {
        return <img src={"./exterior/battery0.png"} style={{ width: size, height: size }} alt="" />;
    }
}

export default Battery;