export function initSignals() {		
    for (const signal of initSignalList) {
        if (signalMap.has(signal.name)) {
            signal.value = signalMap.get(signal.name);
        }
    }
    setSignals({ signals: initSignalList, type: SIGNAL_TYPES.VSS });

    let destination = null;
    if (signalMap.has("destination")) {
        destination = signalMap.get("destination");
    }
    setSignal("destination", destination, SIGNAL_TYPES.EXT);

    let autopilot = false;
    if (signalMap.has("autopilot")) {
        autopilot = signalMap.get("autopilot");
    }
    setSignal("autopilot", autopilot, SIGNAL_TYPES.EXT);
}
