import { useEffect, useState } from "react";
import styled from "styled-components";

import MainLayout from "./MainLayout";
import CarExterior from "./car_exterior/CarExterior";
import CarInterior from "./car_interior/CarInterior";
import MapSection from "./map/Map";
import Touchscreen from "./touchscreen/Touchscreen";

import { findServer } from "./vplug/WsReq";
import vssApi from "./vplug/VssAPI.json";
import { initSignals, signalMap } from "./vplug/Signals";


function App() {
	
	const SCREEN_WIDTH_MIN = 1528;
	const SCREEN_HEIGHT_MIN = 952;
	const BROWSER_WIDTH = window.innerWidth;
	const BROWSER_HEIGHT = window.innerHeight;
	const isLargeWindow = (BROWSER_WIDTH >= SCREEN_WIDTH_MIN && BROWSER_HEIGHT >= SCREEN_HEIGHT_MIN);

	const SCREEN_WIDTH = Math.max(BROWSER_WIDTH, SCREEN_WIDTH_MIN);
	const SCREEN_HEIGHT = Math.max(BROWSER_HEIGHT, SCREEN_HEIGHT_MIN);
	const HALF_HEIGHT = SCREEN_HEIGHT * 0.5;

	const gridLayout = {
		left: 0,
		right: SCREEN_WIDTH,
		up: HALF_HEIGHT,
		bottom: HALF_HEIGHT,
	};

	const [mainTicks, setMainTicks] = useState(0);
	const [departureReady, setDepartureReady] = useState(false);
	const [touchscreenOpen, setTouchscreenOpen] = useState(false);
	// see https://developers.google.com/maps/documentation/javascript/3.exp/reference#StreetViewPanoramaOptions
	const [streetViewPanoramaOptions, setStreetViewPanoramaOptions] = useState(null);
	
	const [autopilotOn, setAutopilotOn] = useState(false);
	const [heading, setHeading] = useState(0);
	const [destination, setDestination] = useState(null);

	const [gear, setGear] = useState("p");
	const [speed, setSpeed] = useState(0);
	const [distanceToEmpty, setDistanceToEmpty] = useState(500000);

	document.addEventListener("contextmenu", function(e) {
		e.preventDefault();
	}, false);

	useEffect(() => {
		findServer(initSignals);

		mainInterval.handle = setInterval(() => {
			mainInterval.ticks++;
			setMainTicks(mainInterval.ticks);
		}, mainInterval.period);

		return () => {
			if (mainInterval.handle) {
				clearInterval(mainInterval.handle);
				mainInterval.handle = null;
			}
		};
	}, 
	// eslint-disable-next-line
	[]);

	useEffect(() => {
		signalMap.set("autopilot", autopilotOn);
	}, 
	[autopilotOn]);

	useEffect(() => {
		signalMap.set(vssApi.Vehicle.CurrentLocation.Heading.name, heading);
	}, 
	[heading]);

	useEffect(() => {
		signalMap.set("destination", destination);
	}, 
	[destination]);

	useEffect(() => {
		let selGear;
		if (gear === "d") {
			selGear = vssApi.Vehicle.Powertrain.Transmission.SelectedGear.allowed.Drive;
		}
		else if (gear === "n") {
			selGear = vssApi.Vehicle.Powertrain.Transmission.SelectedGear.allowed.Neutral;
		}
		else if (gear === "r") {
			selGear = vssApi.Vehicle.Powertrain.Transmission.SelectedGear.allowed.Reverse;
		}
		else {
			selGear = vssApi.Vehicle.Powertrain.Transmission.SelectedGear.allowed.Park;
		}
		signalMap.set(vssApi.Vehicle.Powertrain.Transmission.SelectedGear.name, selGear);
	}, 
	[gear]);

	useEffect(() => {
		signalMap.set(vssApi.Vehicle.Speed.name, speed);
	}, 
	[speed]);

	useEffect(() => {
		signalMap.set(vssApi.Vehicle.Powertrain.Range.name, distanceToEmpty);
	}, 
	[distanceToEmpty]);

	return (
		<StyledDiv className="App">
			<MainLayout gridLayout={gridLayout}>
				<div></div>
				{departureReady ?
					<>
						<div style={{ width: SCREEN_WIDTH, height: HALF_HEIGHT }}>
							<MapSection
								SCREEN_WIDTH={SCREEN_WIDTH} SCREEN_HEIGHT={HALF_HEIGHT}
								mainTicks={mainTicks}
								autopilotOn={autopilotOn} setAutopilotOn={setAutopilotOn}
								heading={heading} setHeading={setHeading}
								destination={destination} setDestination={setDestination}
								speed={speed} setSpeed={setSpeed} gear={gear}
								distanceToEmpty={distanceToEmpty} setDistanceToEmpty={setDistanceToEmpty}
								streetViewPanoramaOptions={streetViewPanoramaOptions} setStreetViewPanoramaOptions={setStreetViewPanoramaOptions}
								setDepartureReady={setDepartureReady} touchscreenOpen={touchscreenOpen} setTouchscreenOpen={setTouchscreenOpen}
							/>
						</div>
						<div style={{ width: SCREEN_WIDTH, height: HALF_HEIGHT }}>
							<CarInterior
								SCREEN_WIDTH={SCREEN_WIDTH} SCREEN_HEIGHT={HALF_HEIGHT}
								touchscreenOpen={touchscreenOpen} setTouchscreenOpen={setTouchscreenOpen}
								autopilotOn={autopilotOn} setAutopilotOn={setAutopilotOn}
								speed={speed} setSpeed={setSpeed}
								heading={heading} setHeading={setHeading}
								destination={destination}
								gear={gear} setGear={setGear} distanceToEmpty={distanceToEmpty}
								departureReady={departureReady} setDepartureReady={setDepartureReady}
								streetViewPanoramaOptions={streetViewPanoramaOptions}
							/>
						</div>
					</>
					:
					<>
						<div></div>
						<div style={{ width: SCREEN_WIDTH, height: SCREEN_HEIGHT }}>
							<CarExterior
								departureReady={departureReady} setDepartureReady={setDepartureReady}
								distanceToEmpty={distanceToEmpty} setDistanceToEmpty={setDistanceToEmpty}
							/>
						</div>
					</>
				}
			</MainLayout>

			{departureReady &&
				<>
					<Touchscreen
						touchscreenOpen={touchscreenOpen}
						setTouchscreenOpen={setTouchscreenOpen}
						mainTicks={mainTicks}
						isLargeWindow={isLargeWindow}
						SCREEN_WIDTH_MIN={SCREEN_WIDTH_MIN} SCREEN_HEIGHT_MIN={SCREEN_HEIGHT_MIN}
					/>
					{ // background blurring
					touchscreenOpen &&
						<div
							style={{
								position: "absolute", zIndex: 99, width: "100%", height: "100%", backgroundColor: "white", opacity: 0.75
							}}
						/>
					}
				</>
			}
		</StyledDiv>
	);
}

export const mainInterval = {
	handle: null,
	period: 200,
	directionsRefreshCount: 5, // one second
	ticks: 0
};

export default App;

const StyledDiv = styled.div`
	min-height: 100vh;
	min-width: 100%;
	position: relative;
	display: grid;
	place-content: start;

	user-drag: none; 
	user-select: none;
	-moz-user-select: none;
	-webkit-user-drag: none;
	-webkit-user-select: none;
	-ms-user-select: none;

	img {
		pointer-events: none;
	}

	@keyframes blink {
		0% {
			opacity: 1;
		}
		25% {
			opacity: 0.5;
		}
		50% {
			opacity: 0;
		}
		75% {
			opacity: 0.5;
		}
		100% {
			opacity: 1;
		}
	}

	oncontextmenu: return false;
`;
