const DoorSwitch = ({
	doorOpen, pressSwitch, doorOpenImage, doorClosedImage
}) => {

	return (
		<div 
			style={{ 
				width: 32, height: 46, cursor: "pointer", 
				display: "flex", justifyContent: "center", alignItems: "center",
				borderWidth: 2, borderStyle: "solid", borderColor: doorOpen ? "#fbb03b" : "white"
			}}
			onClick={pressSwitch}
		>
			<img src={doorOpen ? doorOpenImage : doorClosedImage} style={{ width: 28, height: 42 }} alt="" />
		</div>
	);
}

export default DoorSwitch;