import { useState } from "react";
import styled from "styled-components";
import SelectionControl from "./components/SelectControl";
import SwitchControl from "./components/SwitchControl";


const AutoPilotControl = () => {

	const [cruiseFollowDistance, setCruiseFollowDistance] = useState(5);
	const [autosteer, setAutosteer] = useState(true);
	const [autosteerActivation, setAutosteerActivation] = useState("Double Click");
	const [navigateOnAutopilot, setNavigateOnAutopilot] = useState(true);
	const [trafficLight, setTrafficLight] = useState(true);
	const [greenTrafficLightChime, setGreenTrafficLightChime] = useState(true);
	const [FsdVisualizationPreview, setFsdVisualizationPreview] = useState(true);
	const [summon, setSummon] = useState(true);

	return (
		<StyledDiv>
			<div style={{ height: 48, display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "flex-start" }}>
				<div style={{ fontSize: 18, fontWeight: 700, color: "#6F6F6F" }}>
					{"Cruise Follow Distance"}
				</div>
			</div>
			<div
				style={{
					width: 250, height: 64, 
					display: "flex", flexDirection: "row", justifyContent: "center", alignItems: "center",
					//borderWidth: 2, borderRadius: 8, borderColor: "darkgray", borderStyle: "solid"
					backgroundColor: "lightgray", borderRadius: 8
				}}
			>
				<div 
					onClick={() => {
						if (cruiseFollowDistance > 0) {
							setCruiseFollowDistance(cruiseFollowDistance - 1);
						}
					}}
					style={{
						width: 75, height: 64, display: "flex", justifyContent: "center", alignItems: "center",
					}}
				>
					<div style={{ fontSize: 28, fontWeight: 900 }}>{"\u2212"}</div>
				</div>
				<div
					style={{
						width: 100, height: 64, display: "flex", justifyContent: "center", alignItems: "center"
					}}
				>
					<div style={{ fontSize: 24, fontWeight: 700 }}>{cruiseFollowDistance}</div>
				</div>
				<div 
					onClick={() => {
						if (cruiseFollowDistance < 10) {
							setCruiseFollowDistance(cruiseFollowDistance + 1);
						}
					}}
					style={{
						width: 75, height: 64, display: "flex", justifyContent: "center", alignItems: "center",
					}}
				>
					<div style={{ fontSize: 28, fontWeight: 900 }}>{"\u002B"}</div>
				</div>
			</div>

			<SwitchControl
				name="Autosteer (Beta)"
				value={autosteer}
				setValue={setAutosteer}
			/>
			<SelectionControl
				name="Autosteer Activation"
				valueList={["Single Click", "Double Click"]}
				value={autosteerActivation}
				setValue={setAutosteerActivation}
			/>
			<SwitchControl
				name="Navigate on Autopilot (Beta)"
				value={navigateOnAutopilot}
				setValue={setNavigateOnAutopilot}
			/>
			<div 
				onClick={() => {
				}}
				style={{
					width: 300, height: 64, display: "flex", justifyContent: "center", alignItems: "center",
					//borderWidth: 2, borderRadius: 8, borderColor: "darkgray", borderStyle: "solid",
					backgroundColor: "lightgray", borderRadius: 8,
					marginTop: 16
				}}
			>
				<div style={{ fontSize: 18, fontWeight: 700 }}>Customize Navigate on Autopilot</div>
			</div>
			<SwitchControl
				name="Traffic Light and Stop Sign Control (Beta)"
				value={trafficLight}
				setValue={setTrafficLight}
			/>
			<SwitchControl
				name="Green Traffic Light Chime"
				value={greenTrafficLightChime}
				setValue={setGreenTrafficLightChime}
			/>
			<SwitchControl
				name="Full Self-Driving Visualization Preview"
				value={FsdVisualizationPreview}
				setValue={setFsdVisualizationPreview}
			/>
			<SwitchControl
				name="Summon (Beta)"
				value={summon}
				setValue={setSummon}
			/>

			<div style={{ display: "flex", flexDirection: "row", gap: 16 }}>
				<div 
					onClick={() => {
					}}
					style={{
						width: 200, height: 64, display: "flex", justifyContent: "center", alignItems: "center",
						//borderWidth: 2, borderRadius: 8, borderColor: "darkgray", borderStyle: "solid",
						backgroundColor: "lightgray", borderRadius: 8,
						marginTop: 16
					}}
				>
					<div style={{ fontSize: 18, fontWeight: 700 }}>Customize Summon</div>
				</div>
				<div 
					onClick={() => {
					}}
					style={{
						width: 200, height: 64, display: "flex", justifyContent: "center", alignItems: "center",
						//borderWidth: 2, borderRadius: 8, borderColor: "darkgray", borderStyle: "solid",
						backgroundColor: "lightgray", borderRadius: 8,
						marginTop: 16
					}}
				>
					<div style={{ fontSize: 18, fontWeight: 700 }}>Standby Mode</div>
				</div>
			</div>
		</StyledDiv>
	);
}

export default AutoPilotControl;

const StyledDiv = styled.div`
	width: 100%;
	height: 100%;
	background-color: #f0f0f0;
	padding: 32px;
	position: relative;
	color: #6F6F6F;
`;
