import { useEffect, useState, useRef } from "react";
import { useJsApiLoader } from '@react-google-maps/api';

import LeftSideBar from "./LeftSideBar";
import CenterScreen from "./CenterScreen";
import RightSideBar from "./RightSideBar";
import DirectionsBox from "./DirectionsBox";
//import SashControlExample from "./SashControlExample";

import { getNewLocation } from "./GeometryUtil";
import { SIGNAL_TYPES, subscribeSignals, unsubscribeSignals, setSignals } from "../vplug/WsReq";
import vssApi from "../vplug/VssAPI.json";
import { mainInterval } from "../App";

const MAP_API_KEY = process.env.REACT_APP_GOOGLE_API_KEY;
const libraries = ["geometry", "drawing", "places"];

const mapMemory = {
	carPosition: null,
	traveledDistanceSinceStart: 0
};


const MapSection = ({
	SCREEN_WIDTH, SCREEN_HEIGHT,
	mainTicks,
	autopilotOn, setAutopilotOn,
	heading, setHeading,
	destination, setDestination,
	speed, setSpeed, gear,
	distanceToEmpty, setDistanceToEmpty,
	streetViewPanoramaOptions, setStreetViewPanoramaOptions,
	touchscreenOpen, setTouchscreenOpen, setDepartureReady
}) => {
	
	const [map, setMap] = useState(null);
	const [mapType, setMapType] = useState("terrain");

	const [carPosition, setCarPosition] = useState(null);

	const [directionsResponse, setDirectionsResponse] = useState(null);
	
	const [streetViewService, setStreetViewService] = useState(null);

	const [traveledDistanceSinceStart, setTraveledDistanceSinceStart] = useState(0);

	const { isLoaded } = useJsApiLoader({
		id: 'google-map-script',
		googleMapsApiKey: MAP_API_KEY,
		libraries,
	});

	useEffect(() => {
		if (mapMemory.carPosition) {
			setCarPosition(mapMemory.carPosition);
			setTraveledDistanceSinceStart(mapMemory.traveledDistanceSinceStart);
		}
		else {
			navigator.geolocation.getCurrentPosition(async (position) => {
				setTimeout(() => {
					setCarPosition({ lat: position.coords.latitude, lng: position.coords.longitude });
				}, 500);
			});
		}

		subscribeSignals(subscriptionData, subscriptionCallback);

		return () => {
			unsubscribeSignals({ channel: subscriptionData.channel });
		};
	}, 
	// eslint-disable-next-line
	[]);

	const subscriptionData = {
		channel: "driving-simulation",
		type: SIGNAL_TYPES.EXT,
		signals: [
			{ name: "destination" }
		]
	};

	const subscriptionCallback = (msg) => {
		for (const signal of msg.signals) {
			if (signal.name === "destination") {
				setDestination(signal.value);
			}
		}
	}

	useEffect(() => {
		if (carPosition != null) {
			let _latitude;
			let _longitude;

			if (typeof carPosition.lat === "function") {
				_latitude = carPosition.lat();
				_longitude = carPosition.lng();
			}
			else {
				_latitude = carPosition.lat;
				_longitude = carPosition.lng;
			}

			const time = new Date().toISOString();
			const jsonData = {
				signals: [
					{ name: vssApi.Vehicle.CurrentLocation.Timestamp.name, value: time },
					{ name: vssApi.Vehicle.CurrentLocation.Latitude.name, value: _latitude },
					{ name: vssApi.Vehicle.CurrentLocation.Longitude.name, value: _longitude },
					{ name: vssApi.Vehicle.CurrentLocation.Heading.name, value: heading },
					{ name: vssApi.Vehicle.Speed.name, value: speed },
					{ name: vssApi.Vehicle.TraveledDistanceSinceStart.name, value: traveledDistanceSinceStart },
					{ name: vssApi.Vehicle.Powertrain.Range.name, value: distanceToEmpty }
				],
				type: SIGNAL_TYPES.VSS
			};
			setSignals(jsonData);

			mapMemory.carPosition = carPosition;
			mapMemory.traveledDistanceSinceStart = traveledDistanceSinceStart;
		}
	},
	// eslint-disable-next-line
	[carPosition, heading, speed]);

	useEffect(() => {
		const jsonData = {
			signals: [
				{ name: "autopilot", value: autopilotOn }
			],
			type: SIGNAL_TYPES.EXT
		};
		setSignals(jsonData);
	},
	[autopilotOn]);

	useEffect(() => {
		if (isLoaded) {
			setStreetViewService(new window.google.maps.StreetViewService());
			//setElevator(new window.google.maps.ElevationService());
		}
	}, [isLoaded]);

	function changeMapType() {
		if (mapType === "terrain") {
			setMapType("hybrid");
		}
		else {
			setMapType("terrain");
		}
	}

	useEffect(() => {
		if (!autopilotOn && speed > 0 && distanceToEmpty > 0) {
			forward();
		}
	}, 
	// eslint-disable-next-line
	[mainTicks]);

	function forward() {
		const delta_d = speed * mainInterval.period / 3600;
		if (gear === "d") {
			setCarPosition(getNewLocation(carPosition, delta_d, heading));
		}
		else if (gear === "r") {
			setCarPosition(getNewLocation(carPosition, delta_d, (heading + 180)));
		}

		setTraveledDistanceSinceStart(traveledDistanceSinceStart + delta_d/1000);
		if (distanceToEmpty > delta_d) {
			setDistanceToEmpty(distanceToEmpty - delta_d);
		}
		else {
			setDistanceToEmpty(0);
			setSpeed(0);
		}
	}

	const originRef = useRef();

	function relocate() {
		if (originRef.current) {
			const addr = originRef.current.value;
			if (addr && addr.length >= 3) {
				const service = new window.google.maps.places.PlacesService(map);
				const request = {
					query: originRef.current.value,
					fields: ['name', 'geometry'],
				};
				service.findPlaceFromQuery(request, function (results, status) {
					if (status === window.google.maps.places.PlacesServiceStatus.OK) {
						const loc = results[0].geometry.location;
						if (loc) {
							//const newPos = { lat: loc.lat(), lng: loc.lng() };
							//setCarPosition(newPos);
							setCarPosition(loc);
						}
					}
				});
			}
		}
	}

	return (
		<div style={{
			position: "relative", height: "100%", width: "100%",
			display: "flex", flexDirection: "row", backgroundColor: "#f8f8f8"
		}}>
			<LeftSideBar
				originRef={originRef}
				relocate={relocate}
				destination={destination}
				setDestination={setDestination}
				setAutopilotOn={setAutopilotOn}
				MAP_API_KEY={MAP_API_KEY}
				streetViewPanoramaOptions={streetViewPanoramaOptions}
				speed={speed}
				gear={gear}
				setDepartureReady={setDepartureReady}
				setTouchscreenOpen={setTouchscreenOpen}
			/>
			<CenterScreen
				carPosition={carPosition}
				setCarPosition={setCarPosition}
				heading={heading}
				setHeading={setHeading}
				speed={speed}
				autopilotOn={autopilotOn}
				streetViewService={streetViewService}
				streetViewPanoramaOptions={streetViewPanoramaOptions}
				setStreetViewPanoramaOptions={setStreetViewPanoramaOptions}
				MAP_API_KEY={MAP_API_KEY}
				touchscreenOpen={touchscreenOpen}
			/>
			<RightSideBar
				isLoaded={isLoaded}
				map={map}
				setMap={setMap}
				carPosition={carPosition}
				setCarPosition={setCarPosition}
				mapType={mapType}
				heading={heading}
				directionsResponse={directionsResponse}
				speed={speed}
				autopilotOn={autopilotOn}
				changeMapType={changeMapType}
				MAP_API_KEY={MAP_API_KEY}
				streetViewPanoramaOptions={streetViewPanoramaOptions}
			/>

			{carPosition && isLoaded &&
				<DirectionsBox
					destination={destination}
					setDestination={setDestination}
					directionsResponse={directionsResponse}
					setDirectionsResponse={setDirectionsResponse}
					mainTicks={mainTicks}
					autopilotOn={autopilotOn}
					setAutopilotOn={setAutopilotOn}
					speed={speed}
					setSpeed={setSpeed}
					carPosition={carPosition}
					setCarPosition={setCarPosition}
					heading={heading}
					setHeading={setHeading}
					// new features
					SCREEN_WIDTH={SCREEN_WIDTH}
					SCREEN_HEIGHT={SCREEN_HEIGHT}
					traveledDistanceSinceStart={traveledDistanceSinceStart}
					setTraveledDistanceSinceStart={setTraveledDistanceSinceStart}
					distanceToEmpty={distanceToEmpty}
					setDistanceToEmpty={setDistanceToEmpty}
					originRef={originRef}
					relocate={relocate}
				/>
			}

			{
			// <SashControlExample
			// 	SCREEN_WIDTH={SCREEN_WIDTH} SCREEN_HEIGHT={SCREEN_HEIGHT} SIDE_BAR_WIDTH={SCREEN_WIDTH/2}
			// />
			}
		</div>
	);
}

export default MapSection;