import React, { useState, useEffect } from 'react';
import ReactStreetview from './ReactStreetview';
import { getDistance } from './GeometryUtil';


const DrivingView = ({
	carPosition,
    setCarPosition,
    heading,
    setHeading,
	speed,
	autopilotOn,
	streetViewService,
	streetViewPanoramaOptions,
	setStreetViewPanoramaOptions,
	MAP_API_KEY,
	touchscreenOpen
}) => {

	const STREET_VIEW_UPDATE_INTERVAL = 2500;

	const [panoUpdateTimer, setPanoUpdateTimer] = useState(null);
	const [streetViewPosition, setStreetViewPosition] = useState(null);
	const [streetViewHeading, setStreetViewHeading] = useState(0);

	useEffect(() => {
		if (!panoUpdateTimer && !touchscreenOpen) {
			let positionObj;
			if (typeof carPosition.lat === "number") {
				positionObj = new window.google.maps.LatLng(carPosition.lat, carPosition.lng);
			}
			else {
				positionObj = new window.google.maps.LatLng(carPosition.lat(), carPosition.lng());
			}

			const panoRequest = {
				location: positionObj,
				preference: window.google.maps.StreetViewPreference.NEAREST,
				radius: 150,
				source: window.google.maps.StreetViewSource.OUTDOOR
			};

			streetViewService.getPanorama(panoRequest, function (panoData, status) {
				if (status === window.google.maps.StreetViewStatus.OK) {
					// location: description, latLng, pano, shortDescription
					// links
					setStreetViewPanoramaOptions({
						position: panoData.location.latLng,
						pov: { heading: heading, pitch: 0 },
						zoom: 1
					});
				}
				else {
					//Handle other statuses here
				}
			});

			if (autopilotOn || speed > 0) {
				const handle = setTimeout(() => {
					setPanoUpdateTimer(null);
				}, STREET_VIEW_UPDATE_INTERVAL);
				setPanoUpdateTimer(handle);
			}
		}
	},
	// eslint-disable-next-line
	[carPosition, heading, panoUpdateTimer, touchscreenOpen]);

	useEffect(() => {
		if (streetViewPosition) {
			if (!autopilotOn && speed === 0) {
				const d = getDistance(carPosition, streetViewPosition);
				if (d > 100) {
					setCarPosition(streetViewPosition);
					setHeading(streetViewHeading);
				}
			}
		}
	},
	// eslint-disable-next-line
	[streetViewPosition, streetViewHeading]);

	return (
		<>
			{streetViewPanoramaOptions && 
			<ReactStreetview
				apiKey={MAP_API_KEY}
				streetViewPanoramaOptions={streetViewPanoramaOptions}
				onPositionChanged={pos => {
					setStreetViewPosition(pos);
				}}
				onPovChanged={pov => {
					setStreetViewHeading(pov.heading);
				}}
			/>}
		</>
	);
}

export default DrivingView;