import { useEffect, useState } from "react";
import { setSignal } from "../vplug/WsReq";

const DoorWindow = ({
    windowName, 
    windowPos, setWindowPos,
    windowChildLock = false
}) => {

    const DELTA = 10;
    const [longPressInterval, setLongPressInterval] = useState(null);
	const [longPressOn, setLongPressOn] = useState(false);
	const [dir, setDir] = useState("none");

	useEffect(() => {
		return () => {
			// Release timers
			if (longPressInterval != null) {
				clearInterval(longPressInterval);
			}
		};
	}, 
	// eslint-disable-next-line
	[]);

	function getNewValue(oldValue, direction) {
		let newValue = oldValue;
		if (direction === "up") {
			newValue += DELTA;
            if (newValue > 100) {
                newValue = 100;
            }
		}
		else {
			newValue -= DELTA;
            if (newValue < 0) {
                newValue = 0;
            }
		}
		return newValue;
	}

    function setNewValue(newVal) {
        setSignal(windowName, newVal);
        setWindowPos(newVal);
    }

	function handleDecrement() {
		if (longPressInterval != null) {
			clearInterval(longPressInterval);
			setLongPressInterval(null);
			if (longPressOn) {
				setLongPressOn(false);
				return;
			}
		}
		
		let newVal = getNewValue(windowPos, "down");
		setNewValue(newVal);
	}

	function handleIncrement() {
		if (longPressInterval != null) {
			clearInterval(longPressInterval);
			setLongPressInterval(null);
			if (longPressOn) {
				setLongPressOn(false);
				return;
			}
		}
		
		let newVal = getNewValue(windowPos, "up");
		setNewValue(newVal);
	}

	function handleDecrementMouseDown() {
		setLongPressOn(false);
		var newVal = windowPos;
		const interval = setInterval(() => {
			setLongPressOn(true);
			newVal = getNewValue(newVal, "down");	
			setNewValue(newVal);
		}, 250);
		setLongPressInterval(interval);
	}

	function handleIncrementMouseDown() {
		setLongPressOn(false);
		var newVal = windowPos;
		const interval = setInterval(() => {
			setLongPressOn(true);
			newVal = getNewValue(newVal, "up");			
			setNewValue(newVal);
		}, 250);
		setLongPressInterval(interval);
	}

    return (
        <div
            style={{
                width: 32, height: 46, 
				borderWidth: 1, borderStyle: "solid", borderColor: (dir !== "none")? "#fbb03b" :"white",
                display: "flex", flexDirection: "column", backgroundColor: "#707070", opacity: 0.5,
                cursor: "pointer"
            }}
        >
            <div
                style={{ width: "100%", height: "50%", display: "flex", justifyContent: "center", alignItems: "center" }}
                onClick={() => {
                    if (!windowChildLock) {
                        handleDecrement();
                    }
                }}
                onMouseDown={() => {
					setDir("up");
                    if (!windowChildLock) {
                        handleDecrementMouseDown();
                    }
                }}
				onMouseUp={() => {
					setDir("none");
                }}
            >
                <img 
					src={(dir === "up")? "./interior/up-active.png" : "./interior/up.png"} 
					style={{ width: 22, height: 22 }} alt="" 
				/>
            </div>
            <div
                style={{ width: "100%", height: "50%", display: "flex", justifyContent: "center", alignItems: "center" }}
                onClick={() => {
                    if (!windowChildLock) {
                        handleIncrement();
                    }
                }}
                onMouseDown={() => {
					setDir("down");
                    if (!windowChildLock) {
                        handleIncrementMouseDown();
                    }
                }}
				onMouseUp={() => {
					setDir("none");
                }}
            >
                <img 
					src={(dir === "down")? "./interior/down-active.png" : "./interior/down.png"} 
					style={{ width: 22, height: 22 }} alt="" 
				/>
            </div>
        </div>
    );
}

export default DoorWindow;