import React, { useEffect, useState } from "react";
//import Iframe from 'react-iframe';

import { SIGNAL_TYPES, setSignal, setSignals, subscribeSignals, unsubscribeSignals } from "../../vplug/WsReq";
import vssApi from "../../vplug/VssAPI.json";

import StatusOverlay from "./StatusOverlay";
import IndicatorOverlay from "./IndicatorOverlay";
//import GearBox from "./GearBox";


const CarStatus = () => {

	const IMAGE_WIDTH = 1480 * 0.3;
	const IMAGE_HEIGHT = 800 * 0.3;

	const SelectedGearType = vssApi.Vehicle.Powertrain.Transmission.SelectedGear;

	const Row1DriverSideDoorType = vssApi.Vehicle.Cabin.Door.Row1.DriverSide;
	const Row1PassengerSideDoorType = vssApi.Vehicle.Cabin.Door.Row1.PassengerSide;
	const Row2DriverSideDoorType = vssApi.Vehicle.Cabin.Door.Row2.DriverSide;
	const Row2PassengerSideDoorType = vssApi.Vehicle.Cabin.Door.Row2.PassengerSide;

	const TrunkFrontType = vssApi.Vehicle.Body.Trunk.Front;
	const TrunkRearType = vssApi.Vehicle.Body.Trunk.Rear;
	const ChargePortStatusType = vssApi.Vehicle.Powertrain.TractionBattery.Charging.ChargePortFlap;

	const [gear, setGear] = useState("p");
	const [speed, setSpeed] = useState(0);
	const [distanceToEmpty, setDistanceToEmpty] = useState(0);
	const [hazardReq, setHazardReq] = useState(false);

	const [autopilotOn, setAutopilotOn] = useState(false);

	const [carLocked, setCarLocked] = useState(true);
	const [row1DriverDoorLocked, setRow1DriverDoorLocked] = useState(true);
	const [row1PassengerDoorLocked, setRow1PassengerDoorLocked] = useState(true);
	const [row2DriverDoorLocked, setRow2DriverDoorLocked] = useState(true);
	const [row2PassengerDoorLocked, setRow2PassengerDoorLocked] = useState(true);
	const [frunkLocked, setFrunkLocked] = useState(true);
	const [trunkLocked, setTrunkLocked] = useState(true);

	const [frunkOpen, setFrunkOpen] = useState(false);
	const [trunkOpen, setTrunkOpen] = useState(false);
	const [chargePortOpen, setChargePortOpen] = useState(false);

	const [leftTurnOn, setLeftTurnOn] = useState(false);
	const [rightTurnOn, setRightTurnOn] = useState(false);

	const [row1DriverDoorOpen, setRow1DriverDoorOpen] = useState(false);
	const [row1PassengerDoorOpen, setRow1PassengerDoorOpen] = useState(false);
	const [row2DriverDoorOpen, setRow2DriverDoorOpen] = useState(false);
	const [row2PassengerDoorOpen, setRow2PassengerDoorOpen] = useState(false);

	const signalList = new Map();
	const subscriptionData = {
		channel: "touchscreen/car-status",
		type: SIGNAL_TYPES.VSS,
		signals: []
	};

	useEffect(() => {
		initSignalList();
		subscribeSignals(subscriptionData, subscriptionCallback);
		subscribeSignals(subscriptionDataExt, subscriptionCallbackExt);

		return () => {
			unsubscribeSignals({ channel: subscriptionData.channel });
			unsubscribeSignals({ channel: subscriptionDataExt.channel });
		};
	}, 
	// eslint-disable-next-line
	[]);

	const subscriptionCallback = (msg) => {
		for (const signal of msg.signals) {
			if (signal.value != null && signal.value !== "undefined") {
				const callback = signalList.get(signal.name);
				if (callback) {
					callback(signal.value);
				}
			}
		}
	}

	function initSignalList() {
		signalList.set(SelectedGearType.name, (value) => {
			if (value === SelectedGearType.allowed.Drive) {
				setGear("d");
			}
			else if (value === SelectedGearType.allowed.Neutral) {
				setGear("n");
			}
			else if (value === SelectedGearType.allowed.Reverse) {
				setGear("r");
			}
			else {
				setGear("p");
			}
		});
		signalList.set(vssApi.Vehicle.Speed.name, (value) => {
			if (value < 511.5) {
				setSpeed(value);
			}
			else {
				setSpeed(0);
			}
		});
		signalList.set(vssApi.Vehicle.Powertrain.Range.name, (value) => {
			if (value < 1023000) {
				setDistanceToEmpty(value);
			}
			else {
				setDistanceToEmpty(0);
			}
		});
		signalList.set(vssApi.Vehicle.Body.Lights.Hazard.IsSignaling.name, (value) => {
			setHazardReq(value === "True");
		});
		// Doors Lock
		signalList.set(Row1DriverSideDoorType.IsLocked.name, (value) => {
			setRow1DriverDoorLocked(value === "True");
		});
		signalList.set(Row1PassengerSideDoorType.IsLocked.name, (value) => {
			setRow1PassengerDoorLocked(value === "True");
		});
		signalList.set(Row2DriverSideDoorType.IsLocked.name, (value) => {
			setRow2DriverDoorLocked(value === "True");
		});
		signalList.set(Row2PassengerSideDoorType.IsLocked.name, (value) => {
			setRow2PassengerDoorLocked(value === "True");
		});
		signalList.set(TrunkFrontType.IsLocked.name, (value) => {
			setFrunkLocked(value === "True");
		});
		signalList.set(TrunkRearType.IsLocked.name, (value) => {
			setTrunkLocked(value === "True");
		});
		// Doors Open
		signalList.set(Row1DriverSideDoorType.IsOpen.name, (value) => {
			setRow1DriverDoorOpen(value === "True");
		});
		signalList.set(Row1PassengerSideDoorType.IsOpen.name, (value) => {
			setRow1PassengerDoorOpen(value === "True");
		});
		signalList.set(Row2DriverSideDoorType.IsOpen.name, (value) => {
			setRow2DriverDoorOpen(value === "True");
		});
		signalList.set(Row2PassengerSideDoorType.IsOpen.name, (value) => {
			setRow2PassengerDoorOpen(value === "True");
		});
		signalList.set(TrunkFrontType.IsOpen.name, (value) => {
			setFrunkOpen(value === "True");
		});
		signalList.set(TrunkRearType.IsOpen.name, (value) => {
			setTrunkOpen(value === "True");
		});
		signalList.set(ChargePortStatusType.name, (value) => {
			setChargePortOpen(value === ChargePortStatusType.allowed.OPEN);
		});
		// Turn Signals
		signalList.set(vssApi.Vehicle.Body.Lights.DirectionIndicator.Left.IsSignaling.name, (value) => {
			setLeftTurnOn(value === "True");
		});
		signalList.set(vssApi.Vehicle.Body.Lights.DirectionIndicator.Right.IsSignaling.name, (value) => {
			setRightTurnOn(value === "True");
		});

		for (const signal of signalList.keys()) {
			subscriptionData.signals.push({ name: signal });
		}
	}

	const subscriptionDataExt = {
		channel: "touchscreen/car-status-ext",
		type: SIGNAL_TYPES.EXT,
		signals: [
			{ name: "autopilot" }
		]
	};

	const subscriptionCallbackExt = (msg) => {
		for (const signal of msg.signals) {
			if (signal.name === "autopilot") {
				setAutopilotOn(signal.value);
			}
		}
	}

	useEffect(() => {
		if (row1DriverDoorLocked && row1PassengerDoorLocked
			&& row2DriverDoorLocked && row2PassengerDoorLocked
			&& frunkLocked && trunkLocked) {
			setCarLocked(true);
		}
		else {
			setCarLocked(false);
		}
	}, [row1DriverDoorLocked, row1PassengerDoorLocked, row2DriverDoorLocked, row2PassengerDoorLocked, frunkLocked, trunkLocked]);

	function getImage() {
		if (isAnySideDoorOpen()) {
			return ("./tesla/tesla-door-open2.jpg");
		}
		else if (frunkOpen) {
			if (trunkOpen) {
				return ("./tesla/model-y-frunk-trunk-open.jpg");
			}
			return ("./tesla/model-y-frunk-open.webp");
		}
		else if (trunkOpen) {
			if (frunkOpen) {
				return ("./tesla/model-y-frunk-trunk-open.jpg");
			}
			return ("./tesla/model-y-trunk-open.jpg");
		}
		else if (chargePortOpen) {
			return ("./tesla/model-y-charge-open.jpg");
		}
		else {
			return ("./tesla/model-y.jpg");
		}
	}

	function isAnyDoorOpen() {
		return (isAnySideDoorOpen() || frunkOpen || trunkOpen);
	}

	function isAnySideDoorOpen() {
		return (row1DriverDoorOpen || row1PassengerDoorOpen || row2DriverDoorOpen || row2PassengerDoorOpen);
	}

	function isAllDoorClosed() {
		return (!frunkOpen && !trunkOpen && !chargePortOpen);
	}

	const doorLockPosInit = {
		vlTop: -43,
		vlLeft: 254,
		vlWidth: 1
	};
	const frunkPosInit = {
		width: 130,
		height: 70,
		top: 82,
		left: 30,
		vlTop: 37,
		vlLeft: 70,
		vlWidth: 1
	};
	const trunkPosInit = {
		width: 100,
		height: 60,
		top: -8,
		left: 320,
		vlTop: -66,
		vlLeft: 370,
		vlWidth: 1
	};
	const chargingDoorPosInit = {
		width: 50,
		height: 40,
		top: 47,
		left: 380,
		vlTop: 67,
		vlLeft: 420,
		vlHeight: 1,
		vlWidth: 32
	};
	const [doorLockPos, setDoorLockPos] = useState(doorLockPosInit);
	const [frunkPos, setFrunkPos] = useState(frunkPosInit);
	const [trunkPos, setTrunkPos] = useState(trunkPosInit);
	const [chargingDoorPos, setChargingDoorPos] = useState(chargingDoorPosInit);

	useEffect(() => {
		let pos1 = doorLockPosInit;
		let pos2 = frunkPosInit;
		let pos3 = trunkPosInit;
		let pos4 = chargingDoorPosInit;

		if (frunkOpen) {
			pos2.width = 225;
			pos2.height = 200;
			pos2.top = 17;
			pos2.left = 40;			
		}

		if (trunkOpen) {
			pos3.width = 150;
			pos3.height = 150;
			pos3.top = -8;
			pos3.left = 260;
		}

		if (chargePortOpen) {
			pos4.width = 170;
			pos4.height = 150;
			pos4.vlWidth = 290;
			pos4.top = -8;
			pos4.left = 25;
			pos4.vlLeft = 165;
		}
		
		setDoorLockPos(pos1);
		setFrunkPos(pos2);
		setTrunkPos(pos3);
		setChargingDoorPos(pos4);
	},
	// eslint-disable-next-line 
	[frunkOpen, trunkOpen, chargePortOpen]);

	function handleCarLock() {
		if (!carLocked && !isAnyDoorOpen()) {
			setSignals({
				signals: [
					{ name: Row1DriverSideDoorType.IsLocked.name, value: "True" },
					{ name: Row1PassengerSideDoorType.IsLocked.name, value: "True" },
					{ name: Row2DriverSideDoorType.IsLocked.name, value: "True" },
					{ name: Row2PassengerSideDoorType.IsLocked.name, value: "True" },
					{ name: TrunkFrontType.IsLocked.name, value: "True" },
					{ name: TrunkRearType.IsLocked.name, value: "True" }
				],
				type: SIGNAL_TYPES.VSS
			});
		}
		else {
			setSignals({
				signals: [
					{ name: Row1DriverSideDoorType.IsLocked.name, value: "False" },
					{ name: Row1PassengerSideDoorType.IsLocked.name, value: "False" },
					{ name: Row2DriverSideDoorType.IsLocked.name, value: "False" },
					{ name: Row2PassengerSideDoorType.IsLocked.name, value: "False" },
					{ name: TrunkFrontType.IsLocked.name, value: "True" },
					{ name: TrunkRearType.IsLocked.name, value: "True" }
				],
				type: SIGNAL_TYPES.VSS
			});
		}
	}

	function handleFrunkOpen() {
		if (!frunkOpen) {
			setSignal(TrunkFrontType.IsLocked.name, "False");
			setSignal(TrunkFrontType.IsOpen.name, "True");
		}
		else {
			setSignal(TrunkFrontType.IsOpen.name, "False");
		}
	}

	function handleTrunkOpen() {
		if (!trunkOpen) {
			setSignal(TrunkRearType.IsLocked.name, "False");
			setSignal(TrunkRearType.IsOpen.name, "True");
		}
		else {
			setSignal(TrunkRearType.IsOpen.name, "False");
		}
	}

	function handleChargeDoorOpen() {
		if (!chargePortOpen) {
			setSignal(ChargePortStatusType.name, ChargePortStatusType.allowed.OPEN);
		}
		else {
			setSignal(ChargePortStatusType.name, ChargePortStatusType.allowed.CLOSED);
		}
	}

	// const threeJsModels = [
	// 	"http://vr.ff.com/us/",
	// 	"https://carvisualizer.plus360degrees.com/threejs/",
	// 	"https://www.plus360degrees.com/uxcars/",
	// 	"http://renaultespace.littleworkshop.fr/"
	// ];

	// const [activeThreeJs, setActiveThreeJs] = useState(0);

	// function get3DView() {
	// 	return threeJsModels[activeThreeJs];
	// }

	const styledContainer = {
		position: "relative",
		height: "100%",
		width: "100%",
		backgroundColor: gear === "d" ? "#eeeeee" : "#f8f8f8",
		display: "flex",
		flexDirection: "column",
		justifyContent: "center",
		alignItems: "center",
		color: "#707070",
		borderTopLeftRadius: 8
	};

	const styledCarImage = {
		twoDContainer: {
			position: "relative",
			//height: "100%",
			//width: "100%",
			display: "flex",
			alignItems: "center",
			justifyContent: "center",
			image: {
				width: IMAGE_WIDTH,
				height: IMAGE_HEIGHT,
			}
		},
		threeDContainer: {
			height: "100%",
			width: "100%",
			display: "flex",
			alignItems: "center",
			justifyContent: "center",
			image: {
				width: 274 * 2,
				height: 285 * 2
			}
		}
	};

	const styledDoorLock = {
		cursor: "pointer",
		position: "absolute",
		backgroundColor: "transparent",
		//backgroundColor: "yellow",
		//opacity: 0.5,
		top: doorLockPos.vlTop - 64,
		left: doorLockPos.vlLeft - 32,
		width: 64,
		height: 64,
		padding: 8
	};
	const styledDoorLockImg = {
		width: 48,
		height: 48
	};
	const styledDoorLockVL = {
		position: "absolute",
		backgroundColor: "darkgray",
		width: doorLockPos.vlWidth,
		height: 50,
		top: doorLockPos.vlTop,
		left: doorLockPos.vlLeft
	};

	const styledFrunk = {
		cursor: "pointer",
		position: "absolute",
		top: frunkPos.vlTop,
		left: frunkPos.vlLeft - 64,
		width: 64,
		height: 64,
		color: "inherit",
		backgroundColor: "transparent",
		//backgroundColor: "yellow",
		//opacity: 0.5,
		display: "flex",
		flexDirection: "column",
		justifyContent: "flex-start",
		alignItems: "center",
		border: 0
	};
	const styledNormalLabel = {
		fontSize: 16,
		fontWeight: 500
	};
	const styledLargeLabel = {
		fontSize: 20,
		fontWeight: 700
	};
	const styledFrunkVL = {
		position: "absolute",
		backgroundColor: "darkgray",
		height: 90,
		width: frunkPos.vlWidth,
		top: frunkPos.vlTop,
		left: frunkPos.vlLeft
	};
	const styledFrunkBtn = {
		cursor: "pointer",
		position: "absolute",
		width: frunkPos.width,
		height: frunkPos.height,
		top: frunkPos.top,
		left: frunkPos.left,
		backgroundColor: "transparent",
		//backgroundColor: "yellow",
		//opacity: 0.5,
		border: 0
	};

	const styledTrunk = {
		cursor: "pointer",
		position: "absolute",
		top: trunkPos.vlTop,
		left: trunkPos.vlLeft,
		width: 64,
		height: 64,
		color: "inherit",
		backgroundColor: "transparent",
		//backgroundColor: "yellow",
		//opacity: 0.5,
		display: "flex",
		flexDirection: "column",
		justifyContent: "flex-start",
		alignItems: "center",
		border: 0
	};
	const styledTrunkVL = {
		position: "absolute",
		backgroundColor: "darkgray",
		height: 90,
		width: trunkPos.vlWidth,
		top: trunkPos.vlTop,
		left: trunkPos.vlLeft
	};
	const styledTrunkBtn = {
		cursor: "pointer",
		position: "absolute",
		width: trunkPos.width,
		height: trunkPos.height,
		top: trunkPos.top,
		left: trunkPos.left,
		backgroundColor: "transparent",
		//backgroundColor: "yellow",
		//opacity: 0.5,
		border: 0
	};

	const styledChargingDoor = {
		cursor: "pointer",
		position: "absolute",
		top: chargingDoorPos.vlTop - 28,
		left: chargingDoorPos.vlLeft + chargingDoorPos.vlWidth,
		width: 56,
		height: 56,
		padding: 8,
		color: "inherit",
		backgroundColor: "transparent",
		//backgroundColor: "yellow",
		//opacity: 0.5
	};
	const styledChargingDoorImg = {
		width: 40,
		height: 40
	};
	const styledChargingDoorVL = {
		position: "absolute",
		backgroundColor: chargePortOpen ? "yellow" : "darkgray",
		height: chargingDoorPos.vlHeight,
		width: chargingDoorPos.vlWidth,
		top: chargingDoorPos.vlTop,
		left: chargingDoorPos.vlLeft
	};
	const styledChargingDoorBtn = {
		cursor: "pointer",
		position: "absolute",
		width: chargingDoorPos.width,
		height: chargingDoorPos.height,
		top: chargingDoorPos.top,
		left: chargingDoorPos.left,
		backgroundColor: "transparent",
		//backgroundColor: "yellow",
		//opacity: 0.5,
		border: 0
	};
	
	return (
		<div style={styledContainer}>
			{gear === "d" ?
				<div style={styledCarImage.threeDContainer}>
					{
						<img style={styledCarImage.threeDContainer.image} src="./tesla/tesla-driving.gif" alt=""/>
					}
					{
						// <Iframe url={get3DView()}
						// 	width="100%"
						// 	height="100%"
						// 	id="my-iframe"
						// 	className=""
						// 	display="block"
						// 	position="relative" />
					}
				</div>
				:
				<div style={styledCarImage.twoDContainer}>
					<img style={styledCarImage.twoDContainer.image} src={getImage()} alt="" />

					{!isAnySideDoorOpen() && isAllDoorClosed() &&
						<>
							<div 
								style={styledDoorLock}
								onClick={handleCarLock}
							>
								<img
									style={styledDoorLockImg}
									src={carLocked ? "./touchscreen/car_status/padlock-locked.png" : "./touchscreen/car_status/padlock-unlocked.png"} alt="" 
								/>
							</div>
							<div style={styledDoorLockVL}></div>
						</>
					}

					{!isAnySideDoorOpen() && (!trunkOpen && !chargePortOpen) &&
						<>
							<div 
								style={styledFrunk}
								onClick={handleFrunkOpen}
							>
								<span style={styledNormalLabel}>Frunk</span>
								<span style={styledLargeLabel}>{frunkOpen ? "Close" : "Open"}</span>
							</div>
							<div style={styledFrunkVL}></div>
							<button 
								style={styledFrunkBtn}
								onClick={handleFrunkOpen}
							/>
						</>
					}

					{!isAnySideDoorOpen() && (!frunkOpen && !chargePortOpen) &&
						<>
							<button 
								style={styledTrunk}
								onClick={handleTrunkOpen}
							>
								<span style={styledNormalLabel}>Trunk</span>
								<span style={styledLargeLabel}>{trunkOpen ? "Close" : "Open"}</span>
							</button>
							<div style={styledTrunkVL}></div>
							<button 
								style={styledTrunkBtn}
								onClick={handleTrunkOpen}
							/>
						</>
					}

					{!isAnySideDoorOpen() && (!frunkOpen && !trunkOpen) &&
						<>
							<div 
								style={styledChargingDoor}
								onClick={handleChargeDoorOpen}
							>
								<img 
									style={styledChargingDoorImg}
									src="./touchscreen/car_status/thunder.png" alt="thunder" 
								/>
							</div>
							<div style={styledChargingDoorVL}></div>
							<button 
								style={styledChargingDoorBtn}
								onClick={handleChargeDoorOpen}
							/>
						</>
					}
				</div>
			}

			{
			// gear === "d" &&
			// 	<div style={{
			// 		position: "absolute", zIndex: 2,
			// 		right: 8, top: "50%", width: 40, height: 40,
			// 		display: "flex", justifyContent: "center", alignItems: "center",
			// 		backgroundColor: "gray", opacity: 0.5, borderRadius: 40,
			// 		fontSize: 20, color: "white",
			// 		cursor: "pointer"
			// 	}}
			// 		onClick={() => {
			// 			let newModel = activeThreeJs + 1;
			// 			if (newModel === threeJsModels.length) {
			// 				newModel = 0;
			// 			}
			// 			setActiveThreeJs(newModel);
			// 		}}
			// 	>
			// 		3D
			// 	</div>
			}

			<StatusOverlay
				gear={gear}
				speed={speed}
				distanceToEmpty={distanceToEmpty}
				hazardReq={hazardReq}
				leftTurnOn={leftTurnOn}
				rightTurnOn={rightTurnOn}
			/>

			<IndicatorOverlay
				gear={gear}
				frunkOpen={frunkOpen}
				trunkOpen={trunkOpen}
				chargePortOpen={chargePortOpen}
				autopilotOn={autopilotOn}
			/>

			{
				//<GearBox gear={gear} speed={speed} hazardReq={hazardReq} />
			}
		</div>
	);
}

export default CarStatus;