import { useState, useEffect } from "react";
import styled from "styled-components";


const VolumeControl = ({
	refresh, setRefresh
}) => {

	const [volumeDisplay, setVolumeDisplay] = useState(volumeState.volume);
	const [muteDisplay, setMuteDisplay] = useState(volumeState.mute);

	const [longPressInterval, setLongPressInterval] = useState(null);
	const [longPressOn, setLongPressOn] = useState(false);
	const [volumeVisible, setVolumeVisible] = useState(false);
	
	const [muteInterval, setMuteInterval] = useState(null);

	useEffect(() => {
		if (volumeState.mute) {
			setMediaVolume(0);
		}
		else {
			setMediaVolume(volumeState.volume);
		}

		return () => {
			// Release timers
			if (longPressInterval) {
				clearInterval(longPressInterval);
			}
			if (muteInterval) {
				clearInterval(muteInterval);
			}
		};
	}, 
	// eslint-disable-next-line
	[]);

	useEffect(() => {
		if (refresh) {
			if (volumeState.mute) {
				volumeState.mute = false;
				setMuteDisplay(false);
			}
			setVolumeDisplay(volumeState.volume);
			
			setMediaVolume(volumeState.volume);
			setRefresh(false);
		}
	}, 
	// eslint-disable-next-line
	[refresh]);

	function getNewValue(oldValue, direction, delta = 1) {
		let newValue;
		if (direction === "up") {
			newValue = oldValue + delta;
			if (newValue > 100) {
				newValue = 100;
			}
		}
		else {
			newValue = oldValue - delta;
			if (newValue < 0) {
				newValue = 0;
			}
		}
		return newValue;
	}

	function showsVolume(volume) {
		setVolumeDisplay(volume);
		setVolumeVisible(true);

		if (volumeState.volumeTimerHandle) {
			clearTimeout(volumeState.volumeTimerHandle);
		}
		volumeState.volumeTimerHandle = setTimeout(() => {
			setVolumeVisible(false);
			volumeState.volumeTimerHandle = null;
		}, 3000);
	}

	function handleMouseDown(dir) {
		var longPressTimer = 0;
		const interval = setInterval(() => {
			longPressTimer += 50;
			if (longPressTimer >= 500) {
				setLongPressOn(true);
				const newVal = getNewValue(volumeState.volume, dir);
				setMediaVolume(newVal, true);
				showsVolume(newVal);
			}
		}, 50);
		setLongPressInterval(interval);
	}

	function handleMouseUp(dir) {
		if (longPressInterval) {
			clearInterval(longPressInterval);
			setLongPressInterval(null);
			if (longPressOn) {
				setLongPressOn(false);
				return;
			}
		}
		
		const newVal = getNewValue(volumeState.volume, dir, 5);
		setMediaVolume(newVal, true);
		showsVolume(newVal);
	}

	function handleMuteMouseDown() {
		var longPressTimer = 0;
		const interval = setInterval(() => {
			longPressTimer += 50;
			if (longPressTimer >= 500) {
				if (!volumeState.mute) {
					storeVolume();
				}
				else {
					restoreVolume();
				}

				setMuteInterval(null);
				clearInterval(interval);
			}
		}, 50);

		setMuteInterval(interval);
	}

	function handleMuteMouseUp() {
		if (muteInterval) {
			setMuteInterval(null);
			clearInterval(muteInterval);
		}
	}

	function storeVolume() {
		volumeState.mute = true;
		setMediaVolume(0);
		setMuteDisplay(true);
	}

	function restoreVolume() {
		volumeState.mute = false;
		setMediaVolume(volumeState.volume);
		setMuteDisplay(false);
	}

	return (
		<StyledDiv>
			<button
				onMouseDown={() => handleMouseDown("down")}
				onMouseUp={() => handleMouseUp("down")}
				className="decrease"
			/>

			<div
				onMouseDown={handleMuteMouseDown}
				onMouseUp={handleMuteMouseUp}
				className="counter"
			>
				{volumeVisible ?
					<span>{volumeDisplay}</span>
					:
					<img 
						src={
							(muteDisplay || volumeDisplay === 0) ? 
							"./touchscreen/media/volume-mute.png" : "./touchscreen/media/volume.png"
						}
						alt="volume" 
					/>
				}
			</div>

			<button
				onMouseDown={() => handleMouseDown("up")}
				onMouseUp={() => handleMouseUp("up")}
				className="increase"
			/>
		</StyledDiv>
	);
}

export default VolumeControl;

export function muteMediaVolume(timeout) {
	setMediaVolume(0);
	setTimeout(() => {
		setMediaVolume(volumeState.volume);
	}, timeout);
	return true;
}

const volumeState = {
	volume : 100,
	mute: false,
	volumeTimerHandle: null
};

export function setMediaVolume(volume, store=false) {
	if (store) {
		volumeState.volume = volume;
	}
	let div = document.getElementById("myRadio");
	if (div != null) {
		let video = div.getElementsByTagName("video")[0];
		if (video != null) {
			video.volume = volume/100;
		}
	}
}

const StyledDiv = styled.div`
	color: #ffffff;
	font-size: 32px;
	font-weight: 500;
	
	display: flex;
	justify-content: space-between;
	//gap: 12px;
	flex-direction: row;
	align-items: center;
	width: 156px;

	.counter {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 60px;
		height: 60px;
		cursor: pointer;
		img {
			width: 36px;
			height: 36px;
		}
	}

	button {
		border: 0;
		width: 48px;
		height: 60px;
		background-position: center;
		background-repeat: no-repeat;
		position: center;
		background-repeat: no-repeat;
		cursor: pointer;
	}

	button.increase {
		background: url("./touchscreen/media/right.png");
		background-position: center;
		background-repeat: no-repeat;
		background-size: 24px 24px;
	}

	button.decrease {
		background: url("./touchscreen/media/left.png");
		background-position: center;
		background-repeat: no-repeat;
		background-size: 24px 24px;
	}
`;
