import { setSignal } from "../vplug/WsReq";
import vssApi from "../vplug/VssAPI.json";

import useSound from 'use-sound';
import seatbeltSound from './sounds/seatbelt-102265.mp3';


const Passengers = ({
	row1DriverDoorOpen, row1PassengerDoorOpen, row2DriverDoorOpen, row2PassengerDoorOpen,
	row1DriverSeatOccupied, row1PassengerSeatOccupied, row2DriverSeatOccupied, row2PassengerSeatOccupied,
	row1DriverSeatBelted, row1PassengerSeatBelted, row2DriverSeatBelted, row2PassengerSeatBelted,
	departureReady
}) => {

	const [playSeatbelt] = useSound(seatbeltSound, { interrupt: true });

	const Row1DriverSideSeatType = vssApi.Vehicle.Cabin.Seat.Row1.DriverSide;
	const Row1PassengerSideSeatType = vssApi.Vehicle.Cabin.Seat.Row1.PassengerSide;
	const Row2DriverSideSeatType = vssApi.Vehicle.Cabin.Seat.Row2.DriverSide;
	const Row2PassengerSideSeatType = vssApi.Vehicle.Cabin.Seat.Row2.PassengerSide;

	function isAnySideDoorOpen() {
		return (row1DriverDoorOpen || row1PassengerDoorOpen || row2DriverDoorOpen || row2PassengerDoorOpen);
	}

	function handleGetIn(pos) {
		if (!departureReady) {
			if (isAnySideDoorOpen()) {
				if (pos === "front-left") {
					if (!row1DriverSeatOccupied) {
						setSignal(Row1DriverSideSeatType.IsOccupied.name, "True");
					}
					else {
						setSignal(Row1DriverSideSeatType.IsOccupied.name, "False");
						setSignal(Row1DriverSideSeatType.IsBelted.name, "False");
					}
				}
				else if (pos === "front-right") {
					if (!row1PassengerSeatOccupied) {
						setSignal(Row1PassengerSideSeatType.IsOccupied.name, "True");
					}
					else {
						setSignal(Row1PassengerSideSeatType.IsOccupied.name, "False");
						setSignal(Row1PassengerSideSeatType.IsBelted.name, "False");
					}
				}
				else if (pos === "rear-left") {
					if (!row2DriverSeatOccupied) {
						setSignal(Row2DriverSideSeatType.IsOccupied.name, "True");
					}
					else {
						setSignal(Row2DriverSideSeatType.IsOccupied.name, "False");
						setSignal(Row2DriverSideSeatType.IsBelted.name, "False");
					}
				}
				else if (pos === "rear-right") {
					if (!row2PassengerSeatOccupied) {
						setSignal(Row2PassengerSideSeatType.IsOccupied.name, "True");
					}
					else {
						setSignal(Row2PassengerSideSeatType.IsOccupied.name, "False");
						setSignal(Row2PassengerSideSeatType.IsBelted.name, "False");
					}
				}
			}
		}
		else {
			if (pos === "front-left") {
				if (row1DriverSeatOccupied) {
					if (!row1DriverSeatBelted) {
						setSignal(Row1DriverSideSeatType.IsBelted.name, "True");
					}
					else {
						setSignal(Row1DriverSideSeatType.IsBelted.name, "False");
					}

					playSeatbelt();
				}
			}
			else if (pos === "front-right") {
				if (row1PassengerSeatOccupied) {
					if (!row1PassengerSeatBelted) {
						setSignal(Row1PassengerSideSeatType.IsBelted.name, "True");
					}
					else {
						setSignal(Row1PassengerSideSeatType.IsBelted.name, "False");
					}

					playSeatbelt();
				}
			}
			else if (pos === "rear-left") {
				if (row2DriverSeatOccupied) {
					if (!row2DriverSeatBelted) {
						setSignal(Row2DriverSideSeatType.IsBelted.name, "True");
					}
					else {
						setSignal(Row2DriverSideSeatType.IsBelted.name, "False");
					}

					playSeatbelt();
				}
			}
			else if (pos === "rear-right") {
				if (row2PassengerSeatOccupied) {
					if (!row2PassengerSeatBelted) {
						setSignal(Row2PassengerSideSeatType.IsBelted.name, "True");
					}
					else {
						setSignal(Row2PassengerSideSeatType.IsBelted.name, "False");
					}

					playSeatbelt();
				}
			}
		}
	}

	function getPassengerImage(occupied, belted) {
		if (occupied) {
			if (belted) {
				return "./exterior/person-belted.png";
			}
			else {
				return "./exterior/person.png";
			}
		}
		else {
			return "./exterior/person-absent.png";
		}
	}
	
	return (
		<div 
			style={{
				width: "100%",
				height: "100%",
				display: "flex",
				flexDirection: "row",
				justifyContent: "center",
				alignItems: "center",
				flexWrap: "wrap"
			}}
		>
			<div
				style={{ width: "50%", height: "50%", display: "flex", justifyContent: "center", alignItems: "center" }}
				onClick={() => handleGetIn("front-left")}
			>
				<img src={getPassengerImage(row1DriverSeatOccupied, row1DriverSeatBelted)} style={{ width: 32, height: 32 }} alt="" />
			</div>
			<div
				style={{ width: "50%", height: "50%", display: "flex", justifyContent: "center", alignItems: "center" }}
				onClick={() => handleGetIn("front-right")}
			>
				<img src={getPassengerImage(row1PassengerSeatOccupied, row1PassengerSeatBelted)} style={{ width: 32, height: 32 }} alt="" />
			</div>
			<div
				style={{ width: "50%", height: "50%", display: "flex", justifyContent: "center", alignItems: "center" }}
				onClick={() => handleGetIn("rear-left")}
			>
				<img src={getPassengerImage(row2DriverSeatOccupied, row2DriverSeatBelted)} style={{ width: 32, height: 32 }} alt="" />
			</div>
			<div
				style={{ width: "50%", height: "50%", display: "flex", justifyContent: "center", alignItems: "center" }}
				onClick={() => handleGetIn("rear-right")}
			>
				<img src={getPassengerImage(row2PassengerSeatOccupied, row2PassengerSeatBelted)} style={{ width: 32, height: 32 }} alt="" />
			</div>
		</div>
	);
}

export default Passengers;