import { useEffect, useState } from "react";
import styled from "styled-components";

import { setSignal } from "../../vplug/WsReq";
import vssApi from "../../vplug/VssAPI.json";


const TemperatureControl = ({ signalName, temperature }) => {

	const [longPressInterval, setLongPressInterval] = useState(null);
	const [longPressOn, setLongPressOn] = useState(false);

	const ClimateTemperatureSetType = vssApi.Vehicle.Cabin.HVAC.Station.Row1.Driver.Temperature;

	useEffect(() => {
		return () => {
			// Release timers
			if (longPressInterval != null) {
				clearInterval(longPressInterval);
			}
		};
	}, 
	// eslint-disable-next-line
	[]);

	function getTempLabel() {
		if (temperature >= ClimateTemperatureSetType.allowed.MIN && temperature <= ClimateTemperatureSetType.allowed.MAX) {
			return Number(temperature).toFixed(1).substring(0, 2);
		}
		else if (temperature === ClimateTemperatureSetType.allowed.LO) {
			return "LO";
		}
		else if (temperature === ClimateTemperatureSetType.allowed.HI) {
			return "HI";
		}
		else {
			return "--";
		}
	}

	function getTempFraction() {
		if (temperature >= ClimateTemperatureSetType.allowed.MIN && temperature <= ClimateTemperatureSetType.allowed.MAX) {
			return Number(temperature).toFixed(1).substring(2);
		}
		return null;
	}

	function getNewValue(oldValue, direction) {
		let newValue;
		if (direction === "up") {
			if (oldValue === ClimateTemperatureSetType.allowed.LO) {
				newValue = ClimateTemperatureSetType.allowed.MIN; // LO to MIN
			}
			else if (oldValue === ClimateTemperatureSetType.allowed.HI) {
				newValue = oldValue; // HI to HI
			}
			else if (oldValue >= ClimateTemperatureSetType.allowed.MIN && oldValue < ClimateTemperatureSetType.allowed.MAX) {
				newValue = oldValue + 0.5; // up
			}
			else if (oldValue === ClimateTemperatureSetType.allowed.MAX) {
				newValue = ClimateTemperatureSetType.allowed.HI; // MAX to HI
			}
			else {
				newValue = 25;
			}
		}
		else {
			if (oldValue === ClimateTemperatureSetType.allowed.LO) {
				newValue = oldValue; // LO to LO
			}
			else if (oldValue === ClimateTemperatureSetType.allowed.HI) {
				newValue = ClimateTemperatureSetType.allowed.MAX; // HI to MAX
			}
			else if (oldValue > ClimateTemperatureSetType.allowed.MIN && oldValue <= ClimateTemperatureSetType.allowed.MAX) {
				newValue = oldValue - 0.5; // down
			}
			else if (oldValue === ClimateTemperatureSetType.allowed.MIN) {
				newValue = ClimateTemperatureSetType.allowed.LO; // MIN to LO
			}
			else {
				newValue = 25;
			}
		}
		return newValue;
	}

	function handleDecrement() {
		if (longPressInterval != null) {
			clearInterval(longPressInterval);
			setLongPressInterval(null);
			if (longPressOn) {
				setLongPressOn(false);
				return;
			}
		}
		
		let newVal = getNewValue(temperature, "down");
		setSignal(signalName, newVal);
	}

	function handleIncrement() {
		if (longPressInterval != null) {
			clearInterval(longPressInterval);
			setLongPressInterval(null);
			if (longPressOn) {
				setLongPressOn(false);
				return;
			}
		}
		
		let newVal = getNewValue(temperature, "up");
		setSignal(signalName, newVal);
	}

	function handleDecrementMouseDown() {
		setLongPressOn(false);
		var longPressValue = temperature;
		const interval = setInterval(() => {
			setLongPressOn(true);
			longPressValue = getNewValue(longPressValue, "down");	
			setSignal(signalName, longPressValue);
		}, 250);
		setLongPressInterval(interval);
	}

	function handleIncrementMouseDown() {
		setLongPressOn(false);
		var longPressValue = temperature;
		const interval = setInterval(() => {
			setLongPressOn(true);
			longPressValue = getNewValue(longPressValue, "up");			
			setSignal(signalName, longPressValue);
		}, 250);
		setLongPressInterval(interval);
	}

	return (
		<StyledDiv count={temperature}>
			<button
				onClick={handleDecrement}
				onMouseDown={handleDecrementMouseDown}
				className="decrease"
			></button>

			<div className="counter" id={signalName}>
				<span className="value">{getTempLabel()}</span>
				<span className="fraction">{getTempFraction()}</span>
				{/*(temperature >= 15 && temperature <= 32) ?
				<span className="degree">&deg;</span>
				: null */}
			</div>
			
			<button
				onClick={handleIncrement}
				onMouseDown={handleIncrementMouseDown}
				className="increase"
			></button>
		</StyledDiv>
	);
}

export default TemperatureControl;

const StyledDiv = styled.div`
	color: #ffffff;
	display: flex;
	justify-content: space-between;
	//gap: 12px;
	flex-direction: row;
	align-items: center;
	width: 176px;

	.counter {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 80px;
		height: 60px;
		.value {
			font-size: 36px;
			font-weight: 500;
		}
		.fraction {
			font-size: 28px;
			font-weight: 500;
		}
		.degree {
			font-size: 36px;
			font-weight: 300;
		}
	}

	button {
		border: 0;
		width: 48px;
		height: 60px;
		background-position: center;
		background-repeat: no-repeat;
		position: center;
		background-repeat: no-repeat;
		cursor: pointer;
	}

	button.increase {
		background: url("./touchscreen/climate/right.png");
		background-position: center;
		background-repeat: no-repeat;
		background-size: 24px 24px;
	}

	button.decrease {
		background: url("./touchscreen/climate/left.png");
		background-position: center;
		background-repeat: no-repeat;
		background-size: 24px 24px;
	}
`;
