import { useEffect, useState } from "react";
import Battery from "./Battery";

const StatusOverlay = ({
	gear, speed, distanceToEmpty,
	hazardReq, leftTurnOn, rightTurnOn
}) => {

	const [imageVisible, setImageVisible] = useState(false);
	const [imageInterval, setImageInterval] = useState(false);

	useEffect(() => {
		if (hazardReq || leftTurnOn || rightTurnOn) {
			const interval = setInterval(() => {
				setImageVisible((imageVisible) => !imageVisible);
			}, 250);
			setImageInterval(interval);
		}
		else {
			if (imageInterval) {
				clearInterval(imageInterval);
				setImageInterval(null);
			}
		}
	}, 
	// eslint-disable-next-line
	[hazardReq, leftTurnOn, rightTurnOn]);

    return (
        <>
			<div
				style={{
					position: "absolute", left: 16, top: 0,
					width: 24*4, height: 48,
					display: "flex", flexDirection: "row"
				}}
			>
				<div 
					style={{
						width: "25%", height: "100%",
						display: "flex", justifyContent: "center", alignItems: "center"
					}}
				>
					<div style={{
						color: gear === "p" ? "gray" : "lightgray",
						fontSize: 24, fontWeight: 700
					}}>
						P
					</div>
				</div>
				<div 
					style={{
						width: "25%", height: "100%",
						display: "flex", justifyContent: "center", alignItems: "center"
					}}
				>
					<div style={{
						color: gear === "r" ? "gray" : "lightgray",
						fontSize: 24, fontWeight: 700
					}}>
						R
					</div>
				</div>
				<div 
					style={{
						width: "25%", height: "100%",
						display: "flex", justifyContent: "center", alignItems: "center"
					}}
				>
					<div style={{
						color: gear === "n" ? "gray" : "lightgray",
						fontSize: 24, fontWeight: 700
					}}>
						N
					</div>
				</div>
				<div 
					style={{
						width: "25%", height: "100%",
						display: "flex", justifyContent: "center", alignItems: "center"
					}}
				>
					<div style={{
						color: gear === "d" ? "gray" : "lightgray",
						fontSize: 24, fontWeight: 700
					}}>
						D
					</div>
				</div>
			</div>

			<div
				style={{
					position: "absolute", left: "40%", top: 0,
					width: 48*2, height: 48,
					display: "flex", flexDirection: "row"
				}}
			>
				<div 
					style={{
						width: "50%", height: "100%",
						display: "flex", justifyContent: "center", alignItems: "center"
					}}
				>
					{(leftTurnOn || hazardReq) && imageVisible &&
                        <img 
							src={"./tesla/icons/turn-left-active.png"} 
							style={{ width: 32, height: 32 }} 
							alt=""
						/>
                    }
				</div>
				<div 
					style={{
						width: "50%", height: "100%",
						display: "flex", justifyContent: "center", alignItems: "center"
					}}
				>
					{(rightTurnOn || hazardReq) && imageVisible &&
                        <img 
							src={"./tesla/icons/turn-right-active.png"} 
							style={{ width: 32, height: 32 }} 
							alt=""
						/>
                    }
				</div>
			</div>

			<div style={{
				position: "absolute", right: 8, top: 0,
				width: 48*3, height: 48,
				display: "flex", flexDirection: "row"
			}}>
				<div 
					style={{
						width: 48*2, height: 48,
						display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "flex-end"
					}}
				>
					<div style={{
						color: "#707070",
						fontSize: 16,
						fontWeight: 700
					}}>
						{Number(distanceToEmpty/1000).toFixed(0)} km
					</div>
				</div>
				<div 
					style={{
						width: 48, height: 48,
						display: "flex", justifyContent: "center", alignItems: "center"
					}}
				>
					<Battery distanceToEmpty={distanceToEmpty} size={32} />
				</div>
            </div>

			<div style={{
				position: "absolute", top: 48,
				width: "100%", height: 2,
				display: "flex", flexDirection: "row",
				paddingLeft: 16, paddingRight: 16
			}}>
				<div style={{
					width: "100%", height: "100%", backgroundColor: "lightgray"
				}}>
				</div>
			</div>
			{gear === "d" &&
			<div
				style={{
					position: "absolute", left: 16, top: 48,
					width: 128, height: 96,
					display: "flex", flexDirection: "column"
				}}
			>
				<div 
					style={{
						width: "100%", height: "75%",
						display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "flex-start"
					}}
				>
					<div style={{
						color: "gray", fontSize: 64, fontWeight: 300
					}}>
						{speed}
					</div>
				</div>
				<div 
					style={{
						width: "100%", height: "25%",
						display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "flex-start"
					}}
				>
					<div style={{
						color: "gray", fontSize: 16, fontWeight: 700
					}}>
						km/h
					</div>
				</div>
			</div>
			}
		</>
    );
}

export default StatusOverlay;