import { useEffect } from "react";

import DoorSwitch from "./DoorSwitch";
import DoorWindow from "./DoorWindow";
import PressButton from "./PressButton";
import LongPressButton from "./LongPressButton";

import { SIGNAL_TYPES, setSignal, setSignals } from "../vplug/WsReq";
import vssApi from "../vplug/VssAPI.json";

import { MyTooltip, MyTooltip2 } from "./CarInterior";


const Cabin = ({
    IMAGE_HEIGHT, IMAGE_WIDTH,
	touchscreenOpen, setTouchscreenOpen,

    autopilotOn, setAutopilotOn, distanceToEmpty,
	speed, setSpeed,
    heading, setHeading,
    destination,
    gear,
    carLocked,
    row1DriverDoorOpen, setRow1DriverDoorOpen,
    row1PassengerDoorOpen, row2DriverDoorOpen, row2PassengerDoorOpen,
    row1DriverSeatOccupied,
    leftChildLock, rightChildLock,
    setDepartureReady,

    frontLeftWindowPos, setFrontLeftWindowPos,
	frontRightWindowPos, setFrontRightWindowPos,
	rearLeftWindowPos, setRearLeftWindowPos,
	rearRightWindowPos, setRearRightWindowPos,
	windowChildLock,
	hornReq, setHornReq,
	hazardReq
}) => {

	const SelectedGearType = vssApi.Vehicle.Powertrain.Transmission.SelectedGear;

    const Row1DriverSideDoorType = vssApi.Vehicle.Cabin.Door.Row1.DriverSide;
	const Row1PassengerSideDoorType = vssApi.Vehicle.Cabin.Door.Row1.PassengerSide;
	const Row2DriverSideDoorType = vssApi.Vehicle.Cabin.Door.Row2.DriverSide;
	const Row2PassengerSideDoorType = vssApi.Vehicle.Cabin.Door.Row2.PassengerSide;

	const TrunkFrontType = vssApi.Vehicle.Body.Trunk.Front;
	const TrunkRearType = vssApi.Vehicle.Body.Trunk.Rear;

	useEffect(() => {
		if (speed >= 20) {
			setSignals({
				signals: [
					{ name: Row1DriverSideDoorType.IsLocked.name, value: "True" },
					{ name: Row1PassengerSideDoorType.IsLocked.name, value: "True" },
					{ name: Row2DriverSideDoorType.IsLocked.name, value: "True" },
					{ name: Row2PassengerSideDoorType.IsLocked.name, value: "True" },
					{ name: TrunkFrontType.IsLocked.name, value: "True" },
					{ name: TrunkRearType.IsLocked.name, value: "True" }
				],
				type: SIGNAL_TYPES.VSS
			});
		}
	},
	// eslint-disable-next-line
	[speed]);

	useEffect(() => {
		if (gear === "p" && row1DriverSeatOccupied && carLocked) {
			setSignal(Row1DriverSideDoorType.IsLocked.name, "False");
		}
	},
	// eslint-disable-next-line
	[gear]);

    function handleGearP() {
		if (gear !== "p" && speed === 0 && !autopilotOn) {
			setSignals({
				signals: [
					{
						name: SelectedGearType.name,
						value: SelectedGearType.allowed.Park
					},
					{
						name: vssApi.Vehicle.Speed.name,
						value: 0
					}
				],
				type: SIGNAL_TYPES.VSS
			});
		}
	}

	function handleGearR() {
		if (gear !== "r" && speed === 0 && !autopilotOn) {
			setSignal(SelectedGearType.name, SelectedGearType.allowed.Reverse);
		}
	}

	function handleGearN() {
		if (gear !== "n" && speed === 0 && !autopilotOn) {
			setSignal(SelectedGearType.name, SelectedGearType.allowed.Neutral);
		}
	}

	function handleGearD() {
		if (gear !== "d" && speed === 0) {
			setSignal(SelectedGearType.name, SelectedGearType.allowed.Drive);
		}
	}

	function autoSteer() {
		if (destination && destination.length >= 3 && distanceToEmpty > 0) {
			setAutopilotOn(!autopilotOn);
		}
	}

	function handleHazardReq() {
		if (hazardReq) {
			setSignal(vssApi.Vehicle.Body.Lights.Hazard.IsSignaling.name, "False");
		}
		else {
			setSignal(vssApi.Vehicle.Body.Lights.Hazard.IsSignaling.name, "True");
		}
	}

	const TOUCHSCREEN_X_POS = IMAGE_WIDTH * 0.48;
	const TOUCHSCREEN_Y_POS = IMAGE_HEIGHT * 0.68;

    const STEER_LEFT_X_POS = IMAGE_WIDTH * 0.19;
	const STEER_AUTO_X_POS = IMAGE_WIDTH * 0.24;
	const STEER_RIGHT_X_POS = IMAGE_WIDTH * 0.29;
	const STEER_Y_POS = IMAGE_HEIGHT * 0.42;

	const HORN_X_POS = IMAGE_WIDTH * 0.24;
	const HORN_Y_POS = IMAGE_HEIGHT * 0.60;

	const ACCEL_X_POS = IMAGE_WIDTH * 0.34;
	const BRAKE_X_POS = IMAGE_WIDTH * 0.40;
	const BRAKE_Y_POS = IMAGE_HEIGHT * 0.25;

	const GEAR_X_POS = IMAGE_WIDTH * 0.46;
	const GEAR_Y_POS = IMAGE_HEIGHT * 0.25;

	const FRONT_DOOR_Y_POS = IMAGE_HEIGHT * 0.50;
	const REAR_DOOR_Y_POS = 8;

    function handleFrontLeftDoorOpen() {
		if (row1DriverDoorOpen) {
			setSignal(Row1DriverSideDoorType.IsOpen.name, "False");
		}
		else {
			if (speed === 0 && gear !== "d" && gear !== "r") {
				setSignal(Row1DriverSideDoorType.IsLocked.name, "False");
				setSignal(Row1DriverSideDoorType.IsOpen.name, "True");
				setDepartureReady(false);
				setRow1DriverDoorOpen(true);
			}
		}
	}

	function handleFrontRightDoorOpen() {
		if (row1PassengerDoorOpen) {
			setSignal(Row1PassengerSideDoorType.IsOpen.name, "False");
		}
		else {
			if (speed === 0) {
				setSignal(Row1PassengerSideDoorType.IsLocked.name, "False");
				setSignal(Row1PassengerSideDoorType.IsOpen.name, "True");
			}
		}
	}

	function handleRearLeftDoorOpen() {
		if (row2DriverDoorOpen) {
			setSignal(Row2DriverSideDoorType.IsOpen.name, "False");
		}
		else {
			if (speed === 0 && !leftChildLock) {
				setSignal(Row2DriverSideDoorType.IsLocked.name, "False");
				setSignal(Row2DriverSideDoorType.IsOpen.name, "True");
			}
		}
	}

	function handleRearRightDoorOpen() {
		if (row2PassengerDoorOpen) {
			setSignal(Row2PassengerSideDoorType.IsOpen.name, "False");
		}
		else {
			if (speed === 0 && !rightChildLock) {
				setSignal(Row2PassengerSideDoorType.IsLocked.name, "False");
				setSignal(Row2PassengerSideDoorType.IsOpen.name, "True");
			}
		}
	}

	function getNewSpeed(oldVal, dir) {
		if (distanceToEmpty === 0) {
			return 0;
		}

		let delta = 5;
		if (dir === "up") {
			if (oldVal < 20) {
				delta = 2;
			}
			else if (oldVal < 50) {
				delta = 5;
			}
			else if (oldVal < 100) {
				delta = 10;
			}
			else {
				delta = 20;
			}
		}
		else {
			if (oldVal <= 24) {
				delta = 4;
			}
			else if (oldVal <= 60) {
				delta = 10;
			}
			else if (oldVal <= 120) {
				delta = 20;
			}
			else {
				delta = 40;
			}
		}

		let newVal = oldVal;
		if (dir === "down") {
			newVal -= delta;
			if (newVal < 0) {
				newVal = 0;
			}
		}
		else {
			newVal += delta;
			if (newVal > 500) {
				newVal = 500;
			}
		}

		return newVal;
	}

	const isMediumWindow = (window.innerWidth >= 1024 && window.innerHeight >= 768);

    return (
        <div
            style={{ position: "relative", height: IMAGE_HEIGHT, width: IMAGE_WIDTH }}
		>
            <img src="./tesla/6-Tesla-Model-S.webp" style={{ height: IMAGE_HEIGHT, width: IMAGE_WIDTH }} alt="" />

			{isMediumWindow &&
			<>

			<div style={{ position: "absolute", left: TOUCHSCREEN_X_POS, bottom: TOUCHSCREEN_Y_POS }} id="touchscreen">
				<PressButton
					isActive={() => (touchscreenOpen)}
					onClick={() => setTouchscreenOpen(true)}
					activeImage={"./touchscreen/minimize.png"}
					inactiveImage={"./touchscreen/maximize.png"}
					IMAGE_WIDTH={40}
					IMAGE_HEIGHT={40}
					BORDER_WIDTH={0}
					backgroundColor={"black"}
				/>
			</div>
			
			<div style={{ position: "absolute", left: STEER_LEFT_X_POS, bottom: STEER_Y_POS }} id="left-steer">
				<LongPressButton
					signalValue={heading}
					getNewValue={(oldVal) => {
						let newVal = oldVal;
						if (gear !== "r") {
							newVal -= 2;
							if (newVal < 0) {
								newVal += 360;
							}
						}
						else {
							newVal += 2;
							if (newVal > 360) {
								newVal -= 360;
							}
						}
						return newVal;
					}}
					setSignalValue={setHeading}
					signalName={vssApi.Vehicle.CurrentLocation.Heading.name}
					enabled={!autopilotOn}
					activeImage={"./interior/left-active.png"}
					inactiveImage={"./interior/left.png"}
					IMAGE_WIDTH={40}
					IMAGE_HEIGHT={40}
				/>
			</div>
			<div style={{ position: "absolute", left: STEER_AUTO_X_POS, bottom: STEER_Y_POS }} id="autopilot">
				<PressButton
				    isActive={() => (autopilotOn)}
					onClick={autoSteer}
					activeImage={"./interior/steering-wheel-active.png"}
					inactiveImage={"./interior/steering-wheel.png"}
					IMAGE_WIDTH={48}
					IMAGE_HEIGHT={48}
					BORDER_WIDTH={0}
				/>
			</div>
			<div style={{ position: "absolute", left: STEER_RIGHT_X_POS, bottom: STEER_Y_POS }} id="right-steer">
				<LongPressButton
					signalValue={heading}
					getNewValue={(oldVal) => {
						let newVal = oldVal;
						if (gear !== "r") {
							newVal += 2;
							if (newVal > 360) {
								newVal -= 360;
							}
						}
						else {
							newVal -= 2;
							if (newVal < 0) {
								newVal += 360;
							}
						}
						return newVal;
					}}
					setSignalValue={setHeading}
					signalName={vssApi.Vehicle.CurrentLocation.Heading.name}
					enabled={!autopilotOn}
					activeImage={"./interior/right-active.png"}
					inactiveImage={"./interior/right.png"}
					IMAGE_WIDTH={40}
					IMAGE_HEIGHT={40}
				/>
			</div>

            <div style={{ position: "absolute", left: HORN_X_POS, bottom: HORN_Y_POS }} id="horn">
				<PressButton
				    isActive={() => (hornReq)}
					onClick={() => {
                        //
                    }}
                    onMouseDown={() => {
                        setSignal(vssApi.Vehicle.Body.Horn.IsActive.name, "True");
                        setHornReq(true);
                    }}
                    onMouseUp={() => {
                        setSignal(vssApi.Vehicle.Body.Horn.IsActive.name, "False");
                        setHornReq(false);
                    }}
					activeImage={"./interior/horn-active.png"}
					inactiveImage={"./interior/horn.png"}
					IMAGE_WIDTH={36}
					IMAGE_HEIGHT={36}
				/>
            </div>

			<div style={{ position: "absolute", left: ACCEL_X_POS, bottom: BRAKE_Y_POS }} id="brake">
				<LongPressButton
					signalValue={speed}
					getNewValue={(oldVal) => getNewSpeed(oldVal, "down")}
					setSignalValue={setSpeed}
					signalName={vssApi.Vehicle.Speed.name}
					enabled={true}
					activeImage={"./interior/down-active.png"}
					inactiveImage={"./interior/down.png"}
					IMAGE_WIDTH={44}
					IMAGE_HEIGHT={44}
				/>
            </div>
            <div style={{ position: "absolute", left: BRAKE_X_POS, bottom: BRAKE_Y_POS }} id="accel">
				<LongPressButton
					signalValue={speed}
					getNewValue={(oldVal) => getNewSpeed(oldVal, "up")}
					setSignalValue={setSpeed}
					signalName={vssApi.Vehicle.Speed.name}
					enabled={gear === "d" || gear === "r"}
					activeImage={"./interior/up-active.png"}
					inactiveImage={"./interior/up.png"}
					IMAGE_WIDTH={44}
					IMAGE_HEIGHT={44}
				/>
            </div>

            <div style={{ position: "absolute", left: GEAR_X_POS, bottom: GEAR_Y_POS, display: "flex", flexDirection: "row", gap: 8 }}>
				<div id="gear-p">
					<PressButton
						isActive={() => (gear === "p")}
						onClick={handleGearP}
						label={"P"}
					/>
				</div>
				<div id="gear-r">
					<PressButton
						isActive={() => (gear === "r")}
						onClick={handleGearR}
						label={"R"}
					/>
				</div>
				<div id="hazard">
					<PressButton
						isActive={() => (hazardReq)}
						onClick={handleHazardReq}
						activeImage={"./interior/hazard-active.webp"}
						inactiveImage={"./interior/hazard.webp"}
						IMAGE_WIDTH={48}
						IMAGE_HEIGHT={48}
						BORDER_WIDTH={0}
						animated={true}
					/>
				</div>
				<div id="gear-n">
					<PressButton
						isActive={() => (gear === "n")}
						onClick={handleGearN}
						label={"N"}
					/>
				</div>
				<div id="gear-d">
					<PressButton
						isActive={() => (gear === "d")}
						onClick={handleGearD}
						label={"D"}
					/>
				</div>
            </div>

            <div style={{ 
				position: "absolute", left: 8, bottom: FRONT_DOOR_Y_POS,
				display: "flex", gap: 8,
				width: (32 * 3 + 8 * 2), height: (46 * 2 + 8)
			}}>
				<div style={{ display: "flex", flexDirection: "column", gap: 8 }}>
					<div style={{ width: 32, height: 46 }}/>
					<div className="door-switch">
						<DoorSwitch
							doorOpen={row1DriverDoorOpen} pressSwitch={handleFrontLeftDoorOpen}
							doorOpenImage={"./tesla/tesla-door-left-active.webp"} doorClosedImage={"./tesla/tesla-door-left.webp"}
						/>
					</div>
				</div>
				<div style={{ display: "flex", flexWrap: "wrap", gap: 8 }} className="door-window">
					<DoorWindow
						windowName={Row1DriverSideDoorType.Window.Position.name}
						windowPos={frontLeftWindowPos}
						setWindowPos={setFrontLeftWindowPos}
					/>
					<DoorWindow
						windowName={Row1PassengerSideDoorType.Window.Position.name}
						windowPos={frontRightWindowPos}
						setWindowPos={setFrontRightWindowPos}
					/>
					<DoorWindow
						windowName={Row2DriverSideDoorType.Window.Position.name}
						windowPos={rearLeftWindowPos}
						setWindowPos={setRearLeftWindowPos}
					/>
					<DoorWindow
						windowName={Row2PassengerSideDoorType.Window.Position.name}
						windowPos={rearRightWindowPos}
						setWindowPos={setRearRightWindowPos}
					/>
				</div>
            </div>
            <div style={{ position: "absolute", right: 8, bottom: FRONT_DOOR_Y_POS, display: "flex", gap: 8 }}>
				<div className="door-window">
					<DoorWindow
						windowName={Row1PassengerSideDoorType.Window.Position.name}
						windowPos={frontRightWindowPos}
						setWindowPos={setFrontRightWindowPos}
					/>
				</div>
				<div className="door-switch">
					<DoorSwitch
						doorOpen={row1PassengerDoorOpen} pressSwitch={handleFrontRightDoorOpen}
						doorOpenImage={"./tesla/tesla-door-right-active.webp"} doorClosedImage={"./tesla/tesla-door-right.webp"}
					/>
				</div>
            </div>
			<div style={{ position: "absolute", left: 8, bottom: REAR_DOOR_Y_POS, display: "flex", gap: 8 }}>
				<div className="door-switch">
					<DoorSwitch
						doorOpen={row2DriverDoorOpen} pressSwitch={handleRearLeftDoorOpen}
						doorOpenImage={"./tesla/tesla-door-left-active.webp"} doorClosedImage={"./tesla/tesla-door-left.webp"}
					/>
				</div>
				<div className="door-window">
					<DoorWindow
						windowName={Row2DriverSideDoorType.Window.Position.name}
						windowPos={rearLeftWindowPos}
						setWindowPos={setRearLeftWindowPos}
						windowChildLock={windowChildLock}
					/>
				</div>
            </div>
            <div style={{ position: "absolute", right: 8, bottom: REAR_DOOR_Y_POS, display: "flex", gap: 8 }}>
				
				<div className="door-window">
					<DoorWindow
						windowName={Row2PassengerSideDoorType.Window.Position.name}
						windowPos={rearRightWindowPos}
						setWindowPos={setRearRightWindowPos}
						windowChildLock={windowChildLock}
					/>
				</div>
				
				<div className="door-switch">
					<DoorSwitch
						doorOpen={row2PassengerDoorOpen} pressSwitch={handleRearRightDoorOpen}
						doorOpenImage={"./tesla/tesla-door-right-active.webp"} doorClosedImage={"./tesla/tesla-door-right.webp"}
					/>
				</div>
            </div>

			<MyTooltip id="left-steer" label="Left Steering"/>
			<MyTooltip id="autopilot" label="Activate Autopilot"/>
			<MyTooltip id="right-steer" label="Right Steering"/>
			<MyTooltip id="horn" label="Horn"/>

			<MyTooltip id="brake" label="Brake Pedal"/>
			<MyTooltip id="accel" label="Accelerator Pedal"/>

			<MyTooltip id="gear-p" label="Parking Gear" />
			<MyTooltip id="gear-r" label="Reverse Gear" />
			<MyTooltip id="hazard" label="Hazard Light" />
			<MyTooltip id="gear-n" label="Neutral Gear" />
			<MyTooltip id="gear-d" label="Drive Gear" />
			
			<MyTooltip2 id="door-switch" label="Open/Close Door" />
			<MyTooltip2 id="door-window" label="Open/Close Window" />

			<MyTooltip id="touchscreen" label="Open Touchscreen" />

			</>
			}
        </div>
    );
}

export default Cabin;