import { useEffect, useState } from "react";
import { setSignal } from "../vplug/WsReq";
import vssApi from "../vplug/VssAPI.json";

const ExteriorConditions = ({
	airTemperature, humidity, lightIntensity, rainIntensity
}) => {

	const styles = {
		exteriorConditions: {
			display: "flex",
			flexDirection: "row",
			justifyContent: "center",
			alignItems: "center",
			//padding: 8,
			gap: 16,
			color: "#707070"
		}
	};
	
	return (
		<div style={styles.exteriorConditions}>
			<EnvironmentValue
				signal={airTemperature}
				signalName={vssApi.Vehicle.Exterior.AirTemperature.name}
				title={"Air Temperature"}
				unit={<> &#8451;</>}
			/>
			<EnvironmentValue
				signal={humidity}
				signalName={vssApi.Vehicle.Exterior.Humidity.name}
				title={"Humidity"}
				unit={"%"}
			/>
			<EnvironmentValue
				signal={lightIntensity}
				signalName={vssApi.Vehicle.Exterior.LightIntensity.name}
				title={"Light Intensity"}
				unit={"%"}
			/>
			<EnvironmentValue
				signal={rainIntensity}
				signalName={vssApi.Vehicle.Body.Raindetection.Intensity.name}
				title={"Rain Intensity"}
				unit={"%"}
			/>
		</div>
	);
}

const EnvironmentValue = ({
	signalName, signal, title, unit
}) => {

	const DELTA = 1;
    const [longPressInterval, setLongPressInterval] = useState(null);
	const [longPressOn, setLongPressOn] = useState(false);

	useEffect(() => {
		return () => {
			// Release timers
			if (longPressInterval != null) {
				clearInterval(longPressInterval);
			}
		};
	}, 
	// eslint-disable-next-line
	[]);

	function getNewValue(oldValue, direction) {
		let newValue = oldValue;
		if (direction === "up") {
			newValue += DELTA;
            if (newValue > 100) {
                newValue = 100;
            }
		}
		else {
			newValue -= DELTA;
            if (newValue < 0) {
                newValue = 0;
            }
		}
		return newValue;
	}

    function setNewValue(newVal) {
        setSignal(signalName, newVal);
    }

	function handleDecrement() {
		if (longPressInterval != null) {
			clearInterval(longPressInterval);
			setLongPressInterval(null);
			if (longPressOn) {
				setLongPressOn(false);
				return;
			}
		}
		
		let newVal = getNewValue(signal, "down");
		setNewValue(newVal);
	}

	function handleIncrement() {
		if (longPressInterval != null) {
			clearInterval(longPressInterval);
			setLongPressInterval(null);
			if (longPressOn) {
				setLongPressOn(false);
				return;
			}
		}
		
		let newVal = getNewValue(signal, "up");
		setNewValue(newVal);
	}

	function handleDecrementMouseDown() {
		setLongPressOn(false);
		var newVal = signal;
		const interval = setInterval(() => {
			setLongPressOn(true);
			newVal = getNewValue(newVal, "down");	
			setNewValue(newVal);
		}, 250);
		setLongPressInterval(interval);
	}

	function handleIncrementMouseDown() {
		setLongPressOn(false);
		var newVal = signal;
		const interval = setInterval(() => {
			setLongPressOn(true);
			newVal = getNewValue(newVal, "up");			
			setNewValue(newVal);
		}, 250);
		setLongPressInterval(interval);
	}

	return (
		<div style={{
			display: "flex",
			flexDirection: "column",
			justifyContent: "center",
			alignItems: "center",
			width: 120,
			height: 64
		}}>
			<div style={{ height: "50%", width: "100%", display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "flex-start" }}>
				<div style={{ fontSize: 14, fontWeight: 700, color: "#6F6F6F" }}>
					{title}
				</div>
			</div>
			<div
				style={{
					height: "50%", width: "100%", 
					display: "flex", flexDirection: "row", justifyContent: "center", alignItems: "center",
					backgroundColor: "lightgray", borderRadius: 8
				}}
			>
				<div
					onClick={handleDecrement}
					onMouseDown={handleDecrementMouseDown}
					style={{
						width: "25%", display: "flex", justifyContent: "center", alignItems: "center", cursor: "pointer"
					}}
				>
					<div style={{ fontSize: 16, fontWeight: 900 }}>{"\u2212"}</div>
				</div>
				<div
					style={{
						width: "50%", display: "flex", justifyContent: "center", alignItems: "center"
					}}
				>
					<div style={{ fontSize: 16, fontWeight: 700 }}>{signal}{unit}</div>
				</div>
				<div
					onClick={handleIncrement}
					onMouseDown={handleIncrementMouseDown}
					style={{
						width: "25%", display: "flex", justifyContent: "center", alignItems: "center", cursor: "pointer"
					}}
				>
					<div style={{ fontSize: 16, fontWeight: 900 }}>{"\u002B"}</div>
				</div>
			</div>
		</div>
	);
}

export default ExteriorConditions;