import React from "react";
import styled from "styled-components";

import Webcam from "react-webcam";
import WebRTC from "./WebRTC";

const Dashcam = ({
    active, APP_HEIGHT
}) => {

    const videoConstraints = {
        width: 1024,
        height: 768,
        facingMode: "user"
    };

    const [localStream, setLocalStream] = React.useState(null);
    const [remoteStream, setRemoteStream] = React.useState(null);

    return(
        <StyledDiv APP_HEIGHT={APP_HEIGHT}>
            {active &&
            <>
                <Webcam
                    audio={true}
                    screenshotFormat="image/png"
                    videoConstraints={videoConstraints}
                    imageSmoothing={true}
                    //mirrored={true}
                    controls={false}
                    //width={1024}
                    //height={768}
                    onUserMedia={(stream) => {setLocalStream(stream)}}
                />
                {localStream &&
                <WebRTC localStream={localStream} setLocalStream={setLocalStream} remoteStream={remoteStream} setRemoteStream={setRemoteStream} />
                }
            </>
            }
        </StyledDiv>
    );
}

export default Dashcam;

const StyledDiv = styled.div`
    background: #eeeeee;
    color: #6f6f6f;
    height: ${(props) => (props.APP_HEIGHT)}px;
    width: 100%;
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
`;
