import { useEffect, useState } from "react";
import { setSignal } from "../vplug/WsReq";

const LongPressButton = ({
    signalValue,
	getNewValue,
	setSignalValue = null,
	signalName = null,
	enabled = true,
	activeImage,
	inactiveImage,
	IMAGE_WIDTH, IMAGE_HEIGHT
}) => {

	const [buttonPressed, setButtonPressed] = useState(false);
    const [longPressInterval, setLongPressInterval] = useState(null);
	const [longPressOn, setLongPressOn] = useState(false);

	useEffect(() => {
		return () => {
			// Release timers
			if (longPressInterval != null) {
				clearInterval(longPressInterval);
			}
		};
	}, 
	// eslint-disable-next-line
	[]);

	function handlePressOut() {
		if (longPressInterval) {
			clearInterval(longPressInterval);
			setLongPressInterval(null);
			if (longPressOn) {
				setLongPressOn(false);
				return;
			}
		}
		
		const newVal = getNewValue(signalValue);
		if (signalName) {
			setSignal(signalName, newVal);
		}
		if (setSignalValue) {
			setSignalValue(newVal);
		}
	}

	function handlePressIn() {
		setLongPressOn(false);
		var newVal = signalValue;
		const interval = setInterval(() => {
			setLongPressOn(true);
			newVal = getNewValue(newVal);
			if (signalName) {
				setSignal(signalName, newVal);
			}
			if (setSignalValue) {
				setSignalValue(newVal);
			}
		}, 100);
		setLongPressInterval(interval);
	}

    return (
		<div 
			style={{ 
				width: 48, height: 48, cursor: "pointer", 
				display: "flex", justifyContent: "center", alignItems: "center",
				borderWidth: buttonPressed ? 2 : 0, 
				borderStyle: "solid", 
				borderColor: buttonPressed ? "#fbb03b" : "white"
			}}
			// onClick={() => {
			// }}
			onMouseDown={() => {
				setButtonPressed(true);
				if (enabled) {
					handlePressIn();
				}
			}}
			onMouseUp={() => {
				setButtonPressed(false);
				if (enabled) {
					handlePressOut();
				}
			}}
		>
			<img 
				src={buttonPressed ? activeImage : inactiveImage} 
				style={{ width: IMAGE_WIDTH, height: IMAGE_HEIGHT }} alt=""
			/>
		</div>
    );
}

export default LongPressButton;