import { useEffect, useState } from "react";
import styled from "styled-components";
import SelectionControl from "./components/SelectControl";
import SwitchControl from "./components/SwitchControl";

import { setValue, getValue } from "./CarControlSettings";


const NavigationControl = () => {
	const [mapType, setMapType] = useState(getValue("mapType") ? getValue("mapType") : "Custom");

	useEffect(() => {
		setValue("mapType", mapType);
	}, [mapType]);

	const [navigationVolume, setNavigationVolume] = useState(16);

	const [automaticNavigation, setAutomaticNavigation] = useState(false);
	const [tripPlanner, setTripPlanner] = useState(true);
	const [onlineRouting, setOnlineRouting] = useState(true);

	const [avoidFerries, setAvoidFerries] = useState(false);
	const [avoidTolls, setAvoidTolls] = useState(false);
	const [useHOVLanes, setUseHOVLanes] = useState(false);

	return (
		<StyledDiv>
			<SelectionControl
				name="Map Type"
				valueList={["Kakao", "Custom"]}
				value={mapType}
				setValue={setMapType}
			/>

			<div style={{ height: 48, display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "flex-start" }}>
				<div style={{ fontSize: 18, fontWeight: 700, color: "#6F6F6F" }}>
					{"Navigation Volume"}
				</div>
			</div>
			<div
				style={{ 
					display: "flex", flexDirection: "row", 
					width: 250, height: 64, justifyContent: "center", alignItems: "center",
					//borderWidth: 2, borderRadius: 8, borderColor: "darkgray", borderStyle: "solid"
					backgroundColor: "lightgray", borderRadius: 8
				}}
			>
				<div 
					onClick={() => {
						if (navigationVolume > 0) {
							setNavigationVolume(navigationVolume - 1);
						}
					}}
					style={{
						width: 75, height: 64, display: "flex", justifyContent: "center", alignItems: "center",
					}}
				>
					<div style={{ fontSize: 28, fontWeight: 900 }}>{"\u2212"}</div>
				</div>
				<div
					style={{
						width: 100, height: 64, display: "flex", justifyContent: "center", alignItems: "center"
					}}
				>
					<div style={{ fontSize: 24, fontWeight: 700 }}>{navigationVolume}</div>
				</div>
				<div 
					onClick={() => {
						if (navigationVolume < 100) {
							setNavigationVolume(navigationVolume + 1);
						}
					}}
					style={{
						width: 75, height: 64, display: "flex", justifyContent: "center", alignItems: "center",
					}}
				>
					<div style={{ fontSize: 28, fontWeight: 900 }}>{"\u002B"}</div>
				</div>
			</div>

			<SwitchControl
				name="Automatic Navigation"
				value={automaticNavigation}
				setValue={setAutomaticNavigation}
				desc={["Automatically route to Home, Work or next calendar event upon entry."]}
			/>
			<SwitchControl
				name="Trip Planner"
				value={tripPlanner}
				setValue={setTripPlanner}
				desc={["Adds Supercharger stops if necessary."]}
			/>
			<SwitchControl
				name="Online Routing"
				value={onlineRouting}
				setValue={setOnlineRouting}
				desc={["Finds optimal route based on traffic conditions."]}
			/>

			<SwitchControl
				name="Avoid Ferries"
				value={avoidFerries}
				setValue={setAvoidFerries}
			/>
			<SwitchControl
				name="Avoid Tolls"
				value={avoidTolls}
				setValue={setAvoidTolls}
			/>
			<SwitchControl
				name="Use HOV Lanes"
				value={useHOVLanes}
				setValue={setUseHOVLanes}
			/>
		</StyledDiv>
	);
}

export default NavigationControl;

const StyledDiv = styled.div`
	width: 100%;
	height: 100%;
	background-color: #f0f0f0;
	padding: 32px;
	position: relative;
	color: #6F6F6F;
`;
