import SocketIOClient from 'socket.io-client';

const IO_PORT = process.env.REACT_APP_IO_PORT;
const HTTP_PORT = process.env.REACT_APP_HTTP_PORT;

const serverInfo = {
    SERVER_IP   : "localhost",
    socket      : SocketIOClient("http://localhost:" + IO_PORT),
    serverFound : false
};

export const SIGNAL_TYPES = {
    RAW: "raw", // CAN
    VSS: "vss", // COVESA Vehicle Signal Spec
    EXT: "ext"  // Extended
};

const MY_MACHINE = "vehicle-simulator";
const watchdogChannel = {
    intervalHandle: null,
    initSignals: null,
    heartbeat: 0,
    timeoutCounter: 5,
    lastTime: -1
};
const SUBSCRIPTION_DELAY = 500;
const channelList = new Map();

export const setSignal = (signalName, physicalValue, type = SIGNAL_TYPES.VSS, callback = null) => {
    const jsonData = {
        signals: [{ name: signalName, value: physicalValue }],
        type: type
    };
    setSignals(jsonData, callback);
}

export const getSignal = (signalName, type, callback) => {
    const jsonData = {
        signals: [{ name: signalName }],
        type: type
    };
    getSignals(jsonData, (json) => {
        if (json.signals != null && json.signals.length > 0) {
            if (callback != null) {
                callback(json.signals[0].value);
            }
        }
    });
}

export const getSignals = (jsonData, callback = null) => {
    if (serverInfo.serverFound) {
        serverInfo.socket.emit("get", jsonData);
        serverInfo.socket.off("get");
        serverInfo.socket.on("get", (msg) => {
            serverInfo.socket.off("get");
            if (callback) {
                callback(msg);
            }
        });
    }
}

export const setSignals = (jsonData, callback = null) => {
    if (serverInfo.serverFound) {
        serverInfo.socket.emit("set", jsonData);
        serverInfo.socket.off("set");
        serverInfo.socket.on("set", (msg) => {
            serverInfo.socket.off("set");
            if (callback) {
                callback(msg);
            }
        });
    }
}

export const subscribeSignals = (jsonData, callback) => {
    if (!jsonData.client) {
        jsonData.client = MY_MACHINE;
        jsonData.channel = MY_MACHINE + "/" + jsonData.channel;
    }
    channelList.set(jsonData.channel, {
        channel: jsonData.channel,
        jsonData: jsonData,
        callback: callback
    });
    if (serverInfo.serverFound) {
        serverInfo.socket.emit("subscribe", jsonData);
        serverInfo.socket.off("notify/" + jsonData.channel);
        serverInfo.socket.on("notify/" + jsonData.channel, callback);
    }
}

export const unsubscribeSignals = (jsonData) => {
    channelList.delete(jsonData.channel);
    if (serverInfo.serverFound) {
        serverInfo.socket.emit("unsubscribe", jsonData);
        serverInfo.socket.off("notify/" + jsonData.channel);
    }
}

export const getServerPort = () => {
    return "http://" + serverInfo.SERVER_IP + ":" + HTTP_PORT;
}

export const getServerIP = () => {
    return "http://" + serverInfo.SERVER_IP;
}

export const findServer = (initSignals=null) => {
    watchdogChannel.initSignals = initSignals;

    talkToServer();

    if (watchdogChannel.intervalHandle) {
        clearInterval(watchdogChannel.intervalHandle);
    }
    watchdogChannel.intervalHandle = setInterval(() => {
        const date = new Date();
        const thisTime = date.getTime();
        if (watchdogChannel.lastTime !== -1) {
            const timeDelta = thisTime - watchdogChannel.lastTime;
            if (timeDelta > 5000) {
                watchdogChannel.timeoutCounter = 0;
                serverInfo.serverFound = false;
            }
        }
        watchdogChannel.lastTime = thisTime;

        if (watchdogChannel.timeoutCounter > 0) {
            watchdogChannel.timeoutCounter--;
            if (watchdogChannel.timeoutCounter === 0) {
                serverInfo.serverFound = false;
                //console.log("watchdog timeout");
            }
        }

        if (!serverInfo.serverFound) {
            talkToServer();
        }
        else {
            refreshWatchdog();
        }
    }, 1000);
}

const talkToServer = () => {
    watchdogChannel.heartbeat++;
        
    const jsonData = {
       name  : MY_MACHINE, 
       value : watchdogChannel.heartbeat
    };

    serverInfo.socket.emit("watchdog", jsonData);
    serverInfo.socket.off("watchdog/" + MY_MACHINE);
    serverInfo.socket.on("watchdog/" + MY_MACHINE, onUdpMessage);
}

const refreshWatchdog = () => {
    watchdogChannel.heartbeat++;
    
    const jsonData = {
       name  : MY_MACHINE, 
       value : watchdogChannel.heartbeat
    };

    serverInfo.socket.emit("watchdog", jsonData);
    serverInfo.socket.off("watchdog/" + MY_MACHINE);
    serverInfo.socket.on("watchdog/" + MY_MACHINE, () => {
        serverInfo.socket.off("watchdog/" + MY_MACHINE);
        watchdogChannel.timeoutCounter = 5;
    });
}

const onUdpMessage = () => {
    serverInfo.socket.off("watchdog/" + MY_MACHINE);

    if (!serverInfo.serverFound) {
        serverInfo.serverFound = true;
        
        if (watchdogChannel.initSignals) {
            watchdogChannel.initSignals();
        }

        setTimeout(() => {
            channelList.forEach((channelData) => {
                subscribeSignals(channelData.jsonData, channelData.callback);
            });
        }, SUBSCRIPTION_DELAY);
    }
}
