import ReactStreetviewMulti from "./ReactStreetviewMulti";
import { SIGNAL_TYPES, setSignal, setSignals } from "../vplug/WsReq";
import vssApi from "../vplug/VssAPI.json";


const LeftSideBar = ({
    originRef,
    relocate,
    destination,
    setDestination,
    setAutopilotOn,
    MAP_API_KEY,
    streetViewPanoramaOptions,
    speed,
    gear,
    setDepartureReady,
    setTouchscreenOpen
}) => {

    const EXAMPLE_CURRENT_LOC = "Googleplex";
	const EXAMPLE_DESTINATION = "Apple Park";

    function setCurrentLocation() {
        if (originRef.current) {
            originRef.current.value = EXAMPLE_CURRENT_LOC;
        }
    }

    function setTargetDestination() {
        setTouchscreenOpen(true);
        setDestination(EXAMPLE_DESTINATION);
        setSignal("destination", EXAMPLE_DESTINATION, SIGNAL_TYPES.EXT);
    }

    function startAutopilot() {
        if (destination && destination.length >= 3) {
            setAutopilotOn(true);
        }
    }

    function openDriverDoor() {
        if (speed === 0 && gear !== "d" && gear !== "r") {
            setSignals({
                signals: [
                    { name: vssApi.Vehicle.Cabin.Door.Row1.DriverSide.IsLocked.name, value: "False" },
                    { name: vssApi.Vehicle.Cabin.Door.Row1.DriverSide.IsOpen.name, value: "True" }
                ],
                type: SIGNAL_TYPES.VSS
            });
            setDepartureReady(false);
        }
    }

    function startAutopilotAtOnce() {
        if (originRef.current) {
            originRef.current.value = EXAMPLE_CURRENT_LOC;
            relocate();

            setDestination(null);
            setSignal("destination", null, SIGNAL_TYPES.EXT);
            
            setTimeout(() => {
                setTouchscreenOpen(true);
            }, 3000);

            setTimeout(() => {
                setDestination(EXAMPLE_DESTINATION);
                setSignal("destination", EXAMPLE_DESTINATION, SIGNAL_TYPES.EXT);
            }, 6000);

            setTimeout(() => {
                setAutopilotOn(true);
            }, 9000);
        }
    }

    return (
        <div style={{
            width: "18%",
            height: "100%",
            backgroundColor: '#f8f8f8',
        }}>
            <div style={{ height: "50%", width: "100%", padding: 8 }}>
                <div
                    style={{
                        width: "100%", display: "flex", flexDirection: "row", justifyContent: "flex-start", alignItems: "center"
                    }}
                >
                    <button
                        style={{
                            width: 28, height: 28, marginRight: 8, padding: 4, background: "black"
                        }}
                        alt=""
                        onClick={startAutopilotAtOnce}
                    >
                        <img src="./interior/steering-wheel.png" style={{ width: 20, height: 20 }} alt="" />
                    </button>
                    <b>Autopilot Instructions:</b>
                </div>
                <ul style={{ marginTop: 8, display: "flex", flexDirection: "column", gap: 4 }}>
                    <li>
                        {"Set Current Location: e.g., "}
                        <u
                            style={{ color: "blue", cursor: "pointer" }}
                            onClick={setCurrentLocation}
                        >
                            {EXAMPLE_CURRENT_LOC}
                        </u>
                    </li>
                    <li>
                        {"Click "}
                        <u
                            style={{ color: "blue", cursor: "pointer" }}
                            onClick={relocate}
                        >
                            {"Teleport"}
                        </u>
                    </li>
                    <li>
                        {"Set Destination on Touchscreen: e.g., "}
                        <u
                            style={{ color: "blue", cursor: "pointer" }}
                            onClick={setTargetDestination}
                        >
                            {EXAMPLE_DESTINATION}
                        </u>
                    </li>
                    <li>
                        {"Click "}
                        <u
                            style={{ color: "blue", cursor: "pointer" }}
                            onClick={startAutopilot}
                        >
                            {"Autopilot"}
                        </u>
                        {" on Steering Wheel"}
                    </li>
                </ul>
                
                <div
                    style={{
                        width: "100%", display: "flex", flexDirection: "row", justifyContent: "flex-start", alignItems: "center"
                    }}
                >
                    <button
                        style={{
                            width: 28, height: 28, marginRight: 8, padding: "2px 6px", background: "black"
                        }}
                        alt=""
                        onClick={openDriverDoor}
                    >
                        <img src="./tesla/tesla-door-left.webp" style={{ width: 16, height: 24 }} alt="" />
                    </button>
                    <b>To Exit:</b>
                </div>
                <ul style={{ marginTop: 8 }}>
                    <li>
                        {"Click "}
                        <u
                            style={{ color: "blue", cursor: "pointer" }}
                            onClick={openDriverDoor}
                        >
                            {"Driver Door Switch"}
                        </u>
                        {" if the Car is standstill"}
                    </li>
                </ul>
            </div>
            <div style={{ height: "50%", width: "100%" }}>
                {streetViewPanoramaOptions &&
                    <ReactStreetviewMulti
                        viewId={"left-side-mirror"}
                        apiKey={MAP_API_KEY}
                        streetViewPanoramaOptions={
                            {
                                position: streetViewPanoramaOptions.position,
                                pov: { heading: (streetViewPanoramaOptions.pov.heading - 135), pitch: 0 },
                                zoom: 1
                            }
                        }
                    />
                }
            </div>
        </div>
    );
}

export default LeftSideBar;